package sundial.math

import fixD
import fixL
import kotlinx.serialization.Serializable
import sundial.geometry.Point
import kotlin.math.atan2
import kotlin.math.cos
import kotlin.math.hypot
import kotlin.math.sin

@Serializable
class Vector(val delX: Double, val delY: Double,val polar: Boolean=false) {

    constructor(p1: Point, p2: Point,polar: Boolean=false) : this(p2.x-p1.x,p2.y-p1.y,polar)

    constructor(theta: Double) : this(cos(theta), sin(theta))

    private operator fun invoke(delX: Double,delY: Double): Vector {
        return Vector(delX,delY)
    }

    operator fun plus(b: Vector): Vector {
        return invoke(delX+b.delX,delY+b.delY)
    }

    operator fun minus(b: Vector): Vector {
        return invoke(delX-b.delX,delY-b.delY)
    }

    infix operator fun times(f: Double): Vector {
        return invoke(f*delX,f*delY)
    }

    val rotation: Double by lazy {
        atan2(delY,delX)
    }

    val length: Double by lazy {
        hypot(delX,delY)
    }

    fun rotate(theta: Double): Vector = rotate(this,theta)

    fun normalize() = normalize(this)

    fun norm() = norm(this)

    fun componentNormalTo(v: Vector) = this - componentAlong(v)

    fun componentAlong(v: Vector): Vector {
        val u = v.normalize()

        return (dot(this,u)) * u
    }

    fun toPolar() = "[${fixL(length)}, ${fixD(rotation)}]"

    override fun toString(): String {

        return if (polar)
            "[${fixL(length)}, ${fixD(rotation)}]"
        else
            "[${fixL(delX)}, ${fixL(delY)}]"
    }

    companion object {
        val O = Vector(0.0,0.0)
        val I = Vector(1.0,0.0)
        val J = Vector(0.0,1.0)

        fun rotate(v: Vector, theta: Double): Vector {
            val cTh = cos(theta)
            val sTh = sin(theta)

            return Vector(
                v.delX*cTh - v.delY*sTh,
                v.delX*sTh + v.delY*cTh
            )
        }

        fun normalize(v: Vector): Vector {
            val L = norm(v)

            return Vector(v.delX/L,v.delY/L)
        }

        fun norm(v: Vector): Double = hypot(v.delX,v.delY)

        fun dot(v1: Vector, v2: Vector) = v1.delX*v2.delX + v1.delY*v2.delY
    }
}

infix operator fun Double.times(V: Vector): Vector = V.times(this)