package sundial.astronomy

import BLUE
import CYAN
import DEG
import GRAY
import GREEN
import ITER_MAX
import MINUTES
import ORANGE
import PI_FOURTH
import PI_HALF
import THREE_PI_HALVES
import TWO_PI
import clamp2PI
import clampPI
import fix6
import fixD
import measuredText
import DateTimeUTC
import DateTimeUTC.Companion.now
import printLn
import printLnErr
import sundial.*
import sundial.geometry.*
import sundial.math.Transformation
import sundial.math.Vector
import sundial.numberset.AngleSet
import sundial.numberset.NumberSet.Companion.CLOSED
import sundial.numberset.RealSet
import svg.SVG
import zoodiac
import kotlin.math.*

/**
 * The Equation of Time (EoT) is a correction that is added to the apparent zone time as displayed on a dial to obtain the
 * zone clock time as displayed on a clock. THe EoT is slowly varying over the years, so a year in the middle of the
 * expected lifespan of the dial should be chosen.
 * @property year the year for which to compute the EoT
 */
class EquationOfTime(val year: Int,info: Boolean=false) : Astronomy() { // TODO year should be yearUTC
    // https://www.wikiwand.com/en/Equation_of_time#/Major_components_of_the_equation  Right ascension calculation

    /**
     * A data class to hold a value for the EoT correction at a certain ecliptic longitude, and also other parameters
     * for further calculations
     * @property eot equation of time in radians
     * @property v true anomaly in radians
     * @property E eccentric anomaly in radians
     * @property M mean anomaly in radians
     */
    data class EoTResult(val eot: Double,val v: Double,val E: Double,val M: Double)

    /**
     * An [EquationOfTime] for the year 10 years from now
     */
    constructor() : this(futureYear(10))

    private val dateTimeUTC = DateTimeUTC(year, 6, 1, 0, 0, 0, 0).also {
        if (info) {
            printLn(CYAN,"Equation og Time")
            printLn(CYAN, "Orbital parameters for date $it")
            printLn(CYAN, "Timestamp: ${it.millisEpoch}")
        }
    }

    /**
     * Number of julian centuries passed since [EPOCH][Astronomy.EPOCH] until 1 June of [year] at 00:00UT
     */
    val centuries = dateTimeUTC.centuries2000

    /**
     * [Obliquity][Astronomy.obliquity] for the [year]
     */
    val epsilon = obliquity(centuries).also {
        if (info)
            printLn(CYAN,"Obliquity of the ecliptic: ${fixD(it)}")
    }

    /**
     * cos([epsilon])
     */
    val cEps = cos(epsilon)

    /**
     * sin([epsilon]
     */
    val sEps = sin(epsilon)

    /**
     * [Longitude of perihelion][Astronomy.perihelionLongitude] for the [year]
     */
    val lamP = perihelionLongitude(centuries).also {
        if (info)
            printLn(CYAN,"Longitude of perihelion: ${fixD(it)}")
    }

    /**
     * [Eccentricity][Astronomy.eccentricity] for the [year]
     */
    val e = eccentricity(centuries).also {
        if (info)
            printLn(CYAN,"Eccentricity of orbit: ${fix6(it)}")
    }

    private val Kecc = sqrt((1 - e)/(1 + e))
    private val Kobl = tan(epsilon/2).pow(2)

    /**
     * Equation of time as a function of the sun's ecliptic longitude. This is unconventional, as the
     * usual parameter for the equation is time. Radians
     * @param ecllng ecliptic longitude of the sun
     */
    fun eot(ecllng: Double) = eotR(ecllng).eot

    /**
     * Equation of time as a function of the sun's ecliptic longitude. The result of [eot]
     * converted to minutes.
     */
    fun minutes(ecllng: Double) = eotR(ecllng).eot/ MINUTES

    /**
     * Equation of time as a function of the sun's ecliptic longitude. Same as [eot], but the result
     * is an [EoTResult]
     */
    fun eotR(ecllng: Double): EoTResult {
        // True anomaly
        val v = clampPI(ecllng) - lamP
        // Eccentric anomaly
        val E = 2* atan(Kecc * tan(v/2))
        // Mean anomaly
        val M = E - e*sin(E)

        // EoT in radians
        val eot =  Kobl*sin(2*M + 2*lamP) - 2*e*sin(M)

        return EoTResult(eot,v,E,M)
    }

    fun deot(ecllng: Double) = deot(eotR(ecllng))

    // dEoT/dEclLongitude in minutes
    fun dMinutes(ecllng: Double) = deot(eotR(ecllng))/ MINUTES

    /**
     * The derived of [eot] over ecliptic longitude
     */
    fun deot(r:EoTResult): Double {
        // EoT derived over mean anomaly
        val dEoT_dM = 2*Kobl*cos(2*lamP+2*r.M)-2*cos(r.M)*e

        // Mean anomaly derived over eccentric anomaly
        val dM_dE = 1-cos(r.E)*e

        // Eccentric anomaly derived oved over true anomaly
        val dE_dv = Kecc*cos(r.E/2).pow(2)/cos(r.v/2).pow(2)

        return dEoT_dM*dM_dE*dE_dv
    }

    override fun toString(): String {
        return StringBuilder("Equation of Time for the year $year\n").
            append("Orbital parameters for date $dateTimeUTC (Timestamp: ${dateTimeUTC.millisEpoch})\n").
            append("\tObliquity of the ecliptic: ${fixD(epsilon)}\n").
            append("\tLongitude of perihelion: ${fixD(lamP)}\n").
            append("\tEccentricity of orbit: ${fix6(e)}\n").toString()
    }

    /**
     * A list of zero point longitude values for this [eot]
     */
    val zeroes: List<Double> by lazy {
        val lst = mutableListOf<Double>()

        var l0 = 0* DEG
        var e0 = eot(l0)

        if (e0 == 0.0)
            lst.add(l0)

        var l1: Double
        var e1: Double

        for (l in 1 ..360) {
            l1 = l* DEG
            e1 = eot(l1)

            if (e1 == 0.0)
                lst.add(l1)
            else if (e0*e1 < 0.0)
                improve((l0+l1)/2).also {
                    lst.add(it)
                }

            l0 = l1
            e0 = e1
        }

        lst.toList()
    }

    /**
     * The domain of ecliptic longitudes [0,2*PI> partitioned so that the [eot] is monotonically increasing
     * or decreasing on each interval
     */
    val monotonic: List<Double> by lazy {
        val lst = mutableListOf<Double>()

        var l0 = 0* DEG
        var de0 = deot(l0)

        if (de0 == 0.0)
            lst.add(l0)

        var l1: Double
        var de1: Double

        for (l in 1 ..360) {
            l1 = l* DEG
            de1 = deot(l1)

            if (de1 == 0.0)
                lst.add(l1)
            else if (de0*de1 < 0.0) {
                dEoTZero(l0,l1)?.let {
                    lst.add(it)
                }
            }

            l0 = l1
            de0 = de1
        }

        lst.add(0,lst.last()- TWO_PI)

//        lst.sort()

        lst.toList()
    }

    /**
     * The inverse function of [eot]. There may be several inverse values, hence the method
     * returns a [DoubleArray]
     * @param eot_ a value such as 5*[MINUTES]
     */
    fun inv(eot_: Double): DoubleArray {
        // eot_ must be supplied in radians. For an eot of 5 minutes, write inv(5*MINUTE)
        val lst = mutableListOf<Double>()

        for (i in 0 until monotonic.size - 1) {
            var l0 = monotonic[i]
            var e0 = eot(l0) - eot_

            if (e0 == 0.0)
                lst.add(l0)
            else {
                var l1 = monotonic[i + 1]
                var e1 = eot(l1) - eot_

                if (e1 == 0.0)
                    lst.add(l1)
                else if (sign(e0) != sign(e1)) {
                    // Crossing longitude-axis somewhere between l0 and l1
                    var count = 0
                    var l: Double? = null

                    while ((abs(l1 - l0) > ANGLE_PRECISION) && (count++ < ITER_MAX)) {
                        l = (l0 + l1) / 2 // Middle longitude between l0 and l1
                        val e = eot(l) - eot_

                        if (e == 0.0)
                            lst.add(l)
                        else if (sign(e0) != sign(e)) {
                            // Crossing longitude-axis on the left side of l
                            l1 = l
                            e1 = e
                        } else if (sign(e) != sign(e1)) {
                            // Crossing longitude-axis on the right side of l
                            l0 = l
                            e0 = e
                        } else
                            break
                    }

                    if (count >= ITER_MAX)
                        printLnErr("EquationOfTime#improve2() failed to converge")

                    l?.let {
                        lst.add(it)
                    }
                }
            }
        }

        return lst.toDoubleArray() // List of angles in radians
    }

    /**
     * The subdomain of ecliptic longitudes where the [eot] >= [eotMin]
     */
    fun over(eotMin: Double): AngleSet {
        // eotMin must be supplied in radians. For an eotMin of 5 minutes, write over(5*MINUTE)
        var a = AngleSet()
        val lam = inv(eotMin)
        for (i in 0 until lam.size )
            if (deot(lam[i]) > 0.0) {
                var j = i + 1
                if (j == lam.size) j = 0
                a = (a union AngleSet(CLOSED, lam[i], lam[j], CLOSED)) as AngleSet
            }

        if (a.isEmptySet())
            if (eotMin < eot(0.0))
                a = a.complement() as AngleSet

        return a
    }

    /**
     * The subdomain of ecliptic longitudes where the [eot] <= [eotMax]
     */
    fun under(eotMax: Double): AngleSet {
        // eotMax must be supplied in radians. For an eotMax of 5 minutes, write under(5*MINUTE)
        // This would generate OPEN intervals:
        // return over(eotMax).complement() as AngleSet

        var a = AngleSet()
        val lam = inv(eotMax)
        for (i in 0 until lam.size )
            if (deot(lam[i]) < 0.0) {
                var j = i + 1
                if (j == lam.size) j = 0
                a = (a union AngleSet(CLOSED, lam[i], lam[j], CLOSED)) as AngleSet
            }

        if (a.isEmptySet())
            if (eotMax > eot(0.0))
                a = a.complement() as AngleSet

        return a
    }

    /**
     * Get an improved solution to [eot] (lam) = 0
     * @param lam a solution to improve
     */
    fun improve(lam: Double): Double {
        // lam1 = lam0 - eot(lam0)/deot(lam00)
        var i = 0
        var l0: Double
        var l1 = lam

        do {
            l0 = l1
            val r = eotR(l0)
            l1 = l0 - r.eot/deot(r)
        } while ((abs(l1 - l0) > ANGLE_PRECISION) && (i++ < ITER_MAX))


        if (i >= ITER_MAX)
            printLnErr("EquationOfTime#improve() failed to converge")

        return l1
    }

    /**
     * Search for an extremal point of the [eot] between the ecliptic longitudes [lam0] and [lam1]. These
     * parameter values should be consecutive values in the [zeroes] list
     */
    fun dEoTZero(lam0: Double, lam1: Double): Double? {
        var i = 0
        var l0 = lam0
        var de0 = deot(l0)
        if (de0 == 0.0)
            return l0

        var l1 = lam1
        var de1 = deot(l1)
        if (de1 == 0.0)
            return l1

        if (sign(de0) == sign(de1))
            return null

        var l: Double? = null

        while((abs(l1 - l0) > ANGLE_PRECISION) && (i++ < ITER_MAX)) {
            l = (l0 + l1)/2
            val de = deot(l)

            if (de == 0.0)
                return l
            else if (sign(de0) != sign(de)) {
                l1 = l
                de1 = de
            }
            else if (sign(de) != sign(de1)) {
                l0 = l
                de0 = de
            }
            else
                return null
        }

        if (i >= ITER_MAX)
            printLnErr("dEoTZero() failed to converge")

        return l
    }

    companion object {
        /**
         * Precision wanted when iterating for ecliptic longitudes
         */
        const val ANGLE_PRECISION = 0.001* DEG

        /**
         * A very light gray color
         */
        val EXTRA_LT = 0xE0E0E0

        fun futureYear(years: Int) =  now().year + years

        suspend fun svg(width: Double, height: Double, eot_: EquationOfTime, springSteps: Map<Double, AngleSet>, autumnSteps: Map<Double, AngleSet>) : SVG {
            val minutesMax = eot_.monotonic.map { lam ->
                eot_.minutes(lam)
            }.maxBy { minutes ->
                minutes
            }

            val minutesMin = eot_.monotonic.map { lam ->
                eot_.minutes(lam)
            }.minBy { minutes ->
                minutes
            }

            val w = TWO_PI
            val h = minutesMax - minutesMin

            val dQ = width/60.0 // A margin
            val W = width - 2*dQ
            val H = height - 2*dQ

            val fx = W/w
            val fy = H/h

            val tr = Transformation.catenate(Transformation.scaling(fx,fy),Transformation.translatinon(Vector(PI_HALF+dQ/fx,-minutesMin+dQ/fy)))

            return SVG(width,height, unit = "", preTrans = tr,comment = "Equation of Time for years around ${eot_.year}").also { svg ->
                val textSize = svg.W/(60.0*svg.scale)

                val lw = (textSize*svg.scale)/15.0

                // X-axis
                Line(Point(-PI_HALF,0.0), Point(THREE_PI_HALVES,0.0)).also { xAxis ->
                    svg.appendGeometry(xAxis,xAxis.fullDomain, GRAY,lw)
                }

                // Vertical grid lines
                for (lambda in -60 .. 240 step 30) {
                    val x = lambda* DEG
                    Line(Point(x,minutesMin),Point(x,minutesMax)).also { vLine ->
                        svg.appendGeometry(vLine,vLine.fullDomain, EXTRA_LT,lw)
                    }
                }

                val mMax = minutesMax.toInt()
                val mMin = minutesMin.toInt()
                for (m in (mMin .. mMax).minus(0)) {
                    val y = m.toDouble()
                    val lnf = if ((m%5)==0) 1.5 else 1.0

                    // Horizontal grid lines
                    Line(Point(-PI_HALF,y),Point(THREE_PI_HALVES,y)).also { ln ->
                        svg.appendGeometry(ln,ln.fullDomain, EXTRA_LT,lw*lnf)
                    }

                    // Minutes texts
                    if ((m%5) == 0) {
                        val s = "${m} min"

                        svg.appendGeometry(
                            Text(measuredText(s,textSize),0.0,Point(0.0,y)),
                            RealSet.R, GRAY,textSize
                        )
                    }
                }

                // Longitude labels
                for (lambda in -90 .. 270 step 30) {
                    if (lambda != 0) svg.appendGeometry(
                        Text(measuredText("$lambda",textSize),0* DEG,Point(lambda* DEG,0.0)),
                        RealSet.R, GRAY,textSize
                    )
                }

                // Signs of zodiac
                for (i in 0 .. 11) {
                    val lambda = (30*i - 120)* DEG
                    svg.appendGeometry(
                        Text(measuredText(zoodiac(i-3),2*textSize),0.0,Point(PI_FOURTH +lambda,(minutesMax+minutesMin)/2)),
                        RealSet.R, EXTRA_LT,7.0/5.0*textSize
                    )
                }

                // Spring step function
                springSteps.forEach { (m,a) ->
                    val m_ = m.toDouble()
                    a.itvl(wrapAndJoin = true).forEach { itv ->
                        Line(Point(clampPI(itv.leftValue()),m_),Point(clampPI(itv.rightValue()),m_)).also { ln ->
                            svg.appendGeometry(ln,ln.fullDomain, GREEN,lw*2.0)
                        }
                    }
                }

                // Autumn step function
                autumnSteps.forEach { (m,a) ->
                    val m_ = m.toDouble()
                    a.itvl(wrapAndJoin = true).forEach { itv ->
                        Line(Point(clamp2PI(itv.leftValue()),m_),Point(clamp2PI(itv.rightValue()),m_)).also { ln ->
                            svg.appendGeometry(ln,ln.fullDomain, ORANGE,lw*2.0)
                        }
                    }
                }

                // EoT graph
                val graph = Polyline(buildList<Point> {
                    for (lambda in -90 .. 270) {
                        add(Point(
                            lambda * DEG,
                            eot_.minutes(lambda * DEG)
                        ))
                    }
                })

                svg.appendGeometry(graph,graph.fullDomain, BLUE,lw*2.0)
            }
        }
    }
}

