import com.ionspin.kotlin.bignum.decimal.BigDecimal
import com.ionspin.kotlin.bignum.decimal.DecimalMode
import com.ionspin.kotlin.bignum.decimal.RoundingMode

fun pi(decimalPrecision: Long = 80) {
    // https://www.wikiwand.com/en/Approximations_of_%CF%80#Efficient_methods
    // formula by Newton
    val DM = DecimalMode(
        decimalPrecision = decimalPrecision,
        roundingMode = RoundingMode.ROUND_HALF_AWAY_FROM_ZERO,
        scale = decimalPrecision - 2L
    )
    val one = BigDecimal.fromInt(1, DM)
    val two = BigDecimal.fromInt(2, DM)
    val three = BigDecimal.fromInt(3, DM)
    var numerator = one
    var denominator = three
    var v = one
    var f = one
    var oldPi: BigDecimal?
    var newPi: BigDecimal? = two
    do {
        f = f.multiply(numerator.divide(denominator, DM), DM)
        v = v.add(f, DM)
        oldPi = newPi
        newPi = two.multiply(v, DM)
        printLn(LT_BLUE,newPi.toString())
        numerator = numerator.add(one, DM)
        denominator = denominator.add(two, DM)
    } while (newPi != oldPi)
}