package sundial.geometry

import DEG
import PI_SIXTH
import RAD
import abbrSign
import fixD
import isCloseToMultipleOf
import measuredText
import sundial.DialParts
import sundial.PlaineSundial
import sundial.Sundial
import sundial.math.Vector
import sundial.numberset.AngleSet
import sundial.numberset.RealSet
import zoodiac
import kotlin.math.*

class ZodiacGraph(val parts: DialParts,
                  eclStep: Int,
                  val symbols: Int,
                  val textSize: Double,
                  val textRotation: Double,
                  val clipConic:(g: Geometry)->RealSet) {

    val solarLongitudes =
        ((parts.longitudesAboveHorizon(true) intersection parts.longitudesAboveDial(true)) as AngleSet).let { longitudeSet ->
            val longitudes = mutableListOf<Double>()

            for (i in -90..90 step eclStep) {
                val l = i*DEG
                if (longitudeSet.contains(l))
                    longitudes.add(l)
            }

            longitudes.toList()/*.also {
                it.forEach { l ->
                    print(MAGENTA,"${fixD(l)} ")
                }
                printLn()
            }*/
        }

    val conics = mutableMapOf<String,Sundial.DialItem>().let { conicmap ->
        solarLongitudes.forEach { lng ->
            val sgn = if (lng >= 0.0) '+' else '-'
            parts.getDeclinationConic(parts.decl(lng))?.let { conic ->
                val label ="${PlaineSundial.ZODIAC_CONIC}$sgn${(abs(lng) * RAD).roundToInt()}"
                conic.label = label
                val domain = clipConic(conic)
                if (!domain.isEmptySet()) {
                    val itm = Sundial.DialItem(conic, domain, lng, if (isCloseToMultipleOf(lng, PI_SIXTH)) PlaineSundial.STRONG else 0)
                    conicmap[label] = itm
                }
            }
        }

        conicmap.toMap()
    }

    suspend fun getSymbolsWithPositions(conics: Map<String,Sundial.DialItem>): Map<String,Sundial.DialItem> {
        val txts = mutableMapOf<String,Sundial.DialItem>()

        fun sortedEndPoints(itm: Sundial.DialItem) =
            itm.geometry.toPoint(itm.domain.first())?.let { pf ->
                itm.geometry.toPoint(itm.domain.last())?.let { pl ->
                    DialParts.sortPointsAlong(Vector.I,pf,pl).let {
                        Triple(it[0],it[1],itm.pId)
                    }
                }
            }

        fun offsetAndAngle(pt0: Point,pt1: Point) =
            // Get the middle point / center of gravity
            Point.cog(pt0,pt1)?.let { cog ->
                // cog corresponds to a solar declination
                parts.getDeclinationOfPoint(cog).let { decl ->
                    // The declination corresponds to a Conic on the dial
                    parts.getDeclinationConic(decl)?.let { conic ->
                        // The position of cog on the conic corresponds to a certain value
                        // for the parameter that parametrizes the conic
                        conic.toParameter(cog)?.let { t ->
                            // This parameter can be used to determine the tangent vector
                            // to the conic at the point cog
                            conic.tangent(t)?.let { tng ->
                                // Determine an acuteness factor that is smallest when
                                // the tangent is perpendicular to the line pt0 - pt1

                                Pair(tng,abs(Vector.dot(Vector(pt0,pt1).normalize(),tng)))
                            }
                        }
                    }
                }
            } ?: Pair(Vector.O,0.0)

        fun textPosAndSize(p0: Point?,p1: Point?,isSpring: Boolean) = if ((p0 != null)&& (p1 != null)) {
            val (offs, acute) = offsetAndAngle(p0, p1)

            val d = ((1 - acute.pow(5))*Point.distance(p0,p1)).let {
                if (it < 1.5*textSize) it else 1.5*textSize
            }

            val pos = p0 + Vector(p0, p1) * 0.5 + offs.times((if (isSpring) 0.5 else -0.5)*(1 + sqrt(1-acute))*d)

            Pair(pos,d)
        }
        else
            null

        val mainconics = conics.values.filter {
            isCloseToMultipleOf(it.pId,PI_SIXTH) // Close to multiple of 30 degrees
        }

        mainconics.mapNotNull {
            sortedEndPoints(it)
        }.let { ends ->
            if (ends.isNotEmpty()){
                var ends0 = ends[0]
                for (i in 1 until ends.size) {
                    ends[i].let { ends1 ->
                        textPosAndSize(ends0.first, ends1.first, false)?.let { (Pa, size) ->
                            val la = PI - (ends[i - 1].third + ends[i].third) / 2
                            val sa = getSymbol(la)
                            val txt = Text(measuredText(sa, size), textRotation, Pa)
                            txts["${PlaineSundial.ZODIAC_TEXT}_$sa"] = Sundial.DialItem(
                                txt, txt.fullDomain, la
                            )
                        }

                        textPosAndSize(ends0.second, ends1.second, true)?.let { (Ps, size) ->
                            val ls = (ends[i - 1].third + ends[i].third) / 2
                            val sa = getSymbol(ls)
                            val txt = Text(measuredText(sa, size), textRotation, Ps)
                            txts["${PlaineSundial.ZODIAC_TEXT}_$sa"] = Sundial.DialItem(
                                txt, txt.fullDomain, ls
                            )
                        }

                        ends0 = ends1
                    }
                }
            }
        }

        return txts
    }

    fun getSymbol(midLong: Double) = when (symbols) {
        PlaineSundial.ZOODIAC_SYMBOLS -> zoodiac(midLong)
        PlaineSundial.ABBREVIATED_SYMBOLS -> abbrSign(midLong)
        else ->""
    }

//    // Mark points for debugging
//    private val points_ = mutableListOf<Pair<Point,Int>>()
//
//    val points: List<Pair<Point,Int>> by lazy {
//        points_.toList()
//    }

    override fun toString(): String {
        val b = StringBuilder("Zodiac Graph\n")

        b.append("Ecliptic longitudes in $solarLongitudes:\n")
        for ( i in 0 until solarLongitudes.size-1)
            b.append("\tfrom ${fixD(solarLongitudes[i])} to ${fixD(solarLongitudes[i + 1])}: ${getSymbol((solarLongitudes[i]+solarLongitudes[i+1]/2))}\n")

        return b.toString()
    }
}