package sundial.geometry

import fixL
import isTiny

class Dimension(val width: Double,val height: Double) {
    override fun equals(other: Any?): Boolean {
        return if (other is Dimension)
            isTiny(other.width-width) && isTiny(other.height-height)
        else
            false
    }

    override fun toString(): String {
        return "width x height = ${fixL(width)} x ${fixL(height)}"
    }
}