import kotlinx.serialization.ExperimentalSerializationApi
import kotlinx.serialization.decodeFromString
import kotlinx.serialization.json.Json

@OptIn(ExperimentalSerializationApi::class)
open class WebWorker(
    val processCommand: (command: String, postToClient:(s: String) -> Unit, lock:(locked: Boolean)->Unit) -> Boolean,
    val processData:(clazz: String, json: String, postToClient:(s: String) -> Unit, lock:(locked: Boolean)->Unit) -> Boolean
) {
    // Callback on lock(true) will lock the GUI so that the user cannot send more processing
    // tasks to the worker. The worker must call lock(false) at the end of a processing coroutine
    // to unlock the GUI again
    private fun lock(locked: Boolean) = if (locked)
        post(BUSY)
    else
        post(DONE)

    private fun post(s: String) {
        self.postMessage(s)
    }

    init {
        self.onerror = { message: dynamic,filename: String,lineNo: Int,colNo: Int,error: Any? ->
            logC(CYAN,"$filename $lineNo:$colNo $message")
        }

        self.onmessage = { event ->
            val msg = event.data as String
            val idx = msg.indexOf('{')
            if (idx > -1) {
                val clazz = msg.substring(0, idx) // Data class name identifying the operation to perform
                val json = msg.substring(idx, msg.length) // Json formatted data to process.
                if (clazz == PrintData::class.simpleName) {
                    // Circuitous way to perform printLn()
                    val printData = Json.decodeFromString<PrintData>(json)
                    printLn(printData.color, printData.msg)
                }
                else if (!processData(clazz,json,::post,::lock)) {
                    logC(CYAN, "Data class not understood by worker: $clazz")
                }
            } else if (!processCommand(msg,::post,::lock)) {
                logC(CYAN, "Command not understood by worker: $msg")
            }

            Unit
        }
    }
}