package sundial.geometry

import MAGENTA
import PI_HALF
import TINY
import clampPI
import fixE
import isCloseToMultipleOf
import isTiny
import printLn
import printLnWrn
import sundial.math.Vector
import kotlin.math.*

open class ConvexHull {

    companion object {
        fun convexHull(points: List<Point>): List<Point> {
            if (points.isEmpty())
                return emptyList()

            // https://www.wikiwand.com/en/Graham_scan

            // let points be the list of points
            // let stack = empty_stack()
            val stack = ArrayDeque<Point>()  // push(p) is addFirst(p), pop() is removeFirst()

            // find the lowest y-coordinate and leftmost point, called P0
            // (Find point with lowest y-coordinate. If there are several points, choose the
            // one with the lowest x-coordinate
            val P0 = points.reduce { acc, point ->
                if (point.y < acc.y)
                    point
                else if (point.y == acc.y) {
                    if (point.x < acc.x) point else acc
                } else
                    acc
            }

            // Certainly P0 must be part of the hull?
            stack.addFirst(P0)

            // sort points by polar angle with P0, if several points have the same polar angle then only keep the farthest
            var acc = Vector(0.0,-1.0)
            val vectors = points.minus(P0).map { p ->
                Vector(P0,p,false)
            }.sortedBy { v ->
                v.rotation
            }.filter { v ->
                if (v.rotation > acc.rotation) {
                    acc = v
                    true
                }
                else
                    if (v.length > acc.length) {
                        acc = v
                        true
                    }
                else
                    false
            }

            vectors.forEach { v ->
                val p = Point(P0.x+v.delX,P0.y+v.delY)

                while ((stack.size > 1) && (ccw(stack.elementAt(1), stack.elementAt(0),p) <= 0)) {
                    stack.removeFirst()
                }

                stack.addFirst(p)
            }

            return stack.toList()
        }

        fun ccw(p1: Point,p2: Point,p: Point): Int {
            // ccw > 0 if three points make a counter-clockwise turn, clockwise if ccw < 0,
            // and collinear if ccw = 0. (In real applications, if the coordinates are
            // arbitrary real numbers, the function requires exact comparison of floating-point
            // numbers, and one has to beware of numeric singularities for "nearly" collinear points.)

            // https://www.wikiwand.com/en/Distance_from_a_point_to_a_line
            //
            // 'Distance' from line through p1 - p2 to p
            val d = (p1.x - p.x)*(p2.y - p1.y) - (p2.x - p1.x)*(p1.y - p.y)

            return if (isTiny(d)) {
                0
//                if (hypot(p.x-p1.x,p.y-p1.y) > hypot(p2.x-p1.x,p2.y-p1.y)) 1 else -1
            }
            else {
                if (d > 0) 1 else -1
            }
        }
    }
}