fun dialToSVG(dial: PlaineSundial,name: String=DIAL_NAME_PART): SVG? {
    dial.items[Sundial.OUTER_RECT]?.let { itm ->
        val outer = itm.geometry as Rectangle
        val theta = if (dial.parts.isSouthernHemisphere && dial.parts.isHorizontal) outer.theta + PI else outer.theta
        // Create SVG file

        val anniv = if (dial.items.containsKey(PlaineSundial.ANNIV_MARK)) {
            """
                    An anniversary is marked for ${dial.anniversaryDT?.iUTC?.format(MINUTES_FORMAT)}
"""
        } else
            ""

        val comment = """
                    A sundial at the geographic location 
                    
                        ${fixLat(dial.parts.geolat)} ${fixLng(dial.parts.geolng)}
                        
                    and wall normal/gnomon orientation
                    
                        azimuth: ${fix360(dial.parts.azimuth)}, altitude: ${fix180(dial.parts.altitude)}
                        
                    and gnomon length 
                    
                        ${fixL(dial.parts.G)} millimeters
                        
                    The dimensions of this design are width: ${fixL(outer.w)}mm x height: ${fixL(outer.h)}mm
                    $anniv    
                    See $HOMEPAGE_ADDRESS
                    
                    Image generated on ${stamp()}
                    """.trimIndent()


        // Create a transformation to bring the lower left corner of 'outer' to the origin, and
        // rotate it to align its sides with the coordinate axes
        val preTrans = Transformation.catenate(
            Transformation.translatinon(Vector(outer.w/2,outer.h/2)), // Done third: Move rectangle into first quadrant
            Transformation.rotation(-theta), // Done second: Align rectangle with axes
//            Transformation.rotation(-outer.theta), // Done second: Align rectangle with axes
            Transformation.translatinon(Vector(-outer.x0,-outer.y0)) // Done first: Center rectangle at Origin
        )
        return SVG(outer.w,outer.h,unit = "mm",preTrans=preTrans, name = name).also { svg ->
            svg.appendComment(comment)

            PlaineSundial.render(dial, svg)
        }
    }

    return null
}

fun styleToSVG(dial: PlaineSundial,name: String=STYLE_NAME_PART): SVG? {
    dial.truncatedStyle?.let { pt ->
        val del = 3.0
        val x = abs(pt.x)
        val rect = Rectangle(x + 2*del,dial.parts.G+2*del,0.0,0.0,0.0)

        val trans = Transformation.catenate(Transformation.translatinon(Vector(del,del)))
        val pt0 = trans.transform(Point.ORIGO)
        val pt1 = trans.transform(Point(x,0.0))
        val pt2 = trans.transform(Point(x,pt.y))
        val pt3 = trans.transform(Point(0.0,dial.parts.G))

        val comment = """
                    A style fo the sundial at the geographic location 
                    
                        ${fixLat(dial.parts.geolat)} ${fixLng(dial.parts.geolng)}
                        
                    and wall normal/gnomon orientation
                    
                        azimuth: ${fix360(dial.parts.azimuth)}, altitude: ${fix180(dial.parts.altitude)}
                        
                    and gnomon length 
                    
                        ${fixL(dial.parts.G)} millimeters
                        
                    The dimensions of this design are width: ${fixL(rect.w)}mm x height: ${fixL(rect.h)}mm    
                    
                    See $HOMEPAGE_ADDRESS
                    
                    Image generated on ${stamp()}
                    """.trimIndent()


        // Create an instance of the SVG graphics
        return SVG(rect.w,rect.h, comment = comment, preTrans = trans, name = name).also { svg ->
            val lw = 0.2
            svg.appendLine(pt0,pt1,BLUE,lw,null,id="STYLE_BASE")
            svg.appendLine(pt1,pt2,GRAY,lw,null,id="STYLE_HEIGHT")
            svg.appendLine(pt2,pt3,GRAY,lw,null,id="STYLE_TOP")
            svg.appendLine(pt0,pt3,GREEN,lw,null,id="STYLE_GNOMON")
        }
    }

    return null
}

fun paperModelStyle(dial: PlaineSundial,name: String=MODEL_NAME_PART): SVG {
    val FOOT = "FOOT"
    val GNOMON = "GNOMON"
    val EDGE = "EDGE"
    fun color(label: String?) = if (label?.startsWith(EDGE) == true)
        BLACK
    else if (label == GNOMON)
        GREEN
    else if (label == FOOT)
        BLUE
    else
        GRAY

    fun angle(a: Double,b:Double,c:Double) = ((a.pow(2) + b.pow(2) - c.pow(2))/(2*a*b)).let {
        acos(if (abs(it) > 1.0) sign(it) else it)
    }

    val pts = mutableListOf<Point>()
    val lns = mutableListOf<Line>()

    val phi = abs(dial.parts.flatLat)

    val S = dial.truncatedStyle?.let {
        Point(abs(it.x),abs(it.y))
    } ?: Point(dial.parts.G/ sin(phi),0.0)

    val delta = dial.parts.G/16.0
    val del = delta*(dial.parts.F - S.x)/dial.parts.F

    val s = S.x*hypot(dial.parts.F,delta)/dial.parts.F
    val t = S.x*hypot(dial.parts.F,dial.parts.G)/dial.parts.F
    val a = hypot(dial.parts.G,delta)
    val b = hypot(dial.parts.F,delta)
    val c = hypot(dial.parts.F,dial.parts.G)
    val alpha = angle(a,b,c) // 90 degrees
    val beta = angle(a,c,b)

    val P0 = Point.ORIGO
    pts.add(P0)
    val P1 = Point(delta,0.0)
    pts.add(P1)
    val P2 = Point(0.0,dial.parts.G)
    pts.add(P2)
    val P3 = P1 + Vector(P1,P2).rotate(-alpha).normalize().times(s)
    pts.add(P3)
    val P4 = P2 + Vector(P2,P1).rotate(beta).normalize().times(t)
    pts.add(P4)
    val P5 = Point(-delta,0.0)
    pts.add(P5)
    val gamma = atan2(S.y,del)
    val V = Vector(P3,P4).rotate(-gamma).normalize()
    val P6 = P3 + V.times(del)
    pts.add(P6)
    val P7 = P3 + V.times(2*del)
    pts.add(P7)
    val my = atan2(dial.parts.F,delta)
    val P8 = P1 + Vector(P1,P0).rotate(my)
    pts.add(P8)
    val P9 = P5 + Vector(P5,P0).rotate(-my)
    pts.add(P9)
    val ny = PI - my
    val P10 = P3 + Vector(P3,P6).rotate(-ny)
    pts.add(P10)
    val P11 = P7 + Vector(P7,P6).rotate(ny)
    pts.add(P11)
    val P12 = P1 + Vector(P1,P3).rotate(-my).normalize().times(2*delta)
    pts.add(P12)
    val P13 = P3 + Vector(P3,P1).rotate(ny).normalize().times(2*del)
    pts.add(P13)
    val P14 = Point.cog(P1,P12)!!
    pts.add(P14)
    val P15 = Point.cog(P3,P13)!!
    pts.add(P15)

    lns.add(Line(P1,P5))
    lns.add(Line(P1,P2))
    lns.add(Line(P5,P2))
    lns.add(Line(P1,P3))
    lns.add(Line(P3,P4))
    lns.add(Line(P3,P7))
    lns.add(Line(P7,P4))
    lns.add(Line(P1,P8))
    lns.add(Line(P5,P9))
    lns.add(Line(P8,P9))
    lns.add(Line(P3,P10))
    lns.add(Line(P7,P11))
    lns.add(Line(P10,P11))
    lns.add(Line(P1,P12))
    lns.add(Line(P3,P13))
    lns.add(Line(P12,P13))
    lns.add(Line(P6,P4).apply { label = "${EDGE}1" })
    lns.add(Line(P2,P4).apply { label= EDGE })
    lns.add(Line(P14,P15).apply{ label = FOOT })
    lns.add(Line(P0,P2).apply { label = GNOMON })

    val ln = lns[17]

    fun reflect(pt: Point) = Vector(ln.pos,pt).let { V ->
        ln.pos + V.componentAlong(ln.u) - V.componentNormalTo(ln.u)
    }

    fun reflect(ln: Line) = Line(reflect(ln.pos),reflect(ln.pos + ln.u*ln.fullDomain.last()))

    val pSize = pts.size
    for ( i in (0..pSize).minus(2).minus(4))
        pts.add(reflect(pts[i]))

    val lSize = lns.size
    for ( i in (0..lSize).minus(17))
        lns.add(reflect(lns[i]))

    val rect = FitRectAround.minimumAreaEnclosingRectangle(ConvexHull.convexHull(pts))?.let { r ->
        Rectangle(r.w+1.0,r.h+1.0,r.theta,r.x0,r.y0)
    }

    val preTrans = Transformation.catenate(
        Transformation.translatinon(Vector(rect!!.w/2,rect.h/2)), // Done third: Move rectangle into first quadrant
        Transformation.rotation(-rect.theta), // Done second: Align rectangle with axes
        Transformation.translatinon(Vector(-rect.x0,-rect.y0)) // Done first: Center rectangle at Origin
    )

    val comment = """
A style model for the sundial at the geographic location 

    ${fixLat(dial.parts.geolat)} ${fixLng(dial.parts.geolng)}
    
and wall normal/gnomon orientation

    azimuth: ${fix360(dial.parts.azimuth)}, altitude: ${fix180(dial.parts.altitude)}
    
and gnomon length 

    ${fixL(dial.parts.G)} millimeters
    
The dimensions of this design are width: ${fixL(rect.w)}mm x height: ${fixL(rect.h)}mm

This design is intended to be printed on paper, cut out, folded and glued together

See $HOMEPAGE_ADDRESS
                    
Image generated on ${stamp()}
""".trimIndent()

    return SVG(rect.w,rect.h, preTrans = preTrans, comment = comment, name = name).let { svg ->
        lns.forEach { ln ->
            svg.appendGeometry(ln,ln.fullDomain,color(ln.label),0.1,id=ln.label)
        }

        svg
    }
}

