fun dialToSCAD(dial: PlaineSundial): SCAD? {
    dial.items[Sundial.OUTER_RECT]?.let { itm ->
        val outer = itm.geometry as Rectangle
        // Create SCAD

        val comment =
"""A sundial as a 3D script to be rendered by 
    OpenSCAD https://openscad.org/
Geographic location    
Latitude:  ${fixLat(dial.parts.geolat)}
Longitude: ${fixLng(dial.parts.geolng)}
Orientation of dial normal (gnomon)
Azimuth:   ${fix360(dial.parts.azimuth)}
Altitude:  ${fix180(dial.parts.altitude)}
Gnomon length: ${fixL(dial.parts.G)}mm
See $HOMEPAGE_ADDRESS   
Script generated on ${stamp()}"""

        return SCAD(outer.w,outer.h,dial.parts.txtRotate,dial.textSize,0.04*dial.textSize*(dial.scale ?: 1.0),comment=comment).also { scad ->

            PlaineSundial.render(dial, scad)
        }
    }

    return null
}