package sundial.astronomy

import DEG
import clamp2PI
import DateTimeUTC
import kotlin.math.pow
import kotlin.math.sin

open class Astronomy {
    // https://www.wikiwand.com/en/Equation_of_time
    // Consider this page: https://www.aa.quae.nl/en/reken/hemelpositie.html
    // or this: https://aa.usno.navy.mil/faq/sun_approx
    companion object {
        /**
         * Obliquity / inclination of the ecliptic (). A slowly varying astronomical parameter (approx. 23.44). Radians
         */
        fun obliquity(centuries: Double) = (23.4393 - 0.013*centuries - 2E-7*centuries.pow(2) + 5E-7*centuries.pow(3))* DEG

        /**
         * Ecliptic longitude of the perihelion (p). A slowly varying astronomical parameter (approx. 283). Radians
         */
        fun perihelionLongitude(centuries: Double) = (282.93807 + 1.7195*centuries + 3.025E-4*centuries.pow(2))* DEG

        /**
         * Eccentricity of the orbit (e). A slowly varying astronomical parameter. No unit.
         */
        fun eccentricity(centuries: Double) = 1.6709E-2 - 4.193E-5*centuries - 1.26E-7*centuries.pow(2)

        /**
         * Mean anomaly. Radians
         */
        fun meanAnomaly(centuries: Double) =  (357.52911 + centuries*(35999.05029 - centuries*0.0001537))* DEG

        /**
         * The true ecliptic longitude of the sun (). The longitude is in the range [0,2*PI>]. Radians
         */
        fun trueLongitude(et: DateTimeUTC): Double {
            val e = eccentricity(et.centuries2000)
            val lamP = perihelionLongitude(et.centuries2000)
            val M = meanAnomaly(et.centuries2000)

            return clamp2PI(M + lamP + 2 * e * sin(M) + 5.0 * e.pow(2) * sin(2 * M) / 4.0)
        }
    }
}