package sundial.geometry

import fixD
import isTiny
import sundial.math.Transformation
import sundial.math.Vector
import sundial.numberset.NumberSet
import sundial.numberset.NumberSet.Companion.CLOSED
import sundial.numberset.RealSet

// TODO Add renderPolyline to Renderer
open class Polyline(val pts: List<Point>, override val theta: Double=0.0) : Geometry() {

    val tlst = kotlin.run {
        // Positions of points as lengths along the polyline from pts[0]
        val tl1 = mutableListOf<Double>()
        var t = 0.0

        for (idx in 0 .. pts.size-2) {
            tl1.add(t)

            t += Point.distance(pts[idx],pts[idx+1])
        }

        tl1.add(t)

        tl1.toList()
    }

    // Total length of polyline
    val length = tlst.last()

    // Could be different values for open, closed or self intersecting
    override val type: Int = 0

    override fun typeName() = "POLYLINE"

    // Position of polyline is pts[0]
    override fun position() = if (pts.isEmpty()) Point.ORIGO else pts[0]

    override val fullDomain = RealSet(CLOSED,tlst.first(),tlst.last(),CLOSED)

    override val canonicalForm: Geometry = this

    override fun rotate(delTheta: Double): Geometry {
        val pts1 = pts.map { pt ->
            pt.rotate(delTheta)
        }

        return Polyline(pts1,theta=theta+delTheta)
    }

    override fun translate(delX: Double, delY: Double): Geometry {
        val pts1 = pts.map { pt ->
            pt.translate(delX,delY)
        }

        return Polyline(pts1)
    }

    override fun scale(f: Double) = pts.map { pt ->
        Point(f*pt.x,f*pt.y)
    }.let { scaledpts ->
        Polyline(scaledpts,theta)
    }

    override fun points(itv: NumberSet<Double>.Interval?, smoothed: Boolean) = sequence<Point> {
        if (itv != null) {

            for (idx in 0 until tlst.size-1) {
                if ((tlst[idx] < itv.leftValue()) && (itv.leftValue() < tlst[idx + 1])) {
                    val pt = pts[idx]+Vector(pts[idx],pts[idx+1]).normalize().times(itv.leftValue()-tlst[idx])
                    yield(pt)
//                    println("#1 lv $pt")
                }
                if ((itv.leftValue() <= tlst[idx]) && (tlst[idx] <= itv.rightValue())) {
                    yield(pts[idx])
//                    println("#2 $idx ${pts[idx]}")
                }
                if ((tlst[idx] < itv.rightValue()) && (itv.rightValue() <= tlst[idx + 1])) {
                    val pt = pts[idx]+Vector(pts[idx],pts[idx+1]).normalize().times(itv.rightValue()-tlst[idx])
                    yield(pt)
//                    println("#3 rv $pt")
                }
            }
        }
    }

