    override fun toPoint(t: Double): Point? {

        return if (!fullDomain.contains(t))
            null
        else {
            val idx = tlst.indexOfLast { ti ->
                t >= ti
            }

            if (idx == -1)
                null
            else if (idx == tlst.size-1) {
                if (t == tlst[idx]) pts.last() else null
            }
            else {
                val V = Vector(pts[idx],pts[idx+1]).normalize()
                val t1 = t - tlst[idx]

                pts[idx] + V.times(t1)
            }
        }
    }

    override fun toParameter(pt: Point): Double? {
        val (_,hit,t) = hit_(pt)

        return if (hit == HIT_ON) t else null
    }

    override fun tangent(t: Double): Vector? {
        for (idx in 0 .. tlst.size-2)
            if ((tlst[idx] <= t) && (t <= tlst[idx+1]))
                return Vector(pts[idx],pts[idx+1]).normalize()

        return null
    }

    override val axis: Line by lazy {
        Line(pts.first(),pts.last())
    }

    override fun hit(pt: Point): Int = hit_(pt).second

    fun hit_(pt: Point): Triple<Int,Int,Double> {
        for (idx in 0 .. pts.size-2) {
            val V = Vector(pts[idx],pts[idx+1])
            val W = Vector(pts[idx],pt)
            if (isTiny(W.componentNormalTo(V).norm())) {
                val t = W.componentAlong(W).norm()

                if ((0.0 <= t) && (t <= tlst[idx+1]-tlst[idx]))
                    return Triple(idx,HIT_ON,tlst[idx]+t)
            }
        }

        return Triple(-1,HIT_OUTSIDE, Double.NaN)
    }

    override fun construction(domain: RealSet?,tr: Transformation?,note: String?): String {
        val t = tr ?: Transformation.IDENTITY
        val buf = StringBuilder("Polyline between points: ")
        points().forEach {
            buf.append("${t.transform(it)}, ")
        }

        return buf.toString()
    }

    override fun toString(): String {
        val b = StringBuilder("POLYLINE\n")
        b.append("points: ")

        pts.forEach {
            b.append("$it ")
        }
        b.append("\n")

        b.append("rotated: ${fixD(theta)} pos at:$pos\n")

        b.append("length: $length")

        b.append("\nfull domain: $fullDomain")

        b.append("\ndomain: $domain")

        return b.toString()
    }
}