package svg

import RAD
import MAGENTA
import colorStr
import fix
import fix2
import fixL
import isTiny
import printLn
import printLnWrn
import sundial.PlaineSundial
import sundial.Renderer2
import sundial.Sundial
import sundial.geometry.*
import sundial.math.Transformation
import sundial.numberset.RealSet
import kotlin.math.*

// SVG has its origin in the upper left corner, x-axis to the right, y-axis downwards inside the area
// defined by width and height
class SVG(W: Double,H: Double, unit: String="mm",preTrans: Transformation= Transformation.IDENTITY, comment: String? = null,name: String="SVG") :
    Renderer2(W,H,preTrans=preTrans) {

    val w = fixL(W)
    val h = fixL(H)
    val u = if (unit.equals("mm",ignoreCase = true)) "mm" else ""

    val c = comment?.let {
        """
<!--
$it
-->
""".trimIndent()
    } ?: ""

    // An SVG-file is displayed with its correct size by having a viewBox
    // set in the svg-tag
    //
    //    <svg viewBox='0 0 100 100' ..
    //

    val sb = StringBuilder(
        """
<svg xmlns='http://www.w3.org/2000/svg' xmlns:xlink='http://www.w3.org/1999/xlink'
width='$w$u' height='$h$u' viewBox='0 0 $w $h'
preserveAspectRatio='xMidYMin meet' text-rendering='auto' shape-rendering='auto' id='svg'>
$c
<title>$name</title>

""".trimIndent()
    )

    override fun appendGeometry(g: Geometry,domain: RealSet,color: Int,lineWith: Double,pattern: String?,id: String?) {
        // appendGeometry() is the only append-function that pre-transforms
        if (g is Text) {
            val a = if (g is Anchor)
                g.address
            else
                null
            appendText(g.mt.txt,preTrans.transform(g.pos),g.theta,color,scale*g.mt.fontSizeMm,addr=a,id=id)
        }
        else
            preTransPaths(g,domain,true).forEach { path ->
//                checkLineIntegrity(g,path,id)
//                checkPointsAdvancing(id,path)

                // https://developer.mozilla.org/en-US/docs/Web/SVG/Attribute/stroke-dasharray
//                val pat0 = "5"
////                val pat0 = "${(10*lineWith).roundToInt()}"
//                val pat = if (pattern == PlaineSundial.DASHED0)
//                    pat0
//                else if (pattern == PlaineSundial.DASHED1)
//                    "0 $pat0 0"
//                else
//                    null

                if ((g is Line) && (path.size != 2))
                    printLnWrn("Line ${id?:""} has ${path.size} points")

                when (path.size) {
                    0,1 ->  {}
                    2 -> appendLine(path[0],path[1],color,lineWith,pattern,id)

                    else -> appendPolyline(path,color,lineWith,pattern,id)
                }
            }
    }

    override fun appendPoint(pt: Point, radius: Double, color: Int,id: String?) {
        // NOTE! No adaptTrans here since that is done in appendCircle()
        appendCircle(pt,radius,color,0.0,fill=true,id=id)
    }

    fun appendCircle(center: Point, radius: Double, color: Int, lineWidth: Double, fill: Boolean = false,id: String?=null)  {
        // <circle cx="50" cy="50" r="40" stroke="sundial.black" stroke-width="3" fill="sundial.red" />
        val c = adaptTrans.transform(center)
        val idStr = if (id != null) " id='$id'" else ""
        sb.append("<circle$idStr cx='${fix1(c.x)}' cy='${fix1(c.y)}' r='${fix1(radius)}' ")

        if (fill)
            sb.append("fill='${colorStr(color)}' stroke='none'/>\n")
        else
            sb.append("stroke='${colorStr(color)}' stroke-width='${fix1(lineWidth)}' stroke-linecap='round' fill='none'/>\n")
    }

    fun appendText(txt: String,pos: Point,theta: Double,color: Int,fSize: Double,addr: String?=null,id: String?=null) {
        val idStr = if (id != null) " id='$id'" else ""
        val p = adaptTrans.transform(pos)
        val x = fixL(p.x)
        val y = fixL(p.y)
        val rot = if (abs(theta*RAD) >= 0.01)
            " transform='rotate(${fix2(-theta*RAD)} $x,$y)'"
        else
            ""

        val html = "<text$idStr x='$x' y='$y' dominant-baseline='central' text-anchor='middle'$rot font-size='${fSize.roundToInt()}' fill='${colorStr(color)}'>$txt</text>".let { h ->
            if (addr != null)
                "<a href='$addr' target='_blank'>$h</a>\n"
            else
                "$h\n"
        }

        sb.append(html)

    }


    fun appendLine(p1: Point,p2: Point,color: Int,lineWidth: Double,pattern: String?,id: String?=null) {
        // https://www.w3schools.com/graphics/svg_stroking.asp
        // <line x1="0" y1="0" x2="200" y2="200" style="stroke:rgb(255,0,0);stroke-width:2" />
        val idStr = if (id != null) " id='$id'" else ""
        val p1t = adaptTrans.transform(p1)
        val p2t = adaptTrans.transform(p2)
        val c = colorStr(color)
        val da = if (pattern != null) " stroke-dasharray='$pattern'" else ""
        sb.append(
            "<line$idStr x1='${fix1(p1t.x)}' y1='${fix1(p1t.y)}' x2='${fix1(p2t.x)}' y2='${fix1(p2t.y)}'"
        )
        sb.append(" stroke='$c' stroke-width='${fix1(lineWidth)}' stroke-linecap='round'$da/>\n")
    }

    fun appendPolyline(pts: List<Point>,color: Int,lineWidth: Double,pattern: String?,id: String?=null) {
        // <polyline points="20,20 40,25 60,40 80,120 120,140 200,180"
        //  style="fill:none;stroke:sundial.black;stroke-width:3" />
        val idStr = if (id != null) " id='$id'" else ""
        val c = colorStr(color)
        val da = if (pattern != null) " stroke-dasharray='$pattern'" else ""
        sb.append("<polyline$idStr points='${getPointStr(adaptTrans.transform(pts))}'")
        sb.append(" stroke='$c' stroke-width='${fix1(lineWidth)}' stroke-linecap='round' fill='none'$da/>\n")
    }

    fun appendSvg(svg: String) = sb.append("$svg\n")

    fun appendRect(r: Rectangle,color: Int,lineWidth: Double,id: String?=null) {
        // Voy!
        val p = adaptTrans.transform(r.pos)
        val S = hypot(r.w,r.h)/2
        val phi = atan2(r.h,r.w) - r.theta
        val dx = S*cos(phi)
        val dy = S*sin(phi)
        val x = fixL(p.x - dx) // The x-position for the top-left corner of the ROTATED rectangle
        val y = fixL(p.y - dy) // The y-position for the top-left corner of the ROTATED rectangle
        val th = fix2(-r.theta*RAD)
        val c = colorStr(color)
        val idStr = if (id != null) " id='$id'" else ""
        sb.append("<rect $idStr width='${fixL(r.w)}' height='${fixL(r.h)}' transform='translate($x,$y) rotate($th)' fill='none' stroke-width='${fixL(lineWidth)}' stroke='$c'/>\n")
    }

    fun appendComment(comment: String) {
        sb.append("<!-- $comment -->\n")
    }

    override fun toString(): String {
        if (sb.indexOf("</svg>") == -1)
            sb.append("</svg>\n")

        return sb.toString()
    }

    companion object {
        fun fix0(v: Double) = v.fix(0)
        fun fix1(v: Double) = v.fix(1)

        fun getPointStr(pts: List<Point>): String {
            val s = StringBuilder()
            val iMax = pts.size - 1

            pts.forEachIndexed { i, pt ->
                s.append("${fix1(pt.x)},${fix1(pt.y)}")
                if (i < iMax)
                    s.append(" ")
            }

            return s.toString()
        }

        fun checkLineIntegrity(g: Geometry, path: List<Point>, id: String?=null) {
            if ((g is Line) && (path.size != 2))
                printLnWrn("Line ${id?:""} has ${path.size} points")
        }

        fun checkPointsAdvancing(id: String?=null,path: List<Point>) {
            var ok = true
            for (i in 0 .. path.size-2)
                if (isTiny(Point.distance(path[i],path[i+1])))
                    ok = false

            if (!ok)
                printLnWrn("Geometry ${id?:""} is not advancing its points")
        }
    }
}

