// Plaine is both plane and plain
class PlaineSundial(parts_: DialParts,
                    width: Double,
                    height: Double,
                    info: Boolean,
                    val showEoT: Boolean = false,
                    val eotFmt: Int = EoTGraph.EOT_FULL_MINUTES,
                    val eotDivisions: Int? = null,
                    val clipBelowHorizon: Boolean = false,
                    val eclLongStep: Int? = 30,
                    val minuteStep: Int? = 60,
                    val numerals: Int = ARABIC_NUMERALS,
                    val symbols: Int = ZOODIAC_SYMBOLS,
                    val anniversaryDT: DateTimeUTC? = null,
                    debug: Boolean = false) : Sundial(parts_, width, height,info=info,debug=debug) {

    private var horizonA: Geometry? = null

    private var hourLines: List<Geometry> = emptyList()

    private var hourTexts: List<Text> = emptyList()

    private var midnightSunConic: Geometry? = null

    private var eastArrow: Arrow? = null

    private var northArrow: Arrow? = null

    private var gnomonFoot: Line? = null

    private var gnomonMarks: Line? = null

    private var horizon0: Line? = null

    private var clipHorizon: Line? = null

    private var hourMinLine: Line? = null

    private var hourMaxLine: Line? = null

    private var hourWedge: Wedge? = null

    private var winterTropic: Geometry? = null

    private var summerTropic: Geometry? = null

    private var topArrow: Arrow? = null

    private var worldPlaneMarks: Line? = null

    private var horizonPlaneMarks: Line? = null

    private var gnomonBar:Line? = null

    var eotGraph: EoTGraph? = null
        private set

    var zodiacGraphSpring: ZodiacGraph? = null
        private set

    var zodiacGraphAutumn: ZodiacGraph? = null
        private set

    var anchor: Anchor? = null
        private set

    private var enclosing: List<Rectangle>? = null

    private var anniversary: DialParts.Anniversary? = null

    override fun createScaleModel(): Map<String, DialItem> {
        val itms = mutableMapOf<String,DialItem>()

        printLnTmp(GREEN, "Building scale model from at most 6 elements")
        // The 6 basic elements of the dial:

        // 1) A circle (ConicSection) corresponding to a minimum altitude above the dial for the sun. If the sun is
        // lower than minAltitude but above the horizon there is no guarantee that the shadow falls within the dial
        horizonA = parts.getDialAltitudeConic(parts.minAltitude* DEG)?.also {
            itms[ALT_LIM] = DialItem(it,AngleSet.ANGLE_SET,parts.minAltitude* DEG)
            it.label = ALT_LIM
            if (info) {
                printLnTmp(GREEN, "$ALT_LIM a limiting circle corresponding to solar height ${fixD(parts.minAltitude * DEG)}. radius=${fixL((it as ConicSection).axes.a)}")
            }
        }

        // 2) ConicSection denoting the sun's daily motion at midwinter solstice at the flat dial's location
        winterTropic = parts.winterTropic?.also {
            it.outside = true
            itms[WINTER_TROPIC] = DialItem(it,AngleSet.ANGLE_SET,parts.eclLng(parts.winterTropicDeclination))
            it.label = WINTER_TROPIC
            if (info) {
                printLnTmp(GREEN, "$WINTER_TROPIC a conic section representing the solar declination ${fixD(parts.winterTropicDeclination)}")
            }
        }

        // 3) ConicSection denoting the sun's daily motion at midsummer solstice at the flat dial's location
        summerTropic = parts.summerTropic?.also {
            it.outside = true
            itms[SUMMER_TROPIC] = DialItem(it,AngleSet.ANGLE_SET,parts.eclLng(parts.summerTropicDeclination))
            it.label = SUMMER_TROPIC
            if (info) {
                printLnTmp(GREEN, "$SUMMER_TROPIC a conic section representing the solar declination ${fixD(parts.summerTropicDeclination)}")
            }
        }

        // 4) Line denoting the first morning hour to be displayed on this dial
        hourMinLine = parts.hourMinLine?.also {
            itms[HOUR_MIN] = DialItem(it,RealSet.EMPTY_SET,parts.hourAngles.first())
            it.label = HOUR_MIN
            if (info) {
                printLnTmp(GREEN, "$HOUR_MIN a line representing the the hour ${HMS(parts.getZoneHourByHA(parts.hourAngles.first()))}")
            }
        }

        // 5) Line denoting the last evening hour to be displayed on this dial
        hourMaxLine = parts.hourMaxLine?.also {
            itms[HOUR_MAX] = DialItem(it,RealSet.EMPTY_SET,parts.hourAngles.last())
            it.label = HOUR_MAX

            if (info) {
                printLnTmp(GREEN, "$HOUR_MAX a line representing the hour ${HMS(parts.getZoneHourByHA(parts.hourAngles.last()))}")
            }
        }

        hourMinLine?.let { hMin ->
            hourMaxLine?.let { hMax ->
                hourWedge = Wedge(hMin,hMax).also {
                    it.label = "HOUR_WEDGE"
                }
            }
        }

        // 6) A line (degenerate conic) corresponding to 0 of altitude above the horizon. A horizontal
        // dial does not have a 'horizon0'
        horizon0 = parts.horizonTrace?.also {
            itms[HORIZON_TRACE] = DialItem(it, RealSet.EMPTY_SET,0.0, flags = STRONG)
            it.label = HORIZON_TRACE
            if (info) {
                printLnTmp(GREEN, "$HORIZON_TRACE a line  representing solar altitude 0")
            }
        }

        printLnTmp(GREEN,"Clipping elements to determine dimension of model")

        // The horizon0 may, or may not be used to clip away parts of the dial where the gnomon's shadow
        // never falls
        clipHorizon = if (clipBelowHorizon) horizon0 else null

        // CLIPPING!
        // Clip each basic element by the others to obtain a first draft of the dial
//        val insideAny = parts.isInsideEither ?: false

        winterTropic?.let {
            val domain = Clipper.clipByClippers(it,horizonA,clipHorizon,hourWedge,debug=debug)

            if (debug) println(domain)

            if (info)
                printLnTmp(GREEN,"$WINTER_TROPIC, domain:$domain")

            if (domain.isEmptySet())
                itms.remove(WINTER_TROPIC)
            else
                itms[WINTER_TROPIC]?.domain = domain
        }

        summerTropic?.let {
            val domain = Clipper.clipByClippers(it,horizonA,clipHorizon,hourWedge,debug=debug)

            if (debug) println(domain)

            if (info)
                printLnTmp(GREEN,"$SUMMER_TROPIC, domain:$domain")

            if (domain.isEmptySet())
                itms.remove(SUMMER_TROPIC)
            else
                itms[SUMMER_TROPIC]?.domain = domain
        }

        horizonA?.let { hzA ->
            val domain = Clipper.clipByClippers(hzA,winterTropic,summerTropic,clipHorizon,hourWedge,debug=debug)

            if (debug) println(domain)

            if (info)
                printLnTmp(GREEN,"$ALT_LIM, domain:$domain")

            if (domain.isEmptySet())
                itms.remove(ALT_LIM)
            else
                itms[ALT_LIM]?.domain = domain
        }

        horizon0?.let { h0 ->
            val domain = Clipper.clipByClippers(h0,horizonA,winterTropic,summerTropic,hourWedge,debug=debug)

            if (debug) println(domain)

            if (info)
                printLnTmp(GREEN,"$HORIZON_TRACE, domain:$domain")

            if (domain.isEmptySet())
                itms.remove(HORIZON_TRACE)
            else
                itms[HORIZON_TRACE]?.domain = domain
        }

        hourMinLine?.let { hMin ->
            val domain = Clipper.clipByClippers(hMin,clipHorizon,horizonA,winterTropic,summerTropic,debug=debug)

            if (debug) println()

            if (info)
                printLnTmp(GREEN,"$HOUR_MIN, domain:$domain")

            if (domain.isEmptySet())
                itms.remove(HOUR_MIN)
            else
                itms[HOUR_MIN]?.domain = domain
        }

        hourMaxLine?.let { hMax ->
            val domain = Clipper.clipByClippers(hMax,clipHorizon,horizonA,winterTropic,summerTropic,debug=debug)

            if (debug) println()

            if (info)
                printLnTmp(GREEN,"$HOUR_MAX, domain:$domain")

            if (domain.isEmptySet())
                itms.remove(HOUR_MAX)
            else
                itms[HOUR_MAX]?.domain = domain
        }

        if (itms.isEmpty()/* && debug*/) {
            logC(MAGENTA,"These items fail to create a model:")
            winterTropic?.let {
                logC(MAGENTA,"$WINTER_TROPIC ${it.construction()}")
            }
            summerTropic?.let {
                logC(MAGENTA,"$SUMMER_TROPIC ${it.construction()}")
            }
            horizonA?.let {
                logC(MAGENTA,"$ALT_LIM ${it.construction()}")
            }
            horizon0?.let {
                logC(MAGENTA,"$HORIZON_TRACE ${it.construction()}")
            }
            hourMinLine?.let {
                logC(MAGENTA,"$HOUR_MIN ${it.construction()}")
            }
            hourMaxLine?.let {
                logC(MAGENTA,"$HOUR_MAX ${it.construction()}")
            }
        }

        return itms
    }

    override suspend fun addMeasuredGeometries(textSize: Double, textRotation: Double) {

        if (minuteStep != null)
            hourLines = buildList {
                for (minute in 0 until 60 * 24 step minuteStep) {
                    val zoneHour = minute/60.0
                    if (parts.hourAngles.contains(parts.getHourAngleByZH(zoneHour))) {
                        parts.getLineByZH(zoneHour)?.let { ln ->
                            val label = "${HOUR_LN}_${HM(zoneHour)}"
                            ln.label = label
                            val domain = Clipper.clipByClippers(ln,innerRect,horizonA,winterTropic,summerTropic,clipHorizon)
                            if (!domain.isEmptySet()) {
                                items[label] = DialItem(ln,domain,zoneHour,flags=if (isInteger(zoneHour)) STRONG else 0) // pId is decimal hour
                                add(ln)
                                if (info)
                                    printLnTmp(GREEN,"$label, domain: $domain")
                            }
                        }
                    }
                }
            }

        if (numerals != NO_NUMERALS)
            hourTexts = kotlin.run {
                val lst = mutableListOf<Text>()
                val itm = mutableMapOf<String,DialItem>()

                items.entries.filter { e ->
                    // Get all HOUR_LINEs for integer hours
                    // TODO e.key.startsWith(HOUR_LINE) && e.strong
                    e.key.startsWith(HOUR_LN) && isInteger(e.value.pId) // pId is decimal hour
                }.forEach { e ->
                    try {
                        items[e.key]?.domain?.itvl()?.first()?.let { itv ->
                            val t = itv.rightValue() + textSize

                            e.value.geometry.toPoint(t)?.let { P ->
                                val hour = e.value.pId.roundToInt()

                                val s = if (numerals == ROMAN_NUMERALS)
                                    roman(if (hour==0) 24 else hour)
                                else
                                    hour.toString()

                                val txt = Text(measuredText(s,textSize),textRotation,P)
                                measureText(txt)
                                val label = "${HOUR_TEXT}_$s"
                                itm[label] = DialItem(txt,txt.fullDomain, Double.NaN)

                                lst.add(txt)

                                if (info)
                                    printLnTmp(GREEN,"$label: $s")
                            }
                        }
                    } catch (nsee: NoSuchElementException) {
                        logC(RED,nsee.message ?: nsee::class.simpleName ?: "error")
                    }
                }

                items.putAll(itm)

                lst
            }

        parts.declMidnightSun?.let { decl ->
            midnightSunConic = parts.getDeclinationConic(decl)?.also {
                val domain = Clipper.clipByClippers(it,innerRect,horizonA,clipHorizon,hourWedge)

                if (!domain.isEmptySet()) {
                    items[MIDNIGHT_SUN_CONIC] = DialItem(it, domain, parts.eclLng(decl))
                    it.label = MIDNIGHT_SUN_CONIC

                    if (info)
                        printLnTmp(GREEN,"$MIDNIGHT_SUN_CONIC, domain: $domain")
                }
            }
        }

        // Create the graph of day-lines representing solar ecliptic longitudes
        if (eclLongStep != null)
            ZodiacGraph(parts,eclLongStep,symbols,textSize,textRotation,::clipConic).also { zg ->
                zg.conics.entries.forEach { (k,itm) ->
                    // Get the conic and clip it to fit on the dial
                    itm.domain = Clipper.clipByClippers(itm.geometry,innerRect,horizonA,clipHorizon,hourWedge,debug=debug)

                    // Add the conic to the dial
                    items[k] = itm

                    if (info)
                        printLnTmp(GREEN,"$k, domain: ${itm.domain}")
                }

                zg.getSymbolsWithPositions(zg.conics).entries.forEach { (key,symbol) ->
                    // Add the symbol to the dial
                    items[key] = symbol
                    measureText(symbol.geometry as Text)

                    if (info)
                        printLnTmp(GREEN,"$key: ${symbol.geometry.mt.txt}")
                }
            }

        if (showEoT)
            eotGraph = EoTGraph(
                parts,textSize,textRotation,::clipConic,
                if (eotFmt == EoTGraph.EOT_HALF_MINUTES) EoTGraph::fmtHalf else if (eotFmt == EoTGraph.EOT_FULL_MINUTES) EoTGraph::fmtMin else EoTGraph::fmtMin,
                eotN = eotDivisions,
                info = info
            ).also { eotg ->

                eotg.springConics.entries.forEach { (k,itm) ->
                    // Get the conic and clip it to fit on the dial
                    itm.domain = Clipper.clipByClippers(itm.geometry,innerRect,horizonA,clipHorizon,hourWedge,debug=debug)

                    // Add the conic to the dial
                    items[k] = itm

                    if (info)
                        printLnTmp(GREEN,"$k, domain: ${itm.domain}")
                }

                eotg.getNumbersWithPositions(true).entries.forEach { (key,number) ->
                    // Add the symbol to the dial
                    items[key] = number
                    measureText(number.geometry as Text)

                    if (info)
                        printLnTmp(GREEN,"$key: ${number.geometry.mt.txt}")
                }

                eotg.getNumbersWithPositions(false).entries.forEach { (key,number) ->
                    // Add the symbol to the dial
                    items[key] = number
                    measureText(number.geometry as Text)

                    if (info)
                        printLnTmp(GREEN,"$key: ${number.geometry.mt.txt}")
                }
            }
    }

    private fun clipConic(conic: Geometry) =
        Clipper.clipByClippers(conic, innerRect, horizonA, clipHorizon,hourWedge,debug=debug)

    private fun clipHourLine(hourLine: Line) =
        Clipper.clipByClippers(hourLine,innerRect,horizonA,clipHorizon,summerTropic,winterTropic,debug=debug)

    override suspend fun addNotMeasuredExtras(textSize: Double,textRotation: Double) {
        // Rescaling for the above clip functions
        innerRect?.label = INNER_RECT
        horizonA = items[ALT_LIM]?.geometry
        horizonA?.label = ALT_LIM
        summerTropic = items[SUMMER_TROPIC]?.geometry
        summerTropic?.flip()
        summerTropic?.label = SUMMER_TROPIC
        winterTropic = items[WINTER_TROPIC]?.geometry
        winterTropic?.flip()
        winterTropic?.label = WINTER_TROPIC
        clipHorizon = clipHorizon?.scale(scale ?: 1.0)
        clipHorizon?.label = HORIZON_TRACE
        hourWedge = items[HOUR_MIN]?.geometry?.let { hMin ->
            items[HOUR_MAX]?.geometry?.let { hMax ->
                Wedge(hMin as Line,hMax as Line).also {
                    it.label = HOUR_WEDGE
                }
            }
        }

        outerRect?.let { outer ->
            innerRect?.let { inner ->
                val center = inner.pos

                val drawRadiant = inner.let { r ->
                    val hit = r.hit(parts.radiant)

                    (hit == Geometry.HIT_INSIDE) || (hit == Geometry.HIT_ON)
                }


                gnomonFoot = parts.getGnomonFoot(MARK_SIZE).also { ln ->
                    val domain = if (drawRadiant) ln.fullDomain else RealSet(CLOSED, 0.0, 5.0/*MARK_LENGTH*/, CLOSED)

                    if (!domain.isEmptySet()) {
                        items[GNOMON_LINE] = DialItem(ln, domain, domain.span)
                        ln.label = GNOMON_LINE
                    }
                }

                gnomonBar = gnomonFoot?.let { foot ->
                    Line(
                        Line(Point.ORIGO, Vector(-foot.u.delY, foot.u.delX)), fullDomain = RealSet(
                            CLOSED, -2.5, 2.5, CLOSED
                        )
                    ).also {
                        items[GNOMON_BAR] = DialItem(it, it.fullDomain, it.fullDomain.span)
                        it.label = GNOMON_BAR
                    }
                }

                parts.north?.let {
                    val domain = Line(center, it, fullDomain = RealSet.R).let { ln ->
                        inner.flip()  // Clip away inside of inner
                        val dom = Clipper.clipByClippers(ln, outer, inner)
                        inner.flip()

                        dom
                    }
                    // Marks the intersection with the world-plane through the meridian with
                    // two dots by the edges
                    worldPlaneMarks = Line(center, it, fullDomain = domain).also { ln ->
                        items[WORLD_PLANE_MARKS] = DialItem(ln, domain, 1.0)
                        ln.label = WORLD_PLANE_MARKS
                    }
                }

                horizon0?.let { hor0 ->
                    val domain = Line(center, hor0.u, fullDomain = RealSet.R).let { ln ->
                        inner.flip()  // Clip away inside of inner
                        val dom = Clipper.clipByClippers(ln, outer, inner)
                        inner.flip()

                        dom
                    }

                    // Marks the intersection with the horizon-plane with two dots by the edges
                    horizonPlaneMarks = Line(center, hor0.u, fullDomain = domain).also { ln ->
                        items[HORIZON_PLANE_MARKS] = DialItem(ln, domain, 1.0)
                        ln.label = HORIZON_PLANE_MARKS
                    }
                }

                gnomonFoot?.let { foot ->
                    val domain = Line(foot, fullDomain = RealSet.R).let { ln ->
                        inner.flip()  // Clip away inside of inner
                        val dom = Clipper.clipByClippers(ln, outer, inner)
                        inner.flip()

                        dom
                    }

                    // Marks the intersection with the plane of the style by two dots at the edges
                    gnomonMarks = Line(foot, fullDomain = domain).also { ln ->
                        items[STYLE_PLANE_MARKS] = DialItem(ln, domain, 1.0)
                        ln.label = STYLE_PLANE_MARKS
                    }
                }


                parts.getHorizonAltitudeConic(10*DEG)?.also { conic ->
                    conic.label = REFRACT_LIM
                    Clipper.clipByClippers(conic,innerRect,horizonA,clipHorizon,hourWedge,summerTropic,winterTropic,debug=false).also { domain ->
                        if (!domain.isEmptySet())
                            items[REFRACT_LIM] = DialItem(conic,domain,10*DEG)
                    }
                }
            }
        }

        innerRect?.let { r ->
            val C = Point(r.w/2 - 55.0,r.h/2 - 55.0).rotate(r.theta).translate(r.x0,r.y0)

            eastArrow = parts.east?.let { east ->
                val P = Point(C.x,C.y+2)
                Arrow(P+east.times(-25.0),P+east.times(25.0)).also {
                    items[EAST_ARROW] = DialItem(it,it.fullDomain,it.length)
                    it.label = EAST_ARROW
                }
            }

            northArrow = parts.north?.let { north ->
                val P = Point(C.x,C.y+2)
                Arrow(P+north.times(-25.0),P+north.times(25.0)).also {
                    items[NORTH_ARROW] = DialItem(it,it.fullDomain,it.length)
                    it.label = NORTH_ARROW
                }
            }

            topArrow = parts.top.let { up ->
                val P = Point(C.x,C.y-2)
                Arrow(P+up.times(-25.0),P+up.times(25.0)).also {
                    items[TOP_ARROW] = DialItem(it,it.fullDomain,it.length)
                    it.label = TOP_ARROW
                }
            }

            Vector.I.let { I ->
                val P = Point(C.x,C.y-2)
                Arrow(P+I.times(-25.0),P+I.times(25.0)).also {
                    items[I_ARROW] = DialItem(it,it.fullDomain,it.length)
                    it.label = I_ARROW
                }
            }

            Vector.J.let { J ->
                val P = Point(C.x,C.y-2)
                Arrow(P+J.times(-25.0),P+J.times(25.0)).also {
                    items[J_ARROW] = DialItem(it,it.fullDomain,it.length)
                    it.label = J_ARROW
                }
            }
        }


        anniversaryDT?.let { anvDT ->
            parts.getAnniversary(anvDT,innerRect!!)?.let { anniv ->
                anniversary = anniv

                 anniversary?.let { (_,elng,cs,hl,pt) ->
                     innerRect?.let { irect ->
                         cs?.also {
                             it.label = ANNIV_CONIC
                             val domain = clipConic(it)
                             if (!domain.isEmptySet()) {
                                 items[ANNIV_CONIC] = DialItem(cs, domain,elng)
                             }
                         }

                         hl?.also {
                             it.label = ANNIV_HOUR
                             val domain = clipHourLine(it)
                             if (!domain.isEmptySet()) {
                                 items[ANNIV_HOUR] = DialItem(hl, domain, Double.NaN)
                             }
                         }

                         pt?.let {
                                 Star(it, textSize).also { star ->
                                 star.label = ANNIV_MARK
                                 items[ANNIV_MARK] = DialItem(star, star.fullDomain, Double.NaN)
                             }
                         }
                     }
                 }
            }
        }

        innerRect?.let { inner ->
            dropAnchorAt(inner,convexHull,dial=this/*dial=if (debug) this else null*/)?.let { (pts,p) ->
                val size = (textSize/5).let {
                    if (it < 1.0) 1.0 else it
                }
                anchor = Anchor(measuredText(HOMEPAGE_ADDRESS,size),textRotation,Point(p.x,p.y+size/16),HOMEPAGE_ADDRESS).also { a ->
                    items[ANCHOR] = DialItem(a,a.fullDomain,Double.NaN)
                }
            }

        }
    }

    private fun clippedHourSpan(itm: DialItem?) = itm?.let {
        if (it.geometry is ConicSection)
            clippedHourSpan(it.geometry, it.domain as AngleSet)
        else
            0.0
    } ?: 0.0

    private fun clippedHourSpan(g: Geometry, domain: RealSet): Double {
        var span = 0.0

        domain.itvl().forEach { itv ->
            g.toPoint(itv.leftValue())?.let { pt0 ->
                g.toPoint(itv.rightValue())?.let { pt1 ->
                    span += abs(clampPI(parts.getHourAngleOfPoint(pt1) - parts.getHourAngleOfPoint(pt0)))
                }
            }
        }

        return span
    }

    fun shortDescript() = "${parts.shortDescript()} W:${outerRect?.w?.let { fixL(it) } ?: "-"} H:${outerRect?.h?.let {
        fixL(it)
    } ?: "-"}"

    /**
     * "The style is the time-telling edge of the gnomon .." https://www.wikiwand.com/en/Sundial
     *
     * The style is the triangle formed by the gnomon, the line from the tip of the gnomon to the radiant, and
     * the line from the radiant back to the base of the gnomom (the origin).
     * If the radiant is far from the origin, the style can become very long.
     * The shadow of the upper edge of the style falls parallel to the hour lines.
     * This is an attempt to truncate the style so that the length of this shadow matches the
     * length of the longest hour-line on the dial
     *
     * That [truncatedStyle] har the right shape and length has been verified by OpenSCAD
     */
    val truncatedStyle: Point?  by lazy {
        if (info) printLnTmp(YELLOW,"Computing the truncated style")

        val K = cos(parts.flatLat)*hypot(parts.G,parts.F)
        Analytics(parts.hourAngles.first(),parts.hourAngles.last(),10,0.25*MINUTES,info = info) { ha ->
            // This lambda-function is the function to be analyzed

            parts.getLineByHA(ha+PI)?.let { hl -> // PI added to get the shadow
                hl.label = HMS(parts.getZoneHourByHA(ha))

                Clipper.clipByClippers(hl, innerRect, horizonA, summerTropic, winterTropic, clipHorizon,debug=debug).let { d ->

                    if (d.isEmptySet())
                        Double.NEGATIVE_INFINITY
                    else {
                        // Compute the length of style necessary to throw d.last()-d.first() length of shadow
                        parts.F - K*d.first()/d.last()
                    }
                }
            } ?: Double.NEGATIVE_INFINITY
        }.maximum.let { (maxHa,maxL) ->

            parts.getLineByHA(maxHa)?.also { line ->
                val domain = Clipper.clipByClippers(line,innerRect,horizonA,clipHorizon,summerTropic,winterTropic)
                if (!domain.isEmptySet())
                    items[LONGEST_HOUR_LINE] = DialItem(line,domain,parts.getZoneHourByHA(maxHa)/HOURS)
            }

            printLnTmp(GREEN,"Style is truncated at ${fixL(maxL)}mm for shadow to cover zone hour ${HMS(parts.getZoneHourByHA(maxHa))}")

            Point(sign(parts.radiant.y)*maxL,(parts.G/parts.F)*(parts.F - maxL))
        }
    }


    fun getStylePolygon(): List<Point> {

        val poly = mutableListOf<Point>()

        // Create a style polygon, flat on the dial such that rotating it -90 about the y-axis
        // brings it into its functional  position

        truncatedStyle?.let { style ->
            poly.add(Point.ORIGO)
            poly.add(Point(parts.G, 0.0))
            poly.add(Point(style.y,style.x))
            poly.add(Point(0.0, style.x))
        }

        return poly.toList()
    }

    /**
     * Builds a polygon of Points that may be extruded and serve as a base for the dial when
     * it is to be 3D printed
     */
    fun getBasePolygon(): List<Point>? {
        val pts = mutableListOf<Point>()

        outerRect?.let { orect ->
            val pms = mutableListOf<Double>(0.0,orect.w,orect.w+orect.h,orect.w+orect.h+orect.w)

            parts.getGnomonFoot().let { footLn ->
                Clipper.intersects(footLn,orect).let { lst ->
                    var tMin = Double.POSITIVE_INFINITY
                    lst.forEach { p ->
                        footLn.toParameter(p)?.let { t ->
                            if ((0.0 <= t) && (t < tMin)) tMin = t
                        }
                    }
                    // tMin is parameter of intersection point between orect and footLn closest to origin/base of gnomon
                    if (tMin == Double.POSITIVE_INFINITY) null else footLn.toPoint(tMin)
                }?.let { pI -> // Intersection point of orect and ln closest to origin/base of gnomon
                    truncatedStyle?.x?.let { l ->
                        footLn.toPoint(l)?.let { pLn -> // Point at the truncated end of the style

                            val hit = orect.hit(pLn)
                            if ((hit == Geometry.HIT_ON) || (hit == Geometry.HIT_INSIDE))
                            // The style is entirely on the orect
                                pts.addAll(orect.corners())
                            else {
                                orect.toParameter(pI)?.let { r -> // Parameter of intersection point for orect
                                    // Points to either side of intersection point
                                    val rMin = ((r - textSize / 2) % orect.S).let {
                                        if (it < 0.0) it + orect.S else it
                                    }
                                    val rMax = ((r + textSize / 2) % orect.S).let {
                                        if (it < 0.0) it + orect.S else it
                                    }
                                    pms.add(rMin)
                                    pms.add(rMax)
                                    pms.sort()

                                    // pms now contains parameters for points on the orect in sorted order
                                    // Generate the points
                                    pms.forEach { s ->
                                        orect.toPoint(s)?.let { pt ->
                                            pts.add(pt)
                                        }
                                    }

                                    // Insert point below the truncated end of the style
                                    pms.indexOf(rMax).let { ins ->
                                        if (ins != -1) {
                                            pts.add(ins,pLn)
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }

        return if (pts.size < 3)
            null
        else
            pts.toList()
    }

    override fun printPartsInfo() {
        // A transform to align the OUTER_RECT with the coordinate axes and int the first quadrant
        val trans = (items[OUTER_RECT]?.geometry as Rectangle?)?.let{ outer ->
            // This Transformation should be a parameter in the construction()-methods
            Transformation.catenate(
                Transformation.translatinon(
                    Vector(
                        outer.w / 2,
                        outer.h / 2
                    )
                ), // Done third: Move rectangle into first quadrant (not strictly necessary)
                Transformation.rotation(-outer.theta), // Done second: Align rectangle with axes
                Transformation.translatinon(Vector(-outer.x0, -outer.y0)) // Done first: Center rectangle at Origin
            )
        } ?: Transformation.IDENTITY

        enclosing?.let {
            it.forEach { r ->
                printLn(WHITE, "${r.label} $r\n")
            }
        }

        printLn(GREEN,"Parts NOT drawn on the dial:")
        items[INNER_RECT]?.let {
            printLn(LT_GRAY, "$INNER_RECT ${it.geometry.construction(it.domain,trans,null)}")
        }

        items[WINTER_TROPIC]?.let {
            printLn(CYAN, "$WINTER_TROPIC ${it.geometry.construction(it.domain,trans,zooSignSansBG(it.pId))}")
        }

        items[SUMMER_TROPIC]?.let {
            printLn(GREEN, "$SUMMER_TROPIC ${it.geometry.construction(it.domain,trans,zooSignSansBG(it.pId))}")
        }

        items[HOUR_MIN]?.let {
            printLn(PINK, "$HOUR_MIN ${it.geometry.construction(it.domain,trans,null)}")
        }

        items[HOUR_MAX]?.let {
            printLn(MAGENTA, "$HOUR_MAX ${it.geometry.construction(it.domain,trans,null)}")
        }

        printLn(GREEN, "Parts drawn on the dial:")

        items[OUTER_RECT]?.let {
            printLn(LT_GRAY, "$OUTER_RECT ${it.geometry.construction(it.domain,trans,null)}")
        }

        items[ALT_LIM]?.let {
            printLn(PINK, "$ALT_LIM ${it.geometry.construction(it.domain,trans,"alt=${fixD(it.pId)}")}")
        }

        items[REFRACT_LIM]?.let {
            printLn(PINK, "$REFRACT_LIM ${it.geometry.construction(it.domain,trans,"alt=${fixD(it.pId)}")}")
        }

        items[HORIZON_TRACE]?.let {
            printLn(RED, "$HORIZON_TRACE ${it.geometry.construction(it.domain,trans,null)}")
        }

        items.entries.filter {
            it.key.startsWith(HOUR_LN)
        }.forEach {
            printLn(GRAY, "${it.key} ${it.value.geometry.construction(it.value.domain,trans,null)}")
        }


        items.entries.filter {
            it.key.startsWith(EOT_CONIC)
        }.forEach {
            printLn(GREEN, "${it.key} ${it.value.geometry.construction(it.value.domain,trans,"EoT=${EoTGraph.fmtSec(parts.eot.minutes(it.value.pId))}")}")
        }

        items.entries.filter {
            it.key.startsWith(ZODIAC_CONIC)
        }.forEach {
            printLn(LT_GRAY, "${it.key} ${it.value.geometry.construction(it.value.domain,trans,"=${fixD(clamp2PI(it.value.pId))} and ${fixD(clamp2PI(PI - it.value.pId))}")}")
        }

        items[MIDNIGHT_SUN_CONIC]?.let {
            val s = "=${fixD(it.pId)} and ${fixD(PI - it.pId)}"
            printLn(CYAN, "$MIDNIGHT_SUN_CONIC ${it.geometry.construction(it.domain,trans,s)}")
        }

        items[GNOMON_LINE]?.let {
            printLn(BLUE, "$GNOMON_LINE ${it.geometry.construction(it.domain,trans,null)}")
        }

        items[GNOMON_BAR]?.let {
            printLn(BLUE, "$GNOMON_BAR   ${it.geometry.construction(it.domain,trans,null)}")
        }

        items[STYLE_PLANE_MARKS]?.let {
            endPoints(it.geometry,it.domain)?.let { (p0,p1) ->
                printLn(BLUE,"$STYLE_PLANE_MARKS ${trans.transform(p0)} and ${trans.transform(p1)}")
            }
        }

        items[HORIZON_PLANE_MARKS]?.let {
            endPoints(it.geometry,it.domain)?.let { (p0,p1) ->
                printLn(RED,"$HORIZON_PLANE_MARKS ${trans.transform(p0)} and ${trans.transform(p1)}")
            }
        }

        items[WORLD_PLANE_MARKS]?.let {
            endPoints(it.geometry,it.domain)?.let { (p0,p1) ->
                printLn(CYAN,"$WORLD_PLANE_MARKS ${trans.transform(p0)} and ${trans.transform(p1)}")
            }
        }

        eotGraph?.let {
            printLn(GREEN, it.description(true))
            printLn(ORANGE, it.description(false))
        }

        zodiacGraphSpring?.let { printLn(GREEN, it) }

        zodiacGraphAutumn?.let { printLn(ORANGE, it) }

        anniversary?.let { aniv ->
            printLn(YELLOW, "Anniversary on ${aniv.dt}.  at ${zooLen(aniv.eclLong)} ")

            items[ANNIV_CONIC]?.let { itm ->
                val s = "=${fixD(itm.pId)} and ${fixD(PI - itm.pId)}"
                printLn(YELLOW, "$ANNIV_CONIC ${itm.geometry.construction(itm.domain,trans,s)}")
            }

            if (!aniv.isSunUp)
                printLn(YELLOW,"The Sun is below the horizon at the time of anniversary. ")
            else if (!aniv.isSunInFront)
                printLn(YELLOW,"The Sun is behind the plane of the dial at the time of anniversary. ")
            else if (!aniv.isOnDial)
                printLn(YELLOW,"The shadow at the time of anniversary does not fall within the dial's area.")
            else
                items[ANNIV_HOUR]?.let { itm ->
                    if (!itm.domain.isEmptySet())
                        printLn(YELLOW,"$ANNIV_HOUR ${itm.geometry.construction(itm.domain,trans,null)}")
                }
        }

        items.filter { e ->
            e.key.startsWith(LONGEST_HOUR_LINE)
        }.forEach { e ->
            printLn(YELLOW, "Hour determining truncated style: ${HMS(e.value.pId)}")
        }
    }

    companion object {
        const val NO_NUMERALS = 0
        const val ARABIC_NUMERALS = 1
        const val ROMAN_NUMERALS = 2

        const val NO_SYMBOLS = 0
        const val ZOODIAC_SYMBOLS = 1
        const val ABBREVIATED_SYMBOLS = 2

        const val MARK_SIZE = BORDER_WIDTH

        const val ALT_LIM = "ALT_LIM"
        const val WINTER_TROPIC = "WINTER_TROPIC"
        const val SUMMER_TROPIC = "SUMMER_TROPIC"
        const val HOUR_MIN = "HOUR_MIN"
        const val HOUR_MAX = "HOUR_MAX"
        const val HOUR_LN = "HOUR_LN"
        const val HOUR_WEDGE = "HOUR_WEDGE"
        const val HORIZON_TRACE = "HORIZON_TRACE"
        const val REFRACT_LIM = "REFRACT_LIM"
        const val SPRING_TEXT = "SPRING_TEXT"
        const val EOT_CONIC = "EOT_CONIC"
        const val AUTUMN_TEXT = "AUTUMN_TEXT"
        const val ZODIAC_CONIC = "ZODIAC_CONIC"
        const val ZODIAC_TEXT = "ZODIAC_TEXT"
        const val MIDNIGHT_SUN_CONIC = "MIDNIGHT_SUN_CONIC"
        const val GNOMON_LINE = "GNOMON_LINE"
        const val GNOMON_BAR = "GNOMON_BAR"
        const val WORLD_PLANE_MARKS = "WORLD_PLANE_MARKS"
        const val HORIZON_PLANE_MARKS = "HORIZON_PLANE_MARKS"
        const val STYLE_PLANE_MARKS = "STYLE_PLANE_MARKS"
        const val EAST_ARROW = "EAST_ARROW"
        const val NORTH_ARROW = "NORTH_ARROW"
        const val TOP_ARROW = "TOP_ARROW"
        const val ANNIV_CONIC = "ANNIV_CONIC"
        const val ANNIV_HOUR = "ANNIV_HOUR"
        const val ANNIV_MARK = "ANNIV_MARK"
        const val HOUR_TEXT = "HOUR_TEXT"
        const val LONGEST_HOUR_LINE = "LONGEST_HOUR_LINE"
        const val POINTS = "POINTS"
        const val I_ARROW = "I_ARROW"
        const val J_ARROW = "J_ARROW"
        const val ANCHOR = "ANCHOR"

        const val STRONG = 0x1 // Bit #0: 0 - not strong, 1 - strong
        const val DASH_MASK = 0b110
        const val DASHED0 = 0x2 // Bit #1: 0 - not dashed, 1 - dashed
        const val DASHED1 = 0x4 // Bit #2: 0 - not dashed, 1 - dashed

        const val THIN_LINE = 0.2

//        fun getBits(flags: Int,mask: Int) = flags and mask

//        fun setBits(flags: Int,mask: Int,bits: Int) = (flags and mask.inv()) or (bits and mask)

        fun getNumeralName(numeral: Int?) = when (numeral) {
            null -> "NUMERAL NOT SET (null)"
            ARABIC_NUMERALS -> "ARABIC NUMERALS"
            ROMAN_NUMERALS -> "ROMAN NUMERALS"
            NO_NUMERALS -> "NO NUMERALS"
            else -> "UNKNOWN NUMERALS"
        }

        fun getSymbolName(numeral: Int?) = when (numeral) {
            null -> "SYMBOL NOT SET (null)"
            ZOODIAC_SYMBOLS -> "ZOODIAC SYMBOLS"
            ABBREVIATED_SYMBOLS -> "ABBREVIATED SYMBOLS"
            NO_NUMERALS -> "NO SYMBOLS"
            else -> "UNKNOWN SYMBOLS"
        }

        fun endPoints(geometry: Geometry,domain: RealSet) = geometry.toPoint(domain.first())?.let { p0 ->
            geometry.toPoint(domain.last())?.let { p1 ->
                Pair(p0,p1)
            }
        }

        fun render(dial: PlaineSundial, rnd: Renderer2) {
            val lw = 0.04*dial.textSize*(dial.scale ?: 1.0)

            with (dial) {
                // Search for overlapping ConicSections
                fun isOverlapping(cs1: ConicSection,cs2: ConicSection) = abs(cs1.theta-cs2.theta).let { delTheta ->
                    if ((abs(delTheta) < 0.1*DEG) || (abs(delTheta - TWO_PI) < 0.1*DEG)) {
                        cs1.polar.let { p1 ->
                            cs2.polar.let { p2 ->
                                if ((abs(p1.l - p2.l) < 1.0) && (abs(p1.e - p2.e) < 0.01) && (cs1.branch == cs2.branch)) {
                                    Point.distance(cs1.pos,cs2.pos) < lw
                                }
                                else
                                    false
                            }
                        }
                    }
                    else
                        false
                }

                items.keys.mapNotNull { key ->
                    // Make a list of all items keys
                    if (items[key]?.geometry is ConicSection)
                        key
                    else
                        null
                }.also { keyLst ->
                    for (i in 0..keyLst.size - 2)
                        for (j in i + 1..<keyLst.size) {
                            items[keyLst[i]]?.let { itm1 ->
                                items[keyLst[j]]?.let { itm2 ->
                                    if (!isTiny(abs(itm1.pId) - PI_HALF))
                                        (itm1.geometry as ConicSection).let { cs1 ->
                                            (itm2.geometry as ConicSection).let { cs2 ->
                                                // Check overlap for pairs of ConicSections
                                                // TODO What happens if 3 ConicSections overlap?
                                                if (isOverlapping(cs1, cs2)) {
                                                    printLnTmp(
                                                        MAGENTA,
                                                        "${keyLst[i]} and ${keyLst[j]} are overlapping "
                                                    )
                                                    // TODO This assumes both conics have approximately the same domain
                                                    itm1.flags = setBits(itm1.flags, DASH_MASK, DASHED0) // Draw dashes
                                                    itm2.flags =
                                                        setBits(itm2.flags,DASH_MASK,DASHED1) // Draw dashes between the previous dashes
                                                }
                                            }
                                        }
                                }
                            }
                        }
                }
            }

            dial.items[OUTER_RECT]?.let { outer ->

                fun linePattern(flags: Int): String? {
                    val pat = getBits(flags, DASH_MASK)

                    return if (pat == 0)
                        null
                    else {
                        val s = (dial.textSize / 2).roundToInt().toString()
                        if (pat == DASHED0)
                            s
                        else if (pat == DASHED1)
                            "0 $s 0"
                        else
                            null
                    }
                }

                if (rnd !is SCAD) {
                    rnd.appendGeometry(outer.geometry, outer.domain, LT_GRAY, THIN_LINE, id = OUTER_RECT)
                }
                else {
                    dial.getBasePolygon()?.let { base ->
                        rnd.appendBase3D(base, LT_GRAY, id = OUTER_RECT)
                    }

                    rnd.appendGnomon3D(dial.getStylePolygon(), 0.0, 1.5)
                }

                dial.items[INNER_RECT]?.let { inner ->
                    // Render orientation marks along the edges
                    val r = BORDER_WIDTH/8

                    dial.items[WORLD_PLANE_MARKS]?.let { di ->
                        var count = 1
                        centerPoints(di.geometry as Line, di.domain).forEach { pt ->
                            rnd.appendPoint(rnd.preTrans.transform(pt), r, CYAN, "${WORLD_PLANE_MARKS}_${count++}")
                        }
                    }

                    dial.items[HORIZON_PLANE_MARKS]?.let { di ->
                        var count = 1
                        centerPoints(di.geometry as Line, di.domain).forEach { pt ->
                            rnd.appendPoint(rnd.preTrans.transform(pt), r, RED, "${HORIZON_PLANE_MARKS}_${count++}")
                        }
                    }

                    dial.items[STYLE_PLANE_MARKS]?.let { di ->
                        var count = 1
                        centerPoints(di.geometry as Line, di.domain).forEach { pt ->
                            rnd.appendPoint(rnd.preTrans.transform(pt), r, BLUE, "${STYLE_PLANE_MARKS}_${count++}")
                        }
                    }
                }

//                dial.items[CONVEX_HULL]?.let { hull ->
//                    if (rnd is SVG)
//                        rnd.appendPolyline(
//                            rnd.preTrans.transform((hull.geometry as Polyline).pts),
//                            MAGENTA,
//                            0.1,
//                            null,
//                            id = CONVEX_HULL
//                        )
//                    else
//                        rnd.appendGeometry(hull.geometry, hull.domain, MAGENTA, lw, id = CONVEX_HULL)
//                }

                if (debug) {
                    dial.items[CONVEX_HULL]?.let { hull ->
                        if (rnd is SVG)
                            rnd.appendPolyline(
                                rnd.preTrans.transform((hull.geometry as Polyline).pts),
                                MAGENTA,
                                0.1,
                                null,
                                id = CONVEX_HULL
                            )
                        else
                            rnd.appendGeometry(hull.geometry, hull.domain, MAGENTA, lw, id = CONVEX_HULL)
                    }

                    dial.items[HOUR_MIN]?.let { itm ->
                        rnd.appendGeometry(itm.geometry, itm.domain, CYAN, lw, id = HOUR_MIN)
                    }

                    dial.items[HOUR_MAX]?.let { itm ->
                        rnd.appendGeometry(itm.geometry, itm.domain, MAGENTA, lw, id = HOUR_MIN)
                    }

                    dial.items[SUMMER_TROPIC]?.let {
                        rnd.appendGeometry(it.geometry, it.domain, RED, lw, id = SUMMER_TROPIC)
                    }

                    dial.items[WINTER_TROPIC]?.let {
                        rnd.appendGeometry(it.geometry, it.domain, BLUE, lw, id = WINTER_TROPIC)
                    }
                }

                dial.items[ALT_LIM]?.let { horA ->
                    rnd.appendGeometry(horA.geometry,horA.domain,PINK,lw, id=ALT_LIM)
                }

                dial.items[REFRACT_LIM]?.let { horR ->
                    rnd.appendGeometry(horR.geometry,horR.domain,PINK,lw, id=REFRACT_LIM)
                }

                dial.items.filter { it.key.startsWith(ZODIAC_CONIC) }.forEach { (t, u) ->
                    val strong = (u.flags and STRONG) != 0
                    rnd.appendGeometry(u.geometry, u.domain, LT_GRAY, if (strong) 1.67 * lw else lw,linePattern(u.flags), id = t)
                }

                // Render midnight sun limiting conic
                dial.items[MIDNIGHT_SUN_CONIC]?.let { itm ->
                    rnd.appendGeometry(itm.geometry, itm.domain, CYAN, lw,linePattern(itm.flags), id = MIDNIGHT_SUN_CONIC)
                }

                dial.items.filter { it.key.startsWith(HOUR_LN) }.forEach { (t, u) ->
                    val strong = (u.flags and STRONG) != 0
                    rnd.appendGeometry((u.geometry as Line), u.domain, GRAY, if (strong) 1.67 * lw else lw, id = t)
                }

                dial.items.filter { it.key.startsWith(ZODIAC_TEXT) }.forEach { (t, u) ->
                    rnd.appendGeometry(u.geometry as Text, u.domain, LT_GRAY,u.geometry.mt.fontSizeMm, id = t)
                }

                val springColor = if (dial.parts.geolat >= 0.0) GREEN else ORANGE
                val autumnColor = if (dial.parts.geolat >= 0.0) ORANGE else GREEN

                val drawAll = (dial.eclLongStep == null)

                var color = springColor
                dial.items.filter { it.key.startsWith(EOT_CONIC) }.forEach { (t, u) ->
                    if (drawAll || !isCloseToMultipleOf(u.pId,PI_HALF)) {
                        rnd.appendGeometry(u.geometry, u.domain, color, lw,linePattern(u.flags), id = t)
                        color = if (color == springColor) autumnColor else springColor
                    }
                }

                dial.items.filter { it.key.startsWith(HOUR_TEXT) }.forEach { (t, u) ->
                    rnd.appendGeometry((u.geometry as Text), u.domain, BLACK,u.geometry.mt.fontSizeMm, id = t)
                }
                dial.items.filter { it.key.startsWith(SPRING_TEXT) }.forEach { (t, u) ->
                    rnd.appendGeometry(u.geometry as Text, u.domain, springColor, u.geometry.mt.fontSizeMm, id = t)
                }
                dial.items.filter { it.key.startsWith(AUTUMN_TEXT) }.forEach { (t, u) ->
                    rnd.appendGeometry(u.geometry as Text, u.domain, autumnColor, u.geometry.mt.fontSizeMm, id = t)
                }

                dial.items[HORIZON_TRACE]?.let {
                    rnd.appendGeometry(it.geometry, it.domain, RED, lw, id = HORIZON_TRACE)
                }

                dial.items[ANNIV_CONIC]?.let { itm ->
                    rnd.appendGeometry(itm.geometry, itm.domain, YELLOW, lw,linePattern(itm.flags),id = ANNIV_CONIC)
                }

                dial.items[ANNIV_HOUR]?.let { g ->
                    rnd.appendGeometry(g.geometry, g.domain, YELLOW, lw, id = ANNIV_HOUR)
                }

                dial.items[ANNIV_MARK]?.let { g ->
                    rnd.appendGeometry(g.geometry, g.domain, YELLOW, 1.67*lw, id = ANNIV_MARK)
                }

                dial.items[ANCHOR]?.let { g ->
                    rnd.appendGeometry(g.geometry, g.domain,LT_BLUE, lw, id = ANCHOR)
                }

                dial.items[GNOMON_LINE]?.let { g ->
                    rnd.appendGeometry(g.geometry, g.domain, BLUE, THIN_LINE, id = GNOMON_LINE)
                }

                dial.items[GNOMON_BAR]?.let { g ->
                    rnd.appendGeometry(g.geometry, g.domain, BLUE, THIN_LINE, id = GNOMON_BAR)
                }

                dial.items.mapNotNull { (k,v) ->
                    if (k.startsWith(POINTS)) v else null
                }.forEach { itm ->
                    if (rnd is SVG)
                        rnd.appendPoint(rnd.preTrans.transform(itm.geometry.pos),2.0,RED)
                    else
                        rnd.appendGeometry(itm.geometry,itm.geometry.fullDomain,RED,1.0)
                }

                dial.scale?.let { s ->
                    val tr = Transformation.catenate(rnd.preTrans,Transformation.scaling(s))

                    dial.eotGraph?.points?.forEach { (pt,color) ->
                        rnd.appendPoint(tr.transform(pt),rnd.scale*1.0,color)
                    }

                    dial.points.forEach { (pt,color) ->
                        rnd.appendPoint(tr.transform(pt),rnd.scale*1.0,color)
                    }

                    dial.arrows.forEach { (arw,color) ->
                        val a = arw//.scale(s)

                        rnd.appendGeometry(a,a.fullDomain,color,rnd.scale)
                    }

                    dial.polylines.forEach { (pts,color) ->
                        val a = Polyline(pts)//.scale(s)

                        rnd.appendGeometry(a,a.fullDomain,color,rnd.scale)
                    }
                }
            }
        }
    }
}