import kotlinx.datetime.*
import kotlinx.datetime.format.*
import kotlinx.serialization.Serializable
import kotlin.math.roundToLong

// TODO Read the docs for Instant
/**
 * DateTime holds the time in milliseconds since epoch at 1 January 2000 at 12 noon UT1 (Julian date: 2451545.0)
 * @param iUTC a timestamp obtained from Date.UTC()
 */
@Serializable
class DateTimeUTC(val iUTC: Instant) {
    // Time since the epoch instant 1970-01-01T00:00:00Z

    constructor(
        year: Int,
        month: Int,
        day: Int,
        hour: Int=0,
        minute: Int=0,
        second: Int=0,
        nanos: Int = 0
    ): this(dateToInstantUTC(year, month, day, hour, minute, second = second, nanoSecond=nanos))

    constructor(isoDateTime: String) : this(
            // yyyy-mm-ddThh:MM:SS.ssssZ
            Instant.parse(secondsCheck(isoDateTime), DateTimeComponents.Formats.ISO_DATE_TIME_OFFSET)
    )

    constructor(millis: Double) : this(millisToInstant(millis))

    fun addMillis(millis: Double): DateTimeUTC {
        val f = millis % 1.0
        val i = millis - f

        return DateTimeUTC(iUTC.plus(i.toLong(),DateTimeUnit.MILLISECOND).plus((1E6*f).roundToLong(),DateTimeUnit.NANOSECOND))
    }

    fun toLocalString() = localDateTime.toInstant(TimeZone.UTC).let { instantUTC ->
        val ctz = TimeZone.currentSystemDefault()
        val dtLocal = instantUTC.toLocalDateTime(ctz)
        val instantLocal = dtLocal.toInstant(TimeZone.UTC)
        val duration = instantLocal - instantUTC
        val s = (duration.inWholeMilliseconds + 500L)/1000L
        val m = s/60
        val h = m/60
        val utcOffset = UtcOffset(h.toInt()%24,m.toInt()%60,s.toInt()%60)

        instantUTC.format(DateTimeComponents.Formats.ISO_DATE_TIME_OFFSET,utcOffset) // ctz.id is SYSTEM in firefox
    }

    fun systemTimeZoneId() = TimeZone.currentSystemDefault().id

    override fun toString() = iUTC.format(DateTimeComponents.Formats.ISO_DATE_TIME_OFFSET)


    val localDateTime: LocalDateTime by lazy {
        iUTC.toLocalDateTime(TimeZone.UTC)
    }
    val year by lazy { localDateTime.year }
    val month by lazy { localDateTime.monthNumber }
    val day by lazy { localDateTime.dayOfMonth }
    val hour by lazy { localDateTime.hour }
    val minute by lazy { localDateTime.minute }
    val second by lazy { localDateTime.second }
    val nanoSecond by lazy { localDateTime.nanosecond }

    val millisEpoch: Double by lazy {
        1000.0*iUTC.epochSeconds + 1000_000.0*iUTC.nanosecondsOfSecond
    }

    val millis2000: Double by lazy {
        1000.0*(iUTC.epochSeconds - EPOCH2000.epochSeconds) + (iUTC.nanosecondsOfSecond - EPOCH2000.nanosecondsOfSecond)/1000_000.0
    }

    val centuries2000: Double by lazy {
        ((iUTC.epochSeconds - EPOCH2000.epochSeconds) + (iUTC.nanosecondsOfSecond  - EPOCH2000.nanosecondsOfSecond)*1E-9)/(60.0*60.0*24.0*36525.0)
    }

    fun formal() = Companion.formal(this)

    companion object {
        // EPOCH2000  is time since 1 January 2000 at 12 noon UT1 (Julian date: 2451545.0) in milliseconds
        val EPOCH2000 = dateToInstantUTC(2000,1,1,12,0)

        @OptIn(FormatStringsInDatetimeFormats::class)
        val MINUTES_FORMAT = DateTimeComponents.Format {
            byUnicodePattern("yyyy-MM-dd'T'HH:mm'Z'")
//            byUnicodePattern("uuuu-MM-dd'T'HH:mmZ")
        }

        fun millis2000(year: Int, month: Int, day: Int, hour: Int, minute: Int, second: Int = 0, nanoSecond: Int = 0) =
            dateToInstantUTC(year, month, day, hour, minute, second, nanoSecond).let { i ->
                1000.0*(i.epochSeconds - EPOCH2000.epochSeconds) + (i.nanosecondsOfSecond - EPOCH2000.nanosecondsOfSecond)/1000_000.0
            }

        fun now() = Clock.System.now().toLocalDateTime(TimeZone.UTC).toInstant(UtcOffset.ZERO).let { i ->
            DateTimeUTC(i)
        }

        fun stamp() = now().formal()

        fun dateToInstantUTC(year: Int, month: Int, day: Int, hour: Int=0, minute: Int=0, second: Int = 0, nanoSecond: Int = 0) =
            LocalDateTime(LocalDate(year,month,day), LocalTime(hour=hour,minute=minute,second=second, nanosecond=nanoSecond)).
            toInstant(TimeZone.UTC)


        fun secondsCheck(isoDateTime: String) = if ("""\d+:\d+:\d+\.*\d*Z$""".toRegex().containsMatchIn(isoDateTime))
            isoDateTime
        else
            isoDateTime.replace("Z",":00.0Z")  // Add seconds part

        fun millisToInstant(millis: Double): Instant {
            val seconds = millis/1000.0
            val frac = seconds % 1

            return Instant.fromEpochSeconds((seconds - frac).roundToLong(),(1E9*frac).roundToLong()).also { logC(RED,it) }
        }

        fun formal(dateTimeUTC: DateTimeUTC) =
            "${dateTimeUTC.iUTC.toLocalDateTime(TimeZone.currentSystemDefault()).format(
                LocalDateTime.Format {
                    dayOfWeek(DayOfWeekNames.ENGLISH_FULL)
                    chars(", ")
                    monthName(MonthNames.ENGLISH_FULL)
                    char(' ')
                    dayOfMonth()
                    chars(", ")
                    year()
                    chars(" at ")
                    hour(padding = Padding.ZERO)
                    char(':')
                    minute(padding = Padding.ZERO)
                    char(' ')
                }
        )}$timeZoneId"

    }
}
