package sundial.geometry

import PI_HALF
import sundial.math.Vector
import kotlin.math.min
import kotlin.math.pow

class FitRectAround {

    data class ScaleData(val scale: Double, val rect: Rectangle, val rotation: Double)

    companion object {
        fun scaleRotateToFit(convexHull: List<Point>,W: Double,H: Double,txtRot: Double): ScaleData? {
            var areaMax = Double.NEGATIVE_INFINITY
            var rect: Rectangle? = null
            var scale: Double? = null
            var my: Double? = null
            for (i in 1 until convexHull.size) {
                val u = Vector(convexHull[i-1],convexHull[i]).normalize()
                // Determine width and height along u
                minRectAlong(convexHull,u).let { r ->
                    // Determine scale factor to fit inside W x H
                    scaleRotateToFit(r.w,r.h,W,H).let { (s,ny) ->
                        val area = s.pow(2)*r.w*r.h
                        if (area > areaMax) {
                            areaMax = area
                            rect = r
                            scale = s
                            my = u.rotation + ny
                        }
                    }
                }
            }

            return rect?.let { r ->
                ScaleData(
                    scale!!,
                    r.scale(scale!!),
                    txtRot + my!!
                )
            }
        }

        fun measureAlongDirection(convexHull: List<Point>, direction: Vector) =
            minRectAlong(convexHull,direction).let {
                ScaleData(1.0,it,it.theta)
            }

        fun fitAlongDirection(convexHull: List<Point>, W: Double, H: Double, direction: Vector): ScaleData =
            minRectAlong(convexHull,direction).let { r ->
                scaleToFit(r.w,r.h,W,H).let { s ->
                    ScaleData(s,r.scale(s),0.0)
                }
            }

        fun minimumAreaEnclosingRectangle(hull: List<Point>): Rectangle? {
            var areaMax = 0.0
            var rect: Rectangle? = null
            for (i in 1.. hull.size) {
                val u = Vector(hull[i-1],hull[if (i < hull.size) i else 0]).normalize()
                // Determine width and height along u
                minRectAlong(hull,u).let { r ->
                    val area = r.w*r.h
                    if (area > areaMax) {
                        areaMax = area
                        rect = r
                    }
                }
            }

            return rect
        }

