        /**
         * Determines the smallest rectangle with a side along direction <b>u</b> that encloses a list of points.
         * Also measures the height normal to <b>u</b>
         * @param points the point-cloud
         * @param u unit vector direction
         * @return tilted rectangle
         */
        fun minRectAlong(points: List<Point>, u: Vector): Rectangle {
            val n = Vector(-u.delY,u.delX)
            val P0 = points[0]
            // Vector(P0,P0) = (0,0)
            var xMin = 0.0
            var xMax = 0.0
            var yMin = 0.0
            var yMax = 0.0

            points.forEachIndexed { i, pt ->
                if (i > 0) {
                    val w = Vector(P0,pt)

                    val x = Vector.dot(w,u)
                    if (x < xMin)
                        xMin = x
                    if (x > xMax)
                        xMax = x

                    val y = Vector.dot(w,n)
                    if (y < yMin)
                        yMin = y
                    if (y > yMax)
                        yMax = y
                }
            }

            val pos = P0 + u.times((xMax+xMin)/2) + n.times((yMax+yMin)/2)

            return Rectangle(xMax - xMin,yMax - yMin,u.rotation,pos.x,pos.y)
        }

