        /**
         * Scales rectangle <b>w</b> x <b>h</b> to fit inside rectangle <b>W</b> x <b>H</b>
         * without distortion. It may also rotate <b>w</b> x <b>h</b> for a better fit
         * @return the scaling factor and the rotation
         */
        fun scaleRotateToFit(w: Double,h:Double,W:Double,H: Double): Pair<Double,Double> {
            val s1 = scaleToFit(w,h,W,H)
            val s2 = scaleToFit(h,w,W,H)

            return if (s1 <= s2)
                Pair(s1,0.0)
            else
                Pair(s2,-PI_HALF)
        }

        /**
         * Scales rectangle <b>w</b> x <b>h</b> to fit inside rectangle <b>W</b> x <b>H</b>
         * without distortion
         * @return the scaling factor
         */
        fun scaleToFit(w: Double,h:Double,W:Double,H: Double) =
            min(W/w,H/h)

        const val FIT_HORIZONTAL_RECT = 1
        const val FIT_OMPTIMIZE_AREA = 2
        const val FIT_MAX_DIAMETER_RECT = 3
        const val FIT_ALONG_DIRECTION = 4
        const val FIT_MIN_AREA_RECT = 5
        const val MEASURE_ALONG_DIRECTION = 14

        val NAMES = listOf("FIT_HORIZONTAL_RECT","FIT_OMPTIMIZE_AREA","FIT_MAX_DIAMETER_RECT","FIT_ALONG_DIRECTION")

        fun name(code: Int) = if ((code < 1) || (4 < code))
            "UNKNOWN"
        else
            NAMES[code-1]
    }
}