package sundial.numberset

import GREEN
import ORANGE
import RED
import fix
import printLn
import kotlin.math.PI

open class RealSet(interval: List<NumberSet<Double>.Interval>) : NumberSet<Double>(interval) {

    override val MIN_BRACE: Brace = Brace(LEFT, OPEN,Double.NEGATIVE_INFINITY)

    override val MAX_BRACE: Brace = Brace(RIGHT, OPEN,Double.POSITIVE_INFINITY)

    constructor(leftType: Int,leftValue: Double,rightValue: Double,rightType: Int) : this(
        R.Brace(LEFT,leftType,leftValue),
        R.Brace(RIGHT,rightType,rightValue))

    constructor(leftBrace: NumberSet<Double>.Brace, rightBrace: NumberSet<Double>.Brace) : this(listOf(R.Interval(leftBrace,rightBrace)))

    constructor() : this(emptyList())

    open fun union(leftType: Int,leftValue: Double,rightValue: Double,rightType: Int) =
        union(Interval(
            Brace(LEFT,leftType,leftValue),
            Brace(RIGHT,rightType,rightValue)
        )) as RealSet

    override fun compareValues(value1: Double, value2: Double) = if ((value1 == Double.POSITIVE_INFINITY) && (value2 == Double.POSITIVE_INFINITY))
        0
    else if ((value1 == Double.NEGATIVE_INFINITY) && (value2 == Double.NEGATIVE_INFINITY))
        0
    else
        value1.compareTo(value2)

    override fun instance(intervals: List<Interval>) = RealSet(intervals)

    override fun str(n: Double) = fix(n)

    override fun strbrace(brace: Brace) = if (brace.side == LEFT) {
            "${if (brace.type == OPEN) "<" else "["}${str(brace.value)}"
        }
        else {
            "${str(brace.value)}${if (brace.type == OPEN) ">" else "]"}$stridx"
        }

    override val stridx = ""

    override fun scale(f: Double) = l.map { itv ->
        Interval(
            Brace(itv.leftBrace.side, itv.leftBrace.type, f * itv.leftBrace.value),
            Brace(itv.rightBrace.side, itv.rightBrace.type, f * itv.rightBrace.value)
        )
    }.let { l ->
        RealSet(l)
    }

    val length: Double by lazy {
        last() - first()
    }

    // Combined length of all intervals
    val span: Double by lazy {
        var s = 0.0

        l.forEach { itv ->
            s += itv.rightValue() - itv.leftValue()
        }

        s
    }

    fun first() = try {
        itvl().first().leftValue()
    } catch (e: Exception) {
        0.0
//        Double.NaN
    }

    fun last() = try {
        itvl().last().rightValue()
    } catch (e: Exception) {
        0.0
//        Double.NaN
    }

    companion object {
        val R = RealSet().UNIVERSAL_SET as RealSet
        val R_PLUS = RealSet(R.Brace(LEFT, CLOSED,0.0), R.MAX_BRACE)
        val R_MINUS = RealSet(R.MIN_BRACE,R.Brace(RIGHT, CLOSED,0.0))
        val EMPTY_SET = RealSet()
    }
}

