package sundial.geometry

import TINY
import fixD
import fixL
import sundial.math.Transformation
import sundial.math.Vector
import sundial.numberset.CyclicSet
import sundial.numberset.NumberSet
import sundial.numberset.NumberSet.Companion.OPEN
import sundial.numberset.RealSet
import kotlin.math.cos
import kotlin.math.sin

open class Rectangle(val w: Double, val h: Double, override val theta: Double, x0: Double, y0: Double) : Geometry() {

    constructor(rect: Rectangle, domain: CyclicSet?=null) : this(rect.w,rect.h,rect.theta,rect.x0,rect.y0) {
        this.domain = domain
    }

    override val type: Int = 0

    override fun typeName() = "RECTANGLE"

    private val P: Point by lazy {
        Point(x0,y0)
    }

    override fun position() = P

    val S = 2*(w + h)

    val lns: List<Line> by lazy {
        toLines(this)
    }

    override val canonicalForm: Rectangle by lazy {
        translate(-x0,-y0).rotate(-theta)
    }

    fun corners() = listOfNotNull(
        Point(-w/2,-h/2),
        Point(w/2,-h/2),
        Point(w/2,h/2),
        Point(-w/2,h/2)
    ).map { pt ->
        pt.rotate(theta).translate(x0,y0)
    }

//    fun corners() =  listOfNotNull(
//        toPoint(0.0),
//        toPoint(w),
//        toPoint(w + h),
//        toPoint(2 * w + h)
//    )

    override fun toPoint(t: Double): Point? {
        // t is side length
        val u = t % S
        var v = if (u < 0) u + S else u

        val pt = if (v <= w)
            Point(v-w/2,-h/2)
        else {
            v -= w
            if (v <= h)
                Point(w / 2, v-h/2)
            else {
                v -= h
                if (v <= w)
                    Point(w/2-v, h / 2)
                else {
                    v -= w
                    if (v > h)
                        null
                    else
                        Point(-w / 2, h/2-v)
                }
            }
        }

        return pt?.rotate(theta)?.translate(x0,y0)
    }

    override val fullDomain by lazy {
        CyclicSet(S).UNIVERSAL_SET as CyclicSet
    }

    override fun rotate(delTheta: Double): Rectangle {
        val posR = pos.rotate(delTheta)

        return Rectangle(w, h, theta + delTheta,posR.x,posR.y)
    }

    override fun translate(delX: Double, delY: Double) = Rectangle(w,h,theta,x0+delX,y0+delY)

    override fun scale(f: Double) = Rectangle(f*w,f*h,theta,f*pos.x,f*pos.y)

    override fun points(itv: NumberSet<Double>.Interval?, smoothed: Boolean): Sequence<Point> = sequence {

        val itv_ = itv ?: fullDomain.itvl()[0]

        var l = itv_.leftBrace.value
        if (itv_.leftBrace.type == OPEN) l += TINY

        var r = itv_.rightBrace.value
        if (itv_.rightBrace.type == OPEN) r -= TINY

       listOf<Double>(l,-S,w-S,w+h-S,2*w+h-S,0.0,w,w+h,2*w+h,r).sortedBy {
           it
       }.forEach { t ->
           if (itv_.contains(t)) {
//               println("t=${fix(t)}")
               toPoint(t)?.let {
                   yield(it)
               }
           }
       }
    }

    override fun toParameter(pt: Point): Double? {
        val c = arrayOf(0.0,w,w+h,2*w+h)
        lns.forEachIndexed { i, ln ->
                ln.toParameter(pt)?.let { s ->
                    if (ln.fullDomain.contains(s))
                        return c[i] + s
                }
        }

        return null
    }

