    override fun tangent(t: Double): Vector {
        // t is side length
        val u = t % S
        val v = if (u < 0) u + S else u

        return if (v <= w)
            lns[0].u
        else if (v <= w + h)
            lns[1].u
        else if (v <= 2*w + h)
            lns[2].u
        else
            lns[3].u
    }

    override fun normal(t: Double): Vector? = tangent(t).let {
        Vector(it.delY,-it.delX)
    }

    override val axis: Line by lazy {
        Line(pos, Vector(cos(theta), sin(theta)))
    }

        override fun hit(pt: Point): Int {

            lns.forEach { ln ->
                val code = ln.hit(pt)

                if (code == HIT_RIGHT)
                    return HIT_OUTSIDE

                if ((code == HIT_ON) && (toParameter(pt) != null))
                    return HIT_ON
            }

            return HIT_INSIDE
        }

    override fun construction(domain: RealSet?,tr: Transformation?,note: String?): String {
        val t = tr ?: Transformation.IDENTITY
        val c = t.transform(corners())
        val buf = StringBuilder("RECTANGLE around corners ${c[0]}, ${c[1]}, ${c[2]}, ${c[3]}\n")
        buf.append("Center at ${t.transform(pos)}")

        return buf.toString()
    }

    /**
         * Returns a string representation of the object.
         */
        override fun toString(): String {
            val buf = StringBuilder("RECTANGLE\n")
            buf.append("width: ${fixL(w)} height: ${fixL(h)} area: ${fixL(w * h)}\n")
            buf.append("rotated: ${fixD(theta)} ")
            buf.append("center at:$pos\n")
            buf.append("S: ${fixL(S)}\n")
            buf.append("full domain: $fullDomain")
            if (domain != null) buf.append("\ndomain: $domain")

            return buf.toString()
        }



    fun toCorner(t: Double): Point? {
        // t is side length
        val u = t % S
        var v = if (u < 0) u + S else u

        val pt = if (v <= w)
            Point(w/2,-h/2)
        else {
            v -= w
            if (v <= h)
                Point(w / 2, h/2)
            else {
                v -= h
                if (v <= w)
                    Point(w/2, h / 2)
                else {
                    v -= w
                    if (v > h)
                        null
                    else
                        Point(-w / 2, h/2)
                }
            }
        }

        return pt?.rotate(theta)?.translate(x0,y0)
    }

        companion object {

            fun intersects(r1: Rectangle, r2: Rectangle): List<Point> {
                val l = mutableListOf<Point>()

                toLines(r1).forEach { l1 ->
                    toLines(r2).forEach { l2 ->
                        Line.intersect(l1, l2)?.let { pt ->
                            if ((l1.toParameter(pt) != null) && (l2.toParameter(pt) != null))
                                l.add(pt)
                        }
                    }
                }

                return l.toList()
            }

        fun toLines(rec: Rectangle): List<Line> {
            return with(rec) {
                val pt0 = Point.rotate(Point(-w / 2, -h / 2), theta)
                val pt1 = Point.rotate(Point(w / 2, -h / 2), theta)
                val pt2 = Point.rotate(Point(w / 2, h / 2), theta)
                val pt3 = Point.rotate(Point(-w / 2, h / 2), theta)

                listOf(
                    Line(x0 + pt0.x, y0 + pt0.y, x0 + pt1.x, y0 + pt1.y,side=Line.RIGHT_SIDE),
                    Line(x0 + pt1.x, y0 + pt1.y, x0 + pt2.x, y0 + pt2.y,side=Line.RIGHT_SIDE),
                    Line(x0 + pt2.x, y0 + pt2.y, x0 + pt3.x, y0 + pt3.y,side=Line.RIGHT_SIDE),
                    Line(x0 + pt3.x, y0 + pt3.y, x0 + pt0.x, y0 + pt0.y,side=Line.RIGHT_SIDE)
                )
            }
        }
    }
}