package sundial.math

import clamp2PI
import fix
import fixD
import fixL
import isTiny
import isZero
import kotlin.math.*

class Vector3(val x: Double, val y: Double, val z: Double) {
    constructor(): this(0.0,0.0,0.0)

//    constructor(c: Cartesian) : this(c.x,c.y,c.z)

    constructor(V: Vector3) : this(V.x,V.y,V.z)

    constructor(fromV: Vector3, toV: Vector3): this(toV-fromV)

    val R = this.norm()
    // Assuming a horizontal cartesian coordinate system: x-axis pointing east, y-axis pointing
    // true north and z-axis pointing towards the zenith
    val az = clamp2PI(atan2(x, y))  // Azimuth from north and eastwards
    val alt = if (!isTiny(R)) asin(z/R) else 0.0

    fun norm() = sqrt(this dot this)

    operator fun plus(b: Vector3): Vector3 {
        return invoke(x+b.x,y+b.y,z+b.z)
    }

    operator fun minus(b: Vector3): Vector3 {
        return invoke(x-b.x,y-b.y,z-b.z)
    }

    operator fun times(f: Double): Vector3 {
        return invoke(f*x,f*y,f*z)
    }

    private operator fun invoke(d: Double, d1: Double, d2: Double): Vector3 {
        return Vector3(d,d1,d2)
    }

    infix fun dot(V: Vector3): Double {
        return x*V.x + y*V.y + z*V.z
    }

    infix fun cross(V: Vector3): Vector3 {
        return invoke(
            y * V.z - z * V.y,
            z * V.x - x * V.z,
            x * V.y - y * V.x
        )
    }

    fun normalize(): Vector3 {
        val L = norm()

        return if (isTiny(L))
            O
        else
            this*(1/L)
    }

    fun componentNormalTo(v: Vector3) = this - componentAlong(v)

    fun componentAlong(v: Vector3): Vector3 {
        val u = v.normalize()

        return (this dot u) * u
    }

    val isZero = (isZero(x) && isZero(y) && isZero(z))

    infix operator fun Vector3.times(d: Double): Vector3 = Vector3(d*this.x,d*this.y,d*this.z)

    override fun toString(): String {
        return "|[${fix(x)}, ${fix(y)}, ${fix(z)}]|=${fix(norm())}"
    }

    fun toPolarString(): String {
        return "[Az: ${fixD(az)}, alt: ${fixD(alt)}, R: ${fixL(R)}]"
    }

    companion object {
        val O = Vector3()
        val I = Vector3(1.0, 0.0, 0.0)
        val J = Vector3(0.0, 1.0, 0.0)
        val K = Vector3(0.0, 0.0, 1.0)

        fun fromPolar(az: Double, alt: Double, R: Double): Vector3 {
            // Convert spherical horizontal coordinates to cartesian horizontal coordinates

            // x-axis runs west - east
            // y-axis runs south - north
            // z-axis runs nadir - zenith

            // BEWARE!
            // Azimuth is measured from the positive y-axis (north) towards the positive
            // x-axis(east)

            // Projection of R into the x-y plane
            val r = R * cos(alt)

            return Vector3( r * sin(az),r * cos(az), R * sin(alt))
        }

        fun angleBetween(P: Vector3, Q: Vector3): Double {
            return acos(P.normalize() dot Q.normalize())
        }
    }
}

infix operator fun Double.times(V: Vector3): Vector3 = V.times(this)
