package sundial.geometry

import COS30
import sundial.math.Vector
import sundial.math.times

class Arrow(P: Point, V: Vector) : Polyline(makeArrow(P,V)) {
    constructor(P1: Point,P2: Point) : this(P1, Vector(P1,P2))

    override val theta: Double = V.rotation

    companion object {
        const val S = 5.0 // mm. Side of equilateral arrowhead

        fun makeArrow(P: Point,V: Vector): List<Point> {
            val P2 = P + V
            val Up = V.normalize()
            val Un = Vector(-Up.delY,Up.delX)
            val P0 = P2 - (COS30 *S)*Up
            val P1 = P0 + (S/2)*Un
            val P3 = P0 - (S/2)*Un

            return listOf(P,P0,P1,P2,P3,P0)
        }
    }
}