import kotlinx.coroutines.*
import kotlinx.coroutines.channels.Channel
import kotlinx.serialization.ExperimentalSerializationApi
import kotlinx.serialization.decodeFromString
import kotlinx.serialization.encodeToString
import kotlinx.serialization.json.Json
import org.w3c.dom.DedicatedWorkerGlobalScope
import sundial.*
import sundial.astronomy.EquationOfTime
import sundial.geometry.EoTGraph

external val self: DedicatedWorkerGlobalScope

val measuredTextChannel: Channel<MeasuredTextData> =Channel<MeasuredTextData>()

val measureTextImpl = object : MeasureTextImplementation {
    override suspend fun measureText(data: MeasuredTextData): MeasuredTextData {
        return measureTextImpl(data)
    }
}

@OptIn(ExperimentalSerializationApi::class)
suspend fun measureTextImpl(data: MeasuredTextData): MeasuredTextData {
    val msg = "${MeasuredTextData::class.simpleName}${Json.encodeToString(data)}"

    self.postMessage(msg)

    return measuredTextChannel.receive()
}

@OptIn(ExperimentalSerializationApi::class)
fun main() {
    print.printImp =  object : PrintImplementation {
        override fun prt(color: Int,s: String) = self.postMessage("${PrintData::class.simpleName}${Json.encodeToString(PrintData(PRINT,color,s))}")
        override fun prtln(color: Int,s: String) = self.postMessage("${PrintData::class.simpleName}${Json.encodeToString(PrintData(PRINT_LN,color,s))}")
        override fun prtPre(preData: PreData) {
            self.postMessage("${PreData::class.simpleName}${Json.encodeToString(preData)}")
        }
        override fun prtSvg(svgData: SvgData) {
            self.postMessage("${SvgData::class.simpleName}${Json.encodeToString(svgData)}")
        }
        override fun prtLnWrn(wrn: String) = self.postMessage("${PrintData::class.simpleName}${Json.encodeToString(PrintData(PRINT_WRN,0,wrn))}")
        override fun prtLnErr(err: String) = self.postMessage("${PrintData::class.simpleName}${Json.encodeToString(PrintData(PRINT_ERR,0,err))}")
        override fun prtLnTmp(color: Int, s: String) = self.postMessage("${PrintData::class.simpleName}${Json.encodeToString(PrintData(PRINT_TMP,color,s))}")
    }

    measureText.measureTextImpl = measureTextImpl

