    var tempCleared: Boolean

    var storedDialData: DialData? = null
    var storedDial: PlaineSundial? = null

    suspend fun getDial(params: DialData) = if (Params.isSame(params,storedDialData)) {
        logC(YELLOW,"Stored dial")
        storedDial
    }
    else {
        logC(YELLOW,"New dial")

        val parts = DialParts(
            EquationOfTime(params.year, info = true),
            params.latitude, params.longitude,
            params.azimuth, params.altitude,
            minAltitude = params.minAltAbove ?: 10.0,
            timeZoneMeridian = params.timezoneMeridian,
            hourSpan = params.hourSpan,
            info = true
        )

        val d = PlaineSundial(
            parts,
            params.width, params.height,
            info = true,
            showEoT = params.showEoT,
            eotFmt = params.eotFmt,
            eotDivisions = params.eotN,
            minuteStep = params.minutesStep,
            eclLongStep = params.elongStep,
            clipBelowHorizon = params.clipHorizon,
            symbols = params.symbols,
            numerals = params.hourSymbols,
            anniversaryDT = params.anniversary,
            debug = false
        )

        logC(YELLOW,"Computing dial ..")

        d.make(params.alignment)

        storedDialData = params
        storedDial = d

        d
    }

