                        logC(ORANGE,"Worker received: $eotData")
                        lock(true)
                        CoroutineScope(Dispatchers.Main).launch {
                            EquationOfTime(eotData.year,info = true).let { eot ->
                                DialParts(eot,
                                    eotData.latitude,eotData.longitude,
                                    eotData.azimuth,eotData.altitude,eotData.minAltitude).let { parts ->


                                    val springSteps = parts.getStepEoT(true)

                                    val autumnSteps = parts.getStepEoT(false)

                                    printLn(GREEN, "Approximating step-function for spring EoT")
                                    springSteps.forEach { (m, a) ->
                                        a.itvl().forEach { itv ->
                                            printLn(GREEN, "${EoTGraph.fmtSec(m)} over solar longitudes $itv")
                                        }
                                    }

                                    printLn(ORANGE, "Approximating step-function for autumn EoT")
                                    autumnSteps.forEach { (m, a) ->
                                        a.itvl().forEach { itv ->
                                            printLn(ORANGE, "${EoTGraph.fmtSec(m)} over solar longitudes $itv")
                                        }
                                    }

                                    printLn(DK_GREEN, "Equation of Time for years around ${eotData.year}")

                                    val w = eotData.widthAvail
                                    EquationOfTime.svg(w, w, eot, springSteps, autumnSteps).also { svg ->
                                        printSVG(SvgData("", svg.toString()))
                                    }

                                    logC(ORANGE, "$clazz done")
                                    lock(false)
                                }
                            }
                        }
                        cmdWasPerformed = true
                    }
                }

                DialData::class.simpleName -> {
