val QUBIC_C = 1.5*sqrt(3.0)
val QUBIC_F = 2/sqrt(3.0)

//fun fix4(x: Double) = if (isZero(x)) "0" else "%.8f".format(x)

fun solveCubicReal(A: Double, B: Double, C: Double, D: Double): DoubleArray {
    // *^3 + *^2 + * +  = 0
    if (isZero(A))
        return solveQuadraticReal(B,C,D)

    // ^3 + *^2 + * +  = 0
    val a = B/A
    val b = C/A
    val c = D/A

    val p = b - a.pow(2)/3
    val q = (2*a.pow(3))/27 - a*b/3 + c
    val del = q.pow(2)/4 + p.pow(3)/27

    return when {
        del > 0 -> {
            // println(" > 0")
            val d = sqrt(del)
            doubleArrayOf(powThird(-q/2 + d) + powThird(-q/2 - d) - a/3)
        }
        del < 0 -> {
            // println(" < 0")
            val w = sqrt(-p)
            val alpha = asin(QUBIC_C *q/w.pow(3))/3
            doubleArrayOf(
                QUBIC_F *w* sin(alpha) - a/3,
                -QUBIC_F *w*sin(alpha + PI_THIRD) - a/3,
                QUBIC_F *w* cos(alpha + PI_SIXTH) - a/3
            )
        }
        else -> {
            // println(" == 0")
            val f = (q/2).pow(THIRD)
            doubleArrayOf(
                -2*f - a/3,
                f - a/3
            )
        }
    }
}

fun solveQuadraticReal(A: Double, B: Double, C: Double): DoubleArray {
    if (isZero(A))
        return solveLinear(B,C)
//    if (A == 0.0)
//        return solveLinear(B,C)

    val b = B/A
    val c = C/A

    val det_: Double = b*b - 4*c

    val det = if (isZero(det_)) 0.0 else det_

    return if (det < 0)
        DoubleArray(0)
    else {
        val r = sqrt(det)

        doubleArrayOf((-b - r) / 2, (-b + r) / 2)
    }
}

