fun solveLinear(A: Double, B: Double): DoubleArray {
    // a x + b
    if (isZero(A))
        return DoubleArray(0)

    return doubleArrayOf(-B/A)
}

fun powThird(x: Double): Double {
    val u = abs(x).pow(THIRD)

    return if (x >= 0) u else -u
}

class Polynomial3(val A: BigDecimal, val B: BigDecimal, val C: BigDecimal, val D: BigDecimal) {

    constructor(a: Double,b :Double,c: Double,d: Double) :
            this(BigDecimal.fromDouble(a, MC),BigDecimal.fromDouble(b, MC),BigDecimal.fromDouble(c, MC),BigDecimal.fromDouble(d, MC))

    val a by lazy {
        A.doubleValue(false)
    }
    val b by lazy { B.doubleValue(false) }
    val c by lazy { C.doubleValue(false) }
    val d by lazy { D.doubleValue(false) }

    val pd: (Double) -> Double? by lazy {
        { x ->
            a * x.pow(3) + b * x.pow(2) + c * x + d
        }
    }

    val pb: (BigDecimal) -> BigDecimal? by lazy {
        { x ->
            A.multiply(x.pow(3), MC) + B.multiply(x.pow(2), MC) + C.multiply(x, MC) + D
        }
    }

    val roots by lazy { solveCubicReal(a,b,c,d) }

    val derived by lazy { Polynomial2(3*a,2*b,c) }

    companion object {
        const val ITER: Long = 1000

        fun cuberoot(b: BigDecimal): BigDecimal {
            // Specify a math context with 40 digits of precision.
            var x = BigDecimal.ONE

            // Search for the cube root via the Newton-Raphson loop. Output each // successive iteration's value.
            for (i in 0 until ITER) {
                x = x.subtract(
                    x.pow(3)
                        .subtract(b, MC)
                        .divide(B3).multiply(x.pow(2), MC), MC
                )
            }
            return x
        }

        fun improve(p: Polynomial3, r: Double): BigDecimal {
            // X1 = x0 - f(x0)/f'(x0)
            var r0 = BigDecimal.fromDouble(r, MC)
            var r1 = r0 - (p.pb(r0)!!.divide(p.derived.pb(r0), MC))
            var i = 0

            while (((r0-r1).abs() > BIG_ZERO) && (i++ < ITER_MAX)) {
                val r3 = r0
                r0 = r1
                r1 = r3 - (p.pb(r3)!!.divide(p.derived.pb(r3), MC))
            }

            if (i >= ITER_MAX) printLnErr("Polynomial#improve() failed to converge")

            return r1
        }

        fun equation(p: Polynomial3): String {
            val b = StringBuilder()

            if (!isTiny(p.a)) {
                b.append("${fix(p.a)} x ")
            }
            if (!isTiny(p.b)) {
                b.append(if (p.b >= 0) "+" else "-").
                append(" ${fix(abs(p.b))} x ")
            }
            if (!isTiny(p.c)) {
                b.append(if (p.c >= 0) "+" else "-").
                append(" ${fix(abs(p.c))} x ")
            }
            if (!isTiny(p.d)) {
                b.append(if (p.d >= 0) "+" else "-").
                append(" ${fix(abs(p.d))}")
            }
            b.append(" = 0")

            return b.toString()
        }
    }
}

class Polynomial2(val B: BigDecimal, val C: BigDecimal, val D: BigDecimal) {

    constructor(b :Double,c: Double,d: Double) :
            this(BigDecimal.fromDouble(b, MC),BigDecimal.fromDouble(c, MC),BigDecimal.fromDouble(d, MC))

    val b = B.doubleValue(false)
    val c = C.doubleValue(false)
    val d = D.doubleValue(false)

    val pd: (Double) -> Double? by lazy {
        { x ->
            b * x.pow(2) + c * x + d
        }
    }

    val pb: (BigDecimal) -> BigDecimal by lazy {
        { x ->
            B*x.pow(2) + C.multiply(x, MC) + D
        }
    }

    val roots by lazy {
        solveQuadraticReal(b,c,d)
    }

    companion object {

        fun equation(p: Polynomial2): String {
            val b = StringBuilder()

            if (!isTiny(p.b)) {
                b.append(" ${fix(abs(p.b))} x ")
            }
            if (!isTiny(p.c)) {
                b.append(if (p.c >= 0) "+" else "-").
                append(" ${fix(abs(p.c))} x ")
            }
            if (!isTiny(p.d)) {
                b.append(if (p.d >= 0) "+" else "-").
                append(" ${fix(abs(p.d))}")
            }
            b.append(" = 0")

            return b.toString()
        }
    }
}