package sundial.geometry

import MeasuredTextData

class Anchor(mt: MeasuredTextData, tilt: Double, pos: Point, val address: String) : Text(mt,tilt,pos) {
    override fun typeName() = "ANCHOR"
    override fun scale(f: Double) =
        Anchor(
            MeasuredTextData(mt.txt,f*mt.fontSizeMm,f*mt.width,f*mt.height, mt.offset*f),
            theta,
            Point(f*pos.x,f*pos.y),
            address
        )
}