package sundial

import MeasuredTextData
import PI_HALF
import RAD
import clamp2PI
import colorStr
import fix2
import fixL
import indent
import isTiny
import printLnWrn
import sundial.geometry.*
import sundial.numberset.RealSet

class SCAD(W: Double, H: Double, val txtRot: Double, val textSize: Double,val lineWidth0: Double,comment: String?=null) : Renderer2(W,H) {

    val baseThickness = textSize/7
    val textThikness = fixL(textSize/(2*(7-2)))
    val lineThickness = textSize/(3*(7+1))

    val template = TEMPLATE.let { tmp ->
        (comment?.let { c ->
            val b2 = StringBuilder()
            c.split("\n").forEach { ln ->
                b2.append("// $ln\n")
            }

            b2.toString()
        } ?: "").let { c ->
            StringBuilder(tmp.replace("{comment}",c))
        }
    }

    val base = StringBuilder()
    val positive = StringBuilder()
    val style = StringBuilder()


    override fun appendGeometry(g: Geometry, domain: RealSet, color: Int, lineWith: Double,pattern: String?, id: String?) {
        // appendGeometry() is the only append-function that pre-transforms
        if (g is Text) {
            // Reduce fontSizeMm with a factor of 0.8 for better 3D rendering
            Text(
                MeasuredTextData(g.mt.txt,0.8*scale*g.mt.fontSizeMm,scale*g.mt.width,scale*g.mt.height,g.mt.offset*scale),
                g.theta,
                Point(scale*g.pos.x,scale*g.pos.y)
            ).also {
                appendText(it,color,id)
            }
        }
        else
            preTransPaths(g,domain,true).forEach { path -> // preTransform is Identity?
//                checkLineIntegrity(g,path,id)
//                checkPointsAdvancing(id,path)

                if ((g is Line) && (path.size != 2))
                    printLnWrn("Line ${id?:""} has ${path.size} points")

                // Increase lineWidth by a factor of 1.2 for better 3D rendering
                val lw = (lineWith/lineWidth0)*lineThickness

                when (path.size) {
                    0,1 ->  {}
                    2 -> appendLine(path[0],path[1],color,lw,id)

                    else -> appendPolyline(path,color,lw,id)
                }
            }
    }

    override fun appendPoint(pt: Point,radius: Double,color: Int,id: String?) {
        // Points are difficult to 3D print
    }

    fun appendText(txt: Text,color: Int,id: String?) {
        // Should not clutter 3D drawings with symbols, but leave those for the user to add
        positive.append("text3D(\"${txt.mt.txt}\",${fixL(txt.mt.fontSizeMm)},${fix2(clamp2PI(/*txt.theta-*/PI_HALF+txtRot)*RAD)},${fixL(txt.pos.x)},${fixL(txt.pos.y)},h=$textThikness,c=\"${colorStr(color)}\");\n")
    }

    fun appendLine(p1: Point,p2: Point,color: Int,lineWidth: Double,id: String?) {
        id?.let { appendComment(positive,it) }

        positive.append("line3D([${fixL(p1.x)},${fixL(p1.y)}],[${fixL(p2.x)},${fixL(p2.y)}],w=${fixL(lineWidth)},h=$textThikness,c=\"${colorStr(color)}\");\n")
    }

    fun appendLine(ln: Line,domain: RealSet,lineWidth: Double,id: String?) {

        getPoints(ln,domain,smooth = true).forEach { pts ->
            positive.append("line3D(")

            pts.forEachIndexed { i,pt ->
                positive.append("[${fixL(pt.x)},${fixL(pt.y)}]")
                if (i+1 < pts.size)
                    positive.append(',')
            }

            positive.append(",w=${fixL(lineWidth)},h=$textThikness);\n")
        }
    }

    fun appendPolyline(poly: List<Point>,color: Int,lineWidth: Double, id: String?) {
        id?.let { appendComment(positive,it) }

        positive.append("polyline3D([")

        poly.forEachIndexed { i,pt ->
            positive.append("[${fixL(pt.x)},${fixL(pt.y)}]")
            if (i+1 < poly.size)
                positive.append(',')
        }

        positive.append("],wp=${fixL(lineWidth)},h=$textThikness,c=\"${colorStr(color)}\");\n")
    }

    fun appendRect(rect: Rectangle,domain: RealSet,lineWidth: Double,id: String?) {
        id?.let { appendComment(positive,it) }

        // TODO Consider rect.domain
        rect.lns.forEach { ln ->
            appendLine(ln,ln.fullDomain,lineWidth,id=id)
        }
    }

    fun appendComment(b: StringBuilder,comment: String) {
        b.append("// $comment\n")
    }

    fun appendBase3D(polygon: List<Point>, color: Int, id: String?) {
        id?.let { appendComment(base,it) }

        base.append("translate([0,0,${fixL(-baseThickness/2)}]) {")
        base.append("\n\textrude([")

        polygon.forEachIndexed { i,pt ->
            base.append("[${fixL(pt.x)},${fixL(pt.y)}]")
            if (i+1 < polygon.size)
                base.append(',')
        }

        base.append("],h=${fixL(baseThickness)},c=\"${colorStr(color)}\");\n}\n}")
    }

    fun appendGnomon3D(polygon: List<Point>, rotZ: Double, thickness: Double) {
        style.append("color([0,0,1,0.2]);\n")
        style.append(getGnomon3D(polygon,rotZ,thickness))
    }

    fun getGnomon3D(polygon: List<Point>, rotZ: Double,thickness: Double): String {
        val b = StringBuilder()
        b.append("\trotate([0,-90,${fix2(rotZ*RAD)}])\n")
        b.append("\t\tlinear_extrude(height=${fixL(thickness)},center=true)\n")
        b.append("\t\t\tpolygon([")
        for (i in polygon.indices) {
            val x = if (isTiny(polygon[i].x)) -baseThickness else polygon[i].x
            b.append("[${fixL(x)},${fixL(polygon[i].y)}]")
            if (i < polygon.lastIndex)
                b.append(",")
        }
        b.append("]);")

        return b.toString()
    }

//    fun renderGnomonShoe3D(polygon: List<Point>, height: Double,scad: String) {
//        template.append("// SHOE\n")
//        template.append("difference() {\n")
//        template.append("\tlinear_extrude(height=${fixL(height)},center=false)\n")
//        template.append("\t\tpolygon([")
//        for (i in polygon.indices) {
