//            if (i < polygon.lastIndex)
//                template.append(",")
//        }
//        template.append("]);\n")
//        template.append("${indent(scad)}\n};\n")
//    }

    override fun toString() = if (base.isNotEmpty()) {
        template.toString().replace("{insert}",
"""
module Base() {
    ${base.toString().replace("\n","\n\t").substringBeforeLast("\n\t")}
}

module Positive() {
    ${positive.toString().replace("\n","\n\t").substringBeforeLast("\n\t")}
}

module Style() {
    ${style.toString()}
}

Style();

difference() {
    Base();
    Positive();
}
""".trimIndent()
            )
    } else
        template.toString().replace("{insert}", positive.toString())


    companion object {
        const val TEMPLATE: String =
            """{comment}                
font = "DejaVu Sans:style=Bold";

fn = 3;

module text3D(txt,size,theta,x,y,h,c="#FFFF00") {
    color(c) {
        translate([x,y,-h/2]) {
            rotate([0,0,theta]) {
                linear_extrude(height=h) {
                    text(txt, size = size, font = font, halign = "center", valign = "center", ${'$'}fn = fn);
                }
            }
        }
    }
}

module line3D(p1,p2,w=1.5,h,c="#FFFF00") {
    ${'$'}fn = fn;

    color(c) {
        hull() {
            translate([p1.x,p1.y,-h/2]) cylinder(d=w,h=h,center=false);
            translate([p2.x,p2.y,-h/2]) cylinder(d=w,h=h,center=false);
        }
    }
}

module line3Dp(p1,p2,w=1.5,h) {
    ${'$'}fn = fn;

    hull() {
        translate([p1.x,p1.y,-h/2]) cylinder(d=w,h=h,center=false);
        translate([p2.x,p2.y,-h/2]) cylinder(d=w,h=h,center=false);
    }
}

module polyline3D(points,wp=1.5,h,c="#0000FF") {
    color(c) {
        for (i=[0:len(points)-2]) {
            line3Dp(points[i],points[i+1],w=wp,h=h);
        }
    }
}

module extrude(p,h=1.5,c="#FFFFFF") {
    color(c) {
        linear_extrude(height=h,center=true,convexity=10) {
            polygon(p);
        }
    }
}

{insert}

"""

        fun getPoints(g: Geometry,domain: RealSet,smooth: Boolean) = sequence<List<Point>> {
            val itritv = domain.itvl().iterator()
            if (itritv.hasNext()) do {
                itritv.next().let { itv ->
                    val itrpt = g.points(itv, smoothed = smooth).iterator()

                    if (itrpt.hasNext()) {
                        val pts = mutableListOf<Point>()

                        do {
                            pts.add(itrpt.next())
                        } while (itrpt.hasNext())

                        yield(pts.toList())
                    }
                }
            } while (itritv.hasNext())
        }
    }
}