import kotlinx.browser.document
import kotlinx.serialization.Serializable
import org.w3c.dom.HTMLElement

actual fun colorFG(color: Int) = "color:${colorStr(color)};"

actual fun colorBG(color: Int) = "background-color:${colorStr(color)};"

actual fun colorStr(color: Int) = "#${color.toString(16).padStart(6,'0')}"

actual fun logC(color: Int,a: Any) = console.log("%c$a",colorFG(color))

// Compute the sum of p and q on the worker side
@Serializable
data class SumData(val p: Int, val q: Int)

fun sum(p: SumData) = p.p + p.q

// Compute the product of x and y on the client side
@Serializable
data class SeamlessData(val x: Double, val y: Double)

fun product(p: SeamlessData) = p.x*p.y

@Serializable
data class PiData(val digits: Long)

@Serializable
data class PrintData(val type: Int,val color: Int,val msg: String,val name: String?=null)

@Serializable
data class EoTData(val year: Int,val latitude: Double,val longitude: Double,val azimuth: Double,val altitude: Double,val minAltitude: Double, val eotMinutes: Double, val widthAvail: Double, val pxPrMm: Double)

const val DRAW_DIAL = 1
const val DRAW_STYLE = 2

@Serializable
data class StyleData(val color: Int, val dialData: DialData, val scrollTop: Double?=null)

@Serializable
data class ScadData(val color: Int, val dialData: DialData, val scrollTop: Double?=null)

const val BUSY = "busy"
const val DONE = "done"

const val CLR = 1
const val PRINT = 2
const val PRINT_LN = 3
const val PRINT_PRE = 4
const val PRINT_SVG = 5
const val PRINT_TMP = 6
const val PRINT_WRN = 7
const val PRINT_ERR = 8

actual val pixPerMm: Double by lazy {
    var ppm = 96 / 25.4

    if (js("typeof document") != "undefined")
        (document.getElementById("dummy") as HTMLElement?)?.let { dummy ->
            // https://stackoverflow.com/questions/7650413/pixel-to-mm-equation/27111621#27111621
            ppm = (dummy.getBoundingClientRect().width / 100.0).also {
                logC(PINK, "Computed pixels per millimeter = $it")
                dummy.remove()
            }
        }

    ppm
}

actual fun zoodiac(i: Int) = zooSignSansBG(i)

// TimeZone.currentSystemDefault().id is always SYSTEM for js
actual val timeZoneId = js("Intl.DateTimeFormat().resolvedOptions().timeZone") as String