/**
 * Formats a [Double]
 * @param w width of formatted string
 * @param d number of decimals
 * @return a formatted string
 */
fun Double.fix(w:Int,d: Int) = if (this.isFinite()) {
    val v = this.round(d)

    (if (v == 0.0)
        this.fixE(d)
    else
        v.toString()).padStart(w)
}
else
    this.notNumberText()

/**
 * Formats a [Double] as a scientific exponential (exponent is a multiple of 3
 * @param d number of decimals in mantissa
 * @return a formatted string
 */
fun Double.fixE(d: Int) = if (this.isFinite()) {
    val p = floor(log10(abs(this)))
    val q = p - (p % 3)

    "${(10.0.pow(-q)*this).fix(d)}E$q"
}
else
    this.notNumberText()

/**
 * Round a [Double]
 * @param d number of decimals
 * @return a formatted string
 */
fun Double.fix(d: Int) = if (this.isFinite())
    this.round(d).toString()
else
    this.notNumberText()

/**
 * Round a [Double]
 * @param d number of decimals
 * @return a formatted string
 */
fun Double.round(d: Int) = if (this.isFinite()) {
    val p = 10.0.pow(d)

    round(this*p)/p
}
else
    this

/**
 * Obtains an appropriate text if this [Double] is not a number (isNaN is true)
 */
fun Double.notNumberText() = if (this.isNaN())
    "NaN"
else if (this == Double.POSITIVE_INFINITY)
    "+$INFINITY"
else if (this == Double.NEGATIVE_INFINITY)
    "-$INFINITY"
else
    "?"

/**
 * Splits a string like <q>2.54mm</q> into measure <q>2.54</q> and unit <q>mm</q>
 * @param measure a string with a measure
 * @return a [Pair] with a measure and a unit
 */
fun splitMeasure(measure: String): Pair<Double,String>? {
    // Split a measurement  as 1.2cm into a Double: 1.2 and a unit: cm
    // https://regex101.com/library/wU0wX6?amp;search=card&orderBy=MOST_RECENT&page=153&search=&filterFlavors=javascript
    // Submitted by Etienne Goumet
    return Regex("""([-+]?[ 0-9]*[0-9]+(?:\.|,)?[0-9]*)[ ]*(.*)""").find(measure)?.let { match ->
        val (amount,unit) = match.destructured
        Pair(amount.toDouble(),unit.trim())
    }
}

/**
 * A value small enough to be considered zero
 */
const val ZERO = 1E-14

/**
 * A limit to looping iterations
 */
const val ITER_MAX = 100

/**
 * A tiny number. The length unit is millimeters
 */
const val TINY = 1E-5

/**
 * A huge number
 */
const val HUGE = 1/ TINY

/**
 * True if the absolute of x <= [ZERO]
 */
fun isZero(x: Double) = (abs(x) <= ZERO)

/**
 * True if the absolute of x > [ZERO]
 */
fun isNotZero(x: Double) = (abs(x) > ZERO)
fun isPositive(x: Double) = (x >= ZERO)

/**
 * True if the absolute of the modulus of z <= [ZERO]
 */
fun isZero(z: Complex) = isZero(z.mod)

/**
 * True if the double value of x is 0.0
 */
fun isZero(x: BigDecimal) = (x.doubleValue(false) == 0.0)
/**
 * A number is tiny if its absolute value is equal to, or less than [TINY]
 */
fun isTiny(x: Double) = (abs(x.toDouble()) <= TINY)

/**
 * A number that is not tiny
 */
fun isNotTiny(x: Double) = (abs(x) > TINY)

/**
 * A complex number is tiny if its modulus is tiny
 */
fun isTiny(z: Complex) = isTiny(z.mod)

/**
 * A number is huge if its absolute value is equal to, or greater than HUGE
 */
fun isHuge(x: Double) = (abs(x) >= HUGE)

/**
 * Two numbers x and y are close if the difference x - y is tiny
 */
fun areClose(x: Double,y: Double) = isTiny(x-y)

/**
 * True if x is close to an integer multiple of y
 */
fun isCloseToMultipleOf(x: Double, y: Double): Boolean {
    val c = x%y

    return isTiny(c) || areClose(c,y)
}

/**
 * c is real if the imaginary part of c [isTiny]
 */
fun isReal(c: Complex) = isTiny(c.im)

