fun mkSet(s: RealSet, leftType: Int, leftValue: Double, rightValue: Double, rightType: Int): RealSet {
    return when (s) {
        is AngleSet -> AngleSet(leftType, leftValue, rightValue, rightType)
        is CyclicSet -> CyclicSet(s, leftType, leftValue, rightValue, rightType)
        else -> RealSet(leftType, leftValue, rightValue, rightType)
    }
}

fun mkEmptySet(s: RealSet): RealSet {
    return when (s) {
        is AngleSet -> AngleSet()
        is CyclicSet -> CyclicSet(s)
        else -> RealSet()
    }
}

/**
 * Brings an angle into the range [-,>
 * @param x an angle in radians
 * @return x unwounded
 */
fun clampPI(x: Double): Double {
    val y = x % TWO_PI

    return if (y < -PI) y + TWO_PI
    else if (y >= PI) y - TWO_PI
    else y
}

/**
 * Brings an angle into the range [0,2>
 * @param x an angle in radians
 * @return x unwounded
 */
fun clamp2PI(x: Double): Double {
    val y = x % TWO_PI

    return if (y < 0) y + TWO_PI else y
}

/**
 * Brings an hour into the range [0,24>
 * @param x a decimal hour
 * @return x unwounded
 */
fun clamp24(x: Double): Double {
    val y = x % 24.0

    return if (y < 0) y + 24.0 else y
}

/**
 * Brings an angle into the range [-12,12>
 * @param x an angle in degrees
 * @return x unwounded
 */
fun clamp180(x: Double): Double {
    val y = x % 360.0

    return if (y < -180.0) y + 360.0
    else if (y >= 180.0) y - 360.0
    else y
}

/**
 * Brings an angle into the range [0,360>
 * @param x an angle in degrees
 * @return x unwounded
 */
fun clamp360(x: Double): Double {
    val y = x % 360.0

    return if (y < 0) y + 360.0 else y
}

// Secant of an angle
// https://www.wolframalpha.com/input/?i=secant
fun sec(x: Double): Double {
    val cX = cos(x)

    return if (cX == 0.0) Double.POSITIVE_INFINITY else 1/cX
}

/**
 * Formats a Double in the best way possible
 */
fun fix(x: Double): String {
    return if (x.isNaN())
        "NaN"
    else when {
        x.isInfinite() -> "${if (x > 0) "+" else "-"}$INFINITY"
        isInteger(x) -> x.fix(0)
        abs(x) < 0.01 -> x.fixE(2)
        else -> x.fix(2)
    }
}

/**
 * Formats a Double to 2 decimals
 */
fun fix2(v: Double) = v.fix(2)
/**
 * Formats a Double to 6 decimals
 */
fun fix6(x: Double) = x.fix(6)

/**
 * Defines the formatting of a millimeter length
 */
fun fixL(x: Double): String {
    return if (x.isNaN())
        "NaN"
    else when {
        x.isInfinite() -> "${if (x > 0) "+" else "-"}$INFINITY"
        isInteger(x) -> x.fix(0)
        else -> x.fix(1)
    }
}

/**
 * Formats a Double to decimals and with
 * @param x a Double
 * @param d number of decimals
 * @param w total number of characters after formatting
 */
fun fixw(x: Double,d: Int,w: Int): String {
    val sgn = if (x < 0.0) "-" else ""
    return when {
        isInteger(x) -> "$sgn${abs(x).fix(d)}".padStart(w)
        isTiny(x) ->  "$sgn${abs(x).fixE(d)}".padStart(w)
        else -> "$sgn${abs(x).fix(d)}".padStart(w)
    }
}

/**
 * Defines the formatting of an angle
 * @param x angle in radians
 */
fun fixD(x: Double): String {
    val s = if (!x.isFinite())
        "NaN"
    else when {
        isInteger(x * RAD) -> (x* RAD).fix(0)
        else -> (x* RAD).fix(2)
    }

    return if (s.contains('.'))
        s.replace('.','')
    else
        s.plus('')
}

/**
 * Calls [Double.fixE(2)]
 */
fun fixE(x: Double) = x.fixE(2)

/**
 * Clamps an angle to the range [-180,180> and displays it with 6 decimals
 * @param v an angle in radians
 */
fun fix180(v: Double) = (clampPI(v) * RAD).fix(6)
/**
 * Clamps an angle to the range [0,360> and displays it with 6 decimals
 * @param v an angle in radians
 */
fun fix360(v: Double) = (clamp2PI(v) * RAD).fix(6)

/**
 * Formats a geographic latitude to have N or S as a prefix and 6 decimals
 */
fun fixLat(lat: Double): String {
    val l = clampPI(lat) * RAD

    return "${if (l >=0) 'N' else 'S'}${abs(l).fix(6)}"
}

/**
 * Formats a geographic longitude to have E or W as a prefix and 6 decimals
 */
fun fixLng(lng: Double): String {
    val l = clampPI(lng) * RAD

    return "${if (l >=0) 'E' else 'W'}${abs(l).fix(6)}"
}

fun HMS24(a: Double) = HMS(clamp2PI(a))

fun HMS(hour_: Double): String {
    return doubleCheck(hour_) ?: run {
        var hour = hour_ % 24.0
        if (hour < 0.0) hour += 24.0

        var s = (hour * 60.0 * 60.0).roundToInt()

        val S = s % 60
        s /= 60
        val M = s % 60
        val H = s / 60

        "${H.toString().padStart(2, '0')}:${M.toString().padStart(2, '0')}:${S.toString().padStart(2, '0')}"
    }
}

fun HM(hour_: Double): String {
    return doubleCheck(hour_) ?: run {
        var hour = hour_ % 24.0
        if (hour < 0.0) hour += 24.0

        val m = (hour * 60.0).roundToInt()

        val M = m % 60
        val H = m / 60

        "${H.toString().padStart(2, '0')}:${M.toString().padStart(2, '0')}"
    }
}


