fun doubleCheck(x: Double?) = if (x == null) "null"
else if (x.isNaN()) "NaN"
else if (x == Double.POSITIVE_INFINITY) POS_INFINITY
else if (x == Double.NEGATIVE_INFINITY) NEG_INFINITY
else null

fun seconds(hms: String): Int {
    // Assuming that the hh:mm:ss stamp is last in the hms-string
    var sec = 0

    hms.split(':').forEachIndexed { index, s ->
        when (index) {
            0 -> sec += 3600*s.takeLast(2).toInt()  // hours
            1 -> sec += 60*s.toInt()    // minutes
            2 -> sec += s.toInt()    // sundial.seconds
            else -> printLnWrn("Malformed hour-stamp: $hms")
        }
    }

    return sec
}

