fun nextId(id: String): String {
    val s: String
    val n: Int
    val idx = id.lastIndexOf('_')
    if (idx > -1) {
        s = id.substring(0, idx)
        n = id.substring(idx + 1).toInt()
    }
    else {
        s = id
        n = 0
    }

    return "${s}_${n+1}"
}

/**
 * Converts a number to a string of roman numerals
 * @param n number to convert
 * @param tooFew if true the conversion will append NIMIS PAUCI to the numerals if n < 0
 * @return A string representation of a number as a roman numeral
 */
fun roman(n: Int, tooFew: Boolean=true) = if (n == 0)
    "N"
else {
    fun decade(n:Int, I: String, V: String, X: String) = when (n) {
        1 -> I
        2 -> I+I
        3 -> I+I+I
        4 -> I+V
        5 -> V
        6 -> V+I
        7 -> V+I+I
        8 -> V+I+I+I
        9 -> I+X
        0 -> X
        else -> ""
    }

    val b = StringBuilder()
    var m = abs(n)%100000

    if (m > 10000)
        b.append("X\u0305".repeat(m/10000))

    m %= 10000
    if (m > 999)
        b.append(decade(m / 1000,"M", "V\u0305","X\u0305"))

    m %= 1000
    if (m > 99)
        b.append(decade(m / 100, "C","D","M"))

    m %= 100
    if (m > 9)
        b.append(decade(m / 10, "X","L","C"))

    m %= 10
    if (m > 0)
        b.append(decade(m, "I","V","X"))

    if (tooFew && (n < 0))
        b.append(" NIMIS PAUCI")

    b.toString()
}

fun longestLine(lns: List<Line>) = longestLine(*lns.toTypedArray())

fun longestLine(vararg lns: Line?): Line? {
    var lln: Line? = null
    var len: Double = 0.0

    lns.forEach { ln ->
        if (ln != null) {
            val l = Line.domainLength(ln)
            if (l > len) {
                lln = ln
                len = l
            }
        }
    }

    return lln
}

/**
 * Generates 24389 unique three letter combinations
 */
object IDS {
    val chars : List<Char> = ('A'..'Z') + '' + '' + ''
    var i = 0
    var j = 0
    var k = 0

    /**
     * Call next() to get the next 3 letter combination
     */
    fun next() = "${chars[i]}${chars[j]}${chars[k++]}".also {
        if (k >= chars.size) {
            k = 0
            j++
            if (j >= chars.size) {
                j = 0
                i++
                if (i >= chars.size)
                    i = 0
            }
        }
    }
}

//
/**
 * Generates a list of center-points of all intervals of the line
 * @param ln a [Line]
 * @param domain the domain of the line
 * @return Centerpoints of all intervals of the line
 */
fun centerPoints(ln: Line, domain: RealSet): List<Point> {
    val pts = mutableListOf<Point>()

    domain.itvl().forEach { itv ->
        ln.toPoint((itv.leftValue() + itv.rightValue())/2)?.let {
            pts.add(it)
        }
    }

    return pts.toList()
}

fun measure(str: String): Pair<Double,String>? {
    // Split a measurement  as 1.2cm into a Double: 1.2 and a unit: cm
    // https://regex101.com/library/wU0wX6?amp;search=card&orderBy=MOST_RECENT&page=153&search=&filterFlavors=javascript
    // Submitted by Etienne Goumet
    return Regex("""([-+]?[ 0-9]*[0-9]+(?:\.|,)?[0-9]*)[ ]*(.*)""").find(str)?.let { match ->
        val (amount,unit) = match.destructured
        Pair(amount.toDouble(),unit.trim())
    }
}

const val SUBNUMERALS = ""

// Replaces digits in a String to digit subscripts
/**
 * Replaces all digits in a string with a corresponding sub numeral
 */
fun subnum(s: String): String {
    val b = StringBuilder()

    s.toCharArray().forEach {
        if (it.isDigit())
            b.append(SUBNUMERALS[it - '0'])
        else
            b.append(it)
    }

    return b.toString()
}

