interface PrintImplementation {
    fun prt(color: Int,s: String)
    fun prtln(color: Int,s: String)
    fun prtPre(preData: PreData)
    fun prtSvg(svgData: SvgData)
    fun prtLnWrn(wrn: String)
    fun prtLnErr(err: String)
    fun prtLnTmp(color: Int, s: String)
}

open class Print() {
    var printImp: PrintImplementation? = null
    fun p(color: Int,s: String) = printImp?.prt(color,s)
    fun Ln(color: Int,s: String) = printImp?.prtln(color,s)
    fun pre(preData: PreData) = printImp?.prtPre(preData)
    fun svg(svgData: SvgData) = printImp?.prtSvg(svgData)
    fun wrnLn(wrn: String) = printImp?.prtLnWrn(wrn)
    fun errLn(err: String) = printImp?.prtLnErr(err)
    fun tmpLn(color: Int, s: String) = printImp?.prtLnTmp(color,s)
}

