@Serializable
data class PreData(val color: Int,val data: String,val name: String?=null,val namePart: String?=null,val ext: String?=null,val scrollTop: Double?=null)

@Serializable
data class SvgData(val url: String,val svg: String="",val name:String?=null,val namePart: String?=null)

@Serializable
data class MeasuredTextData(val txt: String, val fontSizeMm: Double, val width: Double=0.0, val height: Double=0.0, val offset: Vector = Vector.O)

interface MeasureTextImplementation {
    suspend fun measureText(data: MeasuredTextData): MeasuredTextData
}

open class MeasureText {
    var measureTextImpl: MeasureTextImplementation? = null
    suspend fun measureText(txt: String,fontSizeMm: Double): MeasuredTextData? {
//        logC(CYAN,"#2")
        return measureTextImpl?.measureText(MeasuredTextData(txt, fontSizeMm))
    }
}

val measureText = object : MeasureText() {
}

suspend fun measuredText(txt: String, fontSizeMm: Double): MeasuredTextData {
//    logC(CYAN,"#1")
    return measureText.measureText(txt, fontSizeMm)!!
}

