import sundial.numberset.RealSet

/**
 * Makes a rendering of Geometrys
 * @constructor
 * @param rect A rectangle enclosing the area to be rendered x0 = 0 and y0 = 0 places the origin in the center,
 * x0 = w/2 and y0 = h/2 places the origin in the lower left corner
 * @param preTrans a transformation to be applied to the Geometrys before rendering
 */
abstract class Renderer2(val W: Double,val H: Double,val preTrans: Transformation=Transformation.IDENTITY) {
    // rect defines the drawing area width = rect.w and height = rect.h
    // The x-axis runs from left to right and its origin is positioned at rect.x0
    // The y-axis runs from bottom to top and its origin is positioned at rect.y0
    // adaptTrans adapt coordinates to SVG/Graphics2D rendering
    val adaptTrans = run {
//        val rot = Transformation.rotation(-rect.theta,rect.pos)
//        val shift = Transformation.translatinon(Vector(rect.x0+rect.w/2,rect.y0+rect.h/2))
//        val flip = Transformation.reflectAboutHorizontal(rect.h/2)

//        Transformation.catenate(flip,shift,rot)

        Transformation.reflectAboutHorizontal(H/2)
    }

    val scale = run {
        // Distilling any scaling out of preTrans to be able to scale Text
        val O = Point.ORIGO
        val A = Point(1.0,0.0)
        val B = Point(0.0,1.0)
        val Ot = preTrans.transform(O)
        val At = preTrans.transform(A)
        val Bt = preTrans.transform(B)
        val sx = Point.distance(At,Ot)/Point.distance(A,O)
        val sy = Point.distance(Bt,Ot)/Point.distance(B,O)
        // sx and sy should be the same
        if (!isTiny(sx - sy))
            printLnWrn("Scaling in pre-transform is direction dependent")

        sx
    }

    abstract fun appendGeometry(g: Geometry, domain: RealSet, color: Int, lineWith: Double,pattern: String?=null, id: String?=null)
    abstract fun appendPoint(pt: Point,radius: Double,color: Int,id: String?=null)

    fun preTransPaths(g: Geometry, domain: RealSet, smooth: Boolean) = sequence<List<Point>> {
        val itritv = domain.itvl().iterator()
        if (itritv.hasNext()) do {
            itritv.next().let { itv ->
                val path = preTrans.transform(g.points(itv, smoothed = smooth).toList())

                if (path.isNotEmpty()) yield(path)
            }
        } while (itritv.hasNext())
    }
}
