class ConicSection(val A: Double,val B: Double,val C: Double,val D: Double,val E: Double,val F: Double,val branch: Int=1):
     Geometry() {

    constructor(cs: ConicSection, domain: AngleSet?=null) : this(cs,cs.branch) {
        this.domain = domain
    }

    // https://en.wikipedia.org/wiki/Conic_section
    // https://en.wikipedia.org/wiki/Matrix_representation_of_conic_sections
    // https://en.wikipedia.org/wiki/Ellipse
    // https://en.wikipedia.org/wiki/Hyperbola
    // https://en.wikipedia.org/wiki/Parabola
    // https://en.wikipedia.org/wiki/Cone
    // https://en.wikipedia.org/wiki/Degenerate_conic
    // https://www.andrews.edu/~rwright/Precalculus-RLW/Text/07-05.html
    // http://users.math.uoc.gr/~pamfilos/eGallery/problems/ConicsDegenerate.html
    // http://users.math.uoc.gr/~pamfilos/eGallery/problems/Conic_Equation.html

    constructor(M: Matrix3, branch: Int=0) : this(
        M.e(0,0).re,2*M.e(0,1).re,M.e(1,1).re,2*M.e(0,2).re,2*M.e(2,1).re,M.e(2,2).re,branch
    )

    constructor(cs: ConicSection, branch: Int): this(cs.A,cs.B,cs.C,cs.D,cs.E,cs.F, branch=branch)

    data class Axes(val a: Double, val b: Double)

    data class Polar(
        val e: Double, // Eccentricity
        val l: Double, // Semi-latus rectum
        val c: Double // Linear eccentricity
    )

    // Determinant of the characteristic matrix
    val det: Double by lazy {
        val AB = BigDecimal.fromDouble(A, MC)
        val BB = BigDecimal.fromDouble(B, MC)
        val CB = BigDecimal.fromDouble(C, MC)
        val DBdiv2 = BigDecimal.fromDouble(D/2, MC)
        val EBdiv2 = BigDecimal.fromDouble(E/2, MC)
        val FB = BigDecimal.fromDouble(F, MC)

        // ((4AC - B^2)F - AE^2 + BDE - CD^2)/4
        ((AB*CB - BigDecimal.fromDouble(B/2, MC).pow(2))*FB - AB*EBdiv2.pow(2) - CB*DBdiv2.pow(2) + BB*DBdiv2*EBdiv2).round()
    }

//    // Determinant of the characteristic matrix
//    val det: Double by lazy {
//        val AB = BigDecimal.fromDouble(A,MC)
//        val BB = BigDecimal.fromDouble(B,MC)
//        val CB = BigDecimal.fromDouble(C,MC)
//        val DB = BigDecimal.fromDouble(D,MC)
//        val EB = BigDecimal.fromDouble(E,MC)
//        val FB = BigDecimal.fromDouble(F,MC)
//
//        // ((4AC - B^2)F - AE^2 + BDE - CD^2)/4
//        (((B4*AB*CB - BB.pow(2))*FB - AB*EB.pow(2) + BB*DB*EB - CB*DB.pow(2)).divide(B4,MC)).round()
////        (((B4*AB*CB - BB.pow(2))*FB - AB*EB.pow(2) + BB*DB*EB - CB*DB.pow(2))/B4).round()
//    }

    // Sub determinant
    val det33: Double by lazy {
        // A*C - B^2/4
        (BigDecimal.fromDouble(A, MC)*BigDecimal.fromDouble(C, MC) - BigDecimal.fromDouble(B/2, MC).pow(2)).round()
//        (BigDecimal.fromDouble(A,MC)*BigDecimal.fromDouble(C,MC) - BigDecimal.fromDouble(B/4,MC).pow(2).divide(B4,MC)).round()
//        (BigDecimal.fromDouble(A,MC)*BigDecimal.fromDouble(C,MC) - BigDecimal.fromDouble(B,MC).pow(2) / B4).round()
    }

    // Type of conic section
    override val type: Int by lazy {
        if (abs(det) > ZERO) { // det != 0
            when {
                abs(det33) <= ZERO -> PARABOLA // det33 == 0
                det33 < 0 -> HYPERBOLA// det33 < 0
                (A + C)*det < 0 -> ELLIPSE or (if (isZero(A - C) && isZero(B)) CIRCLE else 0)
//                (A + C)*det < 0 -> ELLIPSE or (if (isTiny(A - C) && isTiny(B)) CIRCLE else 0) // Caused erroneous clipping
                else -> IMAGINARY
            }
        }
        else { // det = 0, Degenerate cases
            when {
                isTiny(A) && isTiny(B) && isTiny(C) && !(isTiny(D) && isTiny(E)) -> DEGENERATE or NOT_A_CONIC
                abs(det33) <= ZERO -> { // Two parallel straight lines
                    val q = D.pow(2) + E.pow(2) - (A + C)*F
                    DEGENERATE or (when {
                        q > -ZERO /2 -> PARALLEL_LINES
                        isZero(q) -> SINGLE_LINE
                        else -> 0 // Imaginary lines
                    })
                }
                det33 < 0 -> DEGENERATE or INTERSECTING_LINES
                else -> DEGENERATE or A_POINT
            }
        }
    }

    override fun typeName() = "${types(type)} ${subTypes(type)}"

    val isDegenerate: Boolean by lazy {
        isDegenerate(type)
    }

    val isEllipse: Boolean by lazy {
        isEllipse(type)
    }

    val isParabola: Boolean by lazy {
        isParabola(type)
    }

    val isHyperbola: Boolean by lazy {
        isHyperbola(type)
    }

    val isCircle: Boolean by lazy {
        isCircle(type)
    }

    val isCentral = isHyperbola || isEllipse

    override fun position() = position(this)

    // TODO Should store cos(theta) and sin(theta) instead
    override val theta: Double by lazy {
        rotation(this)
    }

    // Create a conic that is this conic's canonical form
    override val canonicalForm: ConicSection by lazy {
        rotate(translate(this, -this.x0, -this.y0), -this.theta)
    }

    // a, b
    val axes: Axes by lazy {
        semiAxes(this)
    }


    // e, l, c
    val polar: Polar by lazy {
        polarParameters(this)
    }

    val focalLoci: List<Point> by lazy {
        if (isParabola) {
            val pt = Point.translate(Point.rotate(Point(-polar.l / 2, 0.0), theta), x0, y0)
            listOf(pt,pt)
        } else  {
            val c = polar.c

            listOf(
                Point.translate(Point.rotate(Point(-c, 0.0), theta), x0, y0),
                Point.translate(Point.rotate(Point(c, 0.0), theta), x0, y0)
            )
        }
    }

    fun rho(phi: Double): Double {
        return polar.l/(1.0 + polar.e*cos(phi))
    }

    override fun rotate(delTheta: Double) = rotate(this,delTheta)

    override fun translate(delX: Double, delY: Double) = translate(this,delX,delY)

//    override fun scale(f: Double): Geometry {
//        val f2 = f.pow(2)
//
//        return ConicSection(A/f2,B/f2,C/f2,D/f,E/f,F,branch=branch)
//    }

    override fun scale(f: Double): Geometry {
        val p = polarParameters(this)
        val a = semiAxes(this)

        return if (isParabola) createParabola(f*p.l/2,theta,f*pos.x,f*pos.y)
        else if (isEllipse) createEllipse(f*a.a,f*a.b,theta,f*pos.x,f*pos.y)
        else /*if (isHyperbola)*/ createHyperbola(f*a.a,f*a.b,theta,f*pos.x,f*pos.y, branch = branch)
    }

    fun point(phi: Double): Point {

        val f = focalLoci[if (isHyperbola) 0 else 1]

        val r = rho(phi)

        return Point(f.x + r*cos(theta+phi),f.y + r*sin(theta+phi))
    }

    fun scaleCoefficients(f: Double = 10.0) = scaleCoefficients(this,f)

    override fun toPoint(t: Double): Point? {

        return if (!fullDomain.contains(t))
            null
        else
            point(t)
    }

    override fun toParameter(pt: Point): Double? {
        return if (pt.x.isNaN() || pt.y.isNaN())
            null
        else if (hit(pt) == HIT_ON)
            focusAngle(pt)
        else
            null
    }

//    override fun toParameter(pt: Point): Double? {
//        return if (hit(pt) == HIT_ON) focusAngle(pt) else null
//    }

    override fun tangent(t: Double): Vector {
        // https://www.emathzone.com/tutorials/geometry/equation-of-tangent-to-conic.html

        return Vector(-sin(t),cos(t) + polar.e).rotate(theta).normalize()
    }

    // The outside of a parabola or hyperbola is the side where there are no focal points
    override fun normal(t: Double): Vector? {
        return super.normal(t)?.times(if (isHyperbola && (branch==POSITIVE_BRANCH)) 1.0 else -1.0)
    }

    override val axis: Line by lazy {
        Line(pos, Vector(cos(theta),sin(theta)))
    }

    override fun points(itv: NumberSet<Double>.Interval?, smoothed: Boolean) = sequence<Point> {
        // Get the points of the CS for one Interval span of values
        if (itv != null) {
            if (isDegenerate) {
                factorDegenerate(this@ConicSection).forEach { ln ->
                    ln.toPoint(itv.leftBrace.value)?.let {
                        yield(it)
                    }
                    ln.toPoint(itv.rightBrace.value)?.let {
                        yield(it)
                    }
                }
            } else  {
                var phi0 = itv.leftBrace.value
                var phi1 = itv.rightBrace.value

                if (isNotTiny(phi1 - phi0)){
                    // Radius of Curvature has the minimum value for phi = 0, i.e. at the vertex
                    // At the vertex, the curve should advance approx. 1mm per step
                    val S = 1.0*abs(curvature(0.0))
                    val addPI = isHyperbola && (branch == POSITIVE_BRANCH)

                    if (phi0 < -PI) phi0 += TWO_PI
                    if (phi1 < phi0) phi1 += TWO_PI
                    if (phi1 < phi0) phi1 += TWO_PI

                    // In case of singularities at the ends of the interval
                    if (itv.leftBrace.type == OPEN) phi0 += TINY
                    if (itv.rightBrace.type == OPEN) phi1 -= TINY
                    
                    val c = if (isHyperbola) -polar.c else if (isEllipse) polar.c else -polar.l/2
                    val F = Point(c,0.0)

                    var phi = phi0
                    var loop = (phi < phi1)
                    while(loop) {
                        val cPhi = cos(phi)
                        val sPhi = sin(phi)
                        val r = polar.l/(1 + polar.e*cPhi)
                        val P = if (r.isNaN() || r.isInfinite()) {
                            printLnErr("rho(${fixD(phi)}) = NaN for ${types(type)} with label \"${label}\"")
                            loop = false
                            null
                        }
                        else
                            Point(c + r*cPhi, r * sPhi)

                        P?.let {
                            yield(it.rotate(theta).translate(x0,y0))
                        }

                        if (smoothed) {
                            // Advance phi by an amount proportional to RoC
                            if (phi == phi1) loop = false
                            else if (P != null) {
                                tngnt(phi).let { T -> // Tangent to curve at P
                                    val roc = abs(1/curvature(P.x,P.y))
                                    // Advancement of curve adapted to current RoC
                                    val dt = S*roc
                                    // Limit advancement
                                    val ds = if (dt <= 10.0) dt else 10.0
                                    // Advance from P ds millimeters along T
                                    val P1 = P + T.times(ds)
                                    // The direction from focal point to P1
                                    val V1 = Vector(F,P1).normalize()
                                    // Next phi angle
                                    var chi = atan2(V1.delY,V1.delX)
                                    if (addPI) chi += PI
                                    if (chi < phi) chi += TWO_PI
                                    if (abs(chi - phi) < 0.01* DEG) loop = false
                                    else {
                                        phi = chi  // Next phi
                                        if (phi > phi1) phi = phi1 // Will yield last point and quit loop
                                    }
                                }
                            }
                        }
                        else {
                            // Not smoothed
                            // Advance phi by fixed amount
                            if (phi == phi1) loop = false
                            else if (P != null) {
                                phi += 1* DEG
                                if (phi > phi1) phi = phi1
                            }
                        }
                    }
                }
            }
        }
    }

    private fun tngnt(phi: Double): Vector {
        // https://www.emathzone.com/tutorials/geometry/equation-of-tangent-to-conic.html

        return Vector(-sin(phi),cos(phi) + polar.e).normalize()
    }

    fun curvature(phi: Double): Double {
        val c = if (isHyperbola) -polar.c else if (isEllipse) polar.c else -polar.l/2
        val r = rho(phi)

        return curvature(c + r*cos(phi),r*sin(phi))
    }

    fun curvature(x: Double,y: Double): Double {
        // radius of curvature = 1/|curvature|
        // https://www.wolframalpha.com/input?i=curvature+of+conic+section&assumption=%7B%22DPClash%22%2C+%22PlaneCurveP%22%2C+%22curvature%22%7D+-%3E+%7B%22ImplicitCurvature%22%7D
        // http://www.pas.rochester.edu/~dmw/ast203/Lectures/Lect_03.pdf


        return when  {
            isParabola -> {
                // https://www.wikiwand.com/en/Curvature
                1/(polar.l*(1 + (x/polar.l).pow(2)).pow(1.5))

                // WolframAlpha does not seem to provide a good formula
//                val q = 4*(polar.l/2).pow(2)
//                q/(q + x.pow(2)).pow(1.5)
            }
            isCircle -> {
                1 / axes.a
            }
            isEllipse ->  {
                val a2 = axes.a.pow(2)
                val b2 = axes.b.pow(2)

                a2 * b2 * (a2 * y.pow(2) + b2 * x.pow(2)) / (a2.pow(2) * y.pow(2) + b2.pow(2) * x.pow(
                    2
                )).pow(1.5)
            }
            isHyperbola -> {
                val a2 = axes.a.pow(2)
                val b2 = axes.b.pow(2)

                a2*b2*(axes.a*y - axes.b*x)*(axes.a*y + axes.b*x) / (a2.pow(2)*y.pow(2) + b2.pow(2)*x.pow(2)).pow(1.5)
            }
            else -> Double.POSITIVE_INFINITY
        }
    }

    override val fullDomain: AngleSet by lazy {
        // Full Domain is only one Interval for ConicSections

        val aSet = AngleSet.ANGLE_SET

        if (isParabola) // if (e == 1) ..
            (AngleSet(CLOSED,PI, PI, CLOSED).complement()) as AngleSet
        else if (isEllipse)
            aSet.UNIVERSAL_SET as AngleSet
        else if (isHyperbola){
            // Hyperbola
            val phi = acos(-1/polar.e) // 90 < phi < 180

            if (branch == NEGATIVE_BRANCH)
                AngleSet(OPEN, -phi, phi, OPEN)
            else
                AngleSet(OPEN,phi, TWO_PI - phi, OPEN)
        }
        else
            // Empty set
            AngleSet()
    }

    fun f(x: Double): List<Point> {
        val lst = mutableListOf<Point>()

        solveQuadraticReal(C,B*x + E, A*x.pow(2) + D*x + F).forEach { y ->
            lst.add(Point(x,y))
        }

        return lst
    }

    fun focusAngle(pt: Point): Double {
        val f = focalLoci[if (isHyperbola) 0 else 1]

        val phi = atan2(pt.y - f.y, pt.x - f.x) - theta

        return if (isHyperbola) {
            if ((branch and NEGATIVE_BRANCH) != 0)
                clampPI(phi)
            else
                clampPI(PI + phi)
        }
        else clampPI(phi)
    }

