    override fun hit(pt: Point) = z(pt).let { z ->
        // https://math.stackexchange.com/questions/817130/how-to-determine-if-arbitrary-point-lies-inside-or-outside-a-conic
        // It seems an inside would be on same side of the conic as a focal point
        // TODO Avoid scale dependency here


        if (abs(z) < TINY) {
            if (!isHyperbola)
                HIT_ON
            else {
                val br = if (pt.rotate(-theta).x < pos.rotate(-theta).x) NEGATIVE_BRANCH else POSITIVE_BRANCH
                // TODO branch should not have bit-flags
                if (br == branch) HIT_ON else HIT_OUTSIDE
            }
        }
        else if (((z > 0) && (det > 0)) || ((z < 0) && (det < 0)))
            HIT_INSIDE
        else
            HIT_OUTSIDE
    }

    fun z(pt: Point): Double {
        val X = BigDecimal.fromDouble(pt.x, MC)
        val Y = BigDecimal.fromDouble(pt.y, MC)

        return (BigDecimal.fromDouble(A, MC)*X.pow(2) +
                BigDecimal.fromDouble(B, MC)*X*Y +
                BigDecimal.fromDouble(C, MC)*Y.pow(2) +
                BigDecimal.fromDouble(D, MC)*X +
                BigDecimal.fromDouble(E, MC)*Y +
                BigDecimal.fromDouble(F, MC)).round()
    }

//    fun z(pt: Point): Double = A*pt.x.pow(2) + B*pt.x*pt.y + C*pt.y.pow(2) + D*pt.x + E*pt.y + F

    val extremals: List<Point> by lazy {
        val lst = mutableListOf<Point>()
        // dy/dx = 0
        if (isNotZero(A))
            solveQuadraticReal(4*A*C-B.pow(2),4*A*E-2*B*D,4*A*F-D.pow(2)).forEach { y ->
                lst.add(Point(-(B*y+D)/(2*A),y))
            }

        // dx/dy = 0
        if (isNotZero(B)) {
            solveQuadraticReal(4*A.pow(2)*C-A*B.pow(2),4*A*C*D-2*A*B*E,B.pow(2)*F-B*D*E+C*D.pow(2)).forEach { x ->
                lst.add(Point(x,-(2*A*x + D)/B))
            }
        }
        else {
            // B = 0
            solveQuadraticReal(4*A*C,4*C*D,4*C*F-E.pow(2)).forEach { x ->
                lst.add(Point(x,-E/(2*C)))
            }
        }

        if (isHyperbola) {
            val i = when {
                lst[0].x < lst[1].x -> 1
                lst[1].x < lst[0].x -> 0
                lst[0].y < lst[1].y -> 1
                else -> 0
            }

            if (branch == NEGATIVE_BRANCH)
                lst.removeAt(i) // Remove extremal point on positive branch
            else if (branch == POSITIVE_BRANCH)
                lst.removeAt((i+1)%2) // Remove extremal point on negative branch
        }

        lst.toList()
    }


    override fun construction(domain: RealSet?,tr: Transformation?, note: String?): String {
        val t = tr ?: Transformation.IDENTITY
        val buf = StringBuilder(note?.let { "$note " } ?: "")
        buf.append("${types(type)} ${subTypes(type)}")
        if (isHyperbola)
            buf.append(" ${if (branch == NEGATIVE_BRANCH) "NEGATIVE" else "POSITIVE"} BRANCH")
        buf.append("\n")
        val th = (theta + t.theta).let {
            "${if (it >= 0.0) "+" else "-"}${fixD(abs(it))}"
        }
        buf.append("Polar equation r() = ${fixL(polar.l)}/(1 + ${fix6(polar.e)}*cos())")
        if (domain != null)  buf.append(", where   $domain.")
        buf.append(" Center at ${t.transform(pos)}.")
        buf.append(" Axis rotated by $th")

        return buf.toString()
    }

    override fun toString(): String {
        val buf = StringBuilder()
        buf.append("${types(type)} ${subTypes(type)}")
        if (isHyperbola) buf.append("branch: ${if (branch == NEGATIVE_BRANCH) "NEGATIVE" else "POSITIVE"} BRANCH")
        buf.append("\ngenerate: ${generatorKT(this)}\n")
//        buf.append("\nequation: ${equation(this)}\n")
        buf.append("determinant: ${fixE(det)} subdeterminant: ${fixE(det33)}\n")
        if (isDegenerate) {

        }
        else {
            buf.append("rotated: ${fixD(theta)} ")
            buf.append(if (isParabola) "vertex at:" else "center at:")
            buf.append("$pos\n")
            if (isParabola)
                buf.append("a: ${fixL(polar.l / 2)}\n")
            else
                buf.append("a: ${fixL(axes.a)} b: ${fixL(axes.b)} eccentricity: ${fix(polar.e)}\n")
            buf.append("full domain: $fullDomain")
            if (domain != null) buf.append("\ndomain: $domain")
        }

        return buf.toString()
    }

    companion object {
        const val TYPE_MASK = 0x7
        const val SUBTYPE_MASK = 0xF shl 3

        const val UNDETERMINED = 0
        const val ELLIPSE = 0x1
        const val HYPERBOLA = 0x2
        const val PARABOLA = 0x3
        const val IMAGINARY = 0x4
        const val DEGENERATE = 0x5

        // DEGENERATE subtypes
        const val A_POINT = 0x1 shl 3   // Degenerate ellipse
        const val SINGLE_LINE = 0x2 shl 3 // if and only if A = B = C = 0 and D and E are not both zero
        const val PARALLEL_LINES = 0x3 shl 3 // Degenerate parabola
        const val INTERSECTING_LINES = 0x4 shl 3 // Degenerate hyperbola
        const val NOT_A_CONIC = 0x5 shl 3

        // ELLIPSE subtype
        const val CIRCLE = 0x5 shl 3

        fun types(type: Int) = when (mainType(type)) {
            ELLIPSE -> "ELLIIPSE"
            HYPERBOLA -> "HYPERBOLA"
            PARABOLA -> "PARABOLA"
            IMAGINARY -> "IMAGINARY"
            DEGENERATE -> "DEGENERATE"
            else -> "UNDETERMINED"
        }

        fun subTypes(type: Int)  = when (subType(type)) {
            A_POINT -> "A POINT"
            SINGLE_LINE -> "SINGLE LINE"
            PARALLEL_LINES -> "PARALLEL LINES"
            INTERSECTING_LINES -> "INTERSECTING LINES"
            CIRCLE -> "CIRCLE"
            else -> ""
        }

        fun mainType(typeFlags: Int) = typeFlags and TYPE_MASK

        fun subType(typeFlags: Int) = typeFlags and SUBTYPE_MASK

        fun isEllipse(type: Int) = mainType(type) == ELLIPSE

        fun isParabola(type: Int) = mainType(type) == PARABOLA

        fun isHyperbola(type: Int) = mainType(type) == HYPERBOLA

        fun isDegenerate(type: Int) = mainType(type) == DEGENERATE

        fun isImaginary(type: Int) = mainType(type) == IMAGINARY

        fun isCircle(type: Int) = (mainType(type) == ELLIPSE) && (subType(type) == CIRCLE)

        fun isNotAConic(type:Int) = (mainType(type) == DEGENERATE) && (subType(type) == NOT_A_CONIC)

        const val NEGATIVE_BRANCH = 1 // Indicates the part of a hyperbola that branches toward the NEGATIVE x-axis
        const val POSITIVE_BRANCH = 2 // Indicates the part of a hyperbola that branches toward the POSITIVE x-axis

        // For ellipses and hyperbolas a standard form has the x-axis as principal axis and
        // the origin (0,0) as center. The vertices are (a, 0) and the foci (c, 0). Define
        // b by the equations c = a  b for an ellipse and c = a + b for a hyperbola.
        // For a circle, c = 0 so a = b.
        // TODO  The type of conic made by createEllipse seems to depend on the position (xc,yc)
        fun createEllipse(a: Double,b: Double, th: Double,xc: Double,yc: Double): ConicSection {
            //  x     y
            //  --  +  -- = 1
            //  a     b
            //
            // bx + ay - ab = 0
            //
            val b2 = b.pow(2)
            val a2 = a.pow(2)

            return translate(rotate(ConicSection(b2,0.0,a2,0.0,0.0,-a2*b2),th),xc,yc).scaleCoefficients()
        }

        fun createHyperbola(a: Double, b: Double, th: Double, xc: Double, yc: Double, branch: Int= NEGATIVE_BRANCH): ConicSection {
            //  x     y
            //  --  -  -- = 1
            //  a     b
            //
            // bx - ay - ab = 0
            //
            val a2 = a.pow(2)
            val b2 = b.pow(2)

            return translate(rotate(ConicSection(b2,0.0,-a2,0.0,0.0,-a2*b2,branch=branch),th),xc,yc).scaleCoefficients()
        }

        fun createParabola(a: Double,th: Double,xc: Double,yc: Double): ConicSection {
            // y + 4 a x = 0
            // NB For the parabola: 0 <= theta <= 2PI

            // The standard form of the parabola has the focus on the x-axis at the point (-a, 0) and
            // the directrix is the line with equation x = a. In standard form the parabola will always
            // pass through the origin.
            //
            // y + 2 p x = 0, where p = 2*a is the focal parameter, the distance from the focus to the directrix
            // The branches of this parabola points along the negative x-axis
            // A = 0, B = 0, C = 1, D = 2p, E = 0, F = 0

            return translate(rotate(ConicSection(0.0,0.0,1.0,4*a,0.0,0.0),th),xc,yc).scaleCoefficients()
        }

        fun createConicSection(a: Double, e: Double, theta: Double, xf: Double, yf: Double, branch:Int= NEGATIVE_BRANCH): ConicSection {
            return when {
                e == 1.0 -> {
                    createParabola(a,theta,xf-a*cos(theta),yf-a*sin(theta))
                }
                e < 1.0 -> {
                    val f = (if (branch == 0) 1.0 else -1.0)*a*e
                    createEllipse(a, a*sqrt(1 - e.pow(2)), theta, xf + f*cos(theta), yf + f*sin(theta))
                }
                else -> { // e > 1
                    val f = (if (branch == 0) 1.0 else -1.0)*a*e
                    createHyperbola(a, a*sqrt(e.pow(2) - 1), theta, xf + f*cos(theta), yf + f*sin(theta),branch = branch)
                }
            }
        }

        fun intersects(cs1: ConicSection, cs2: ConicSection, col: Int?=null): List<Point> {
            val lst = mutableListOf<Point>()

            when {
                cs1.isDegenerate -> lst.addAll(degenerateIntersects(cs1,cs2))
                cs2.isDegenerate -> lst.addAll(degenerateIntersects(cs2,cs1))
                else -> {
                    val deg = degenerateCombination(cs1,cs2)
                    // TODO Could call lst.addAll(degenerateIntersects(deg,cs2))
                    col?.let {
                        print(it, deg)
                        printLn(it, "Line factors:")
                    }

                    factorDegenerate(deg).forEach { cln ->
                        col?.let {
                            printLn(it,"${cln.a} x  +  ${cln.b} y  =  ${-cln.c}")
//                            printLn(it, cln.toString())
                        }
                        if (cln.type != Line.IMPOSSIBLE)
                            lineIntersects(cln,cs1).forEach { pt ->
                                lst.add(pt)
                                col?.let { printLn(it,"${cs1.label} $pt") }
                            }
                    }
                }
            }

            return lst.toList()
        }

        fun degenerateCombination(cA: ConicSection, cB: ConicSection): ConicSection {
            // |my*cB + lam*cA| = 0
            val my = 1.0
            val a = ((4*cA.A*cA.C-cA.B.pow(2))*cA.F-cA.A*cA.E.pow(2)+cA.B*cA.D*cA.E-cA.C*cA.D.pow(2))/4
            val b = (((4*cA.A*cA.C-cA.B.pow(2))*cB.F+(4*cA.A*cB.C+4*cB.A*cA.C-2*cA.B*cB.B)*cA.F+(cA.B*cA.D-2*cA.A*cA.E)*cB.E-cB.A*cA.E.pow(2)+(cA.B*cB.D+cB.B*cA.D)*cA.E-2*cA.C*cA.D*cB.D-cB.C*cA.D.pow(2))*my)/4
            val c = (((4*cA.A*cB.C+4*cB.A*cA.C-2*cA.B*cB.B)*cB.F+(4*cB.A*cB.C-cB.B.pow(2))*cA.F-cA.A*cB.E.pow(2)+(-2*cB.A*cA.E+cA.B*cB.D+cB.B*cA.D)*cB.E+cB.B*cB.D*cA.E-cA.C*cB.D.pow(2)-2*cB.C*cA.D*cB.D)*my.pow(2))/4
            val d = (((4*cB.A*cB.C-cB.B.pow(2))*cB.F-cB.A*cB.E.pow(2)+cB.B*cB.D*cB.E-cB.C*cB.D.pow(2))*my.pow(3))/4

            val poly = Polynomial3(a,b,c,d)

            // TODO Check all solutions poly(x) == 0
            val l = Polynomial3.improve(poly, poly.roots.last())

            return linearCombination(cB,cA,l.doubleValue(false))
        }

        fun degenerateIntersects(deg: ConicSection, cs: ConicSection): List<Point> {
            val lst = mutableListOf<Point>()

            factorDegenerate(deg).forEach { cln ->
                lineIntersects(cln,cs).forEach { pt ->
                    lst.add(pt)
                }
            }

            return lst
        }

        fun lineIntersects(ln: Line, cs: ConicSection): List<Point> {
            val lst = mutableListOf<Point>()

            if (ln.type == Line.PARALLEL_Y_AXIS) {
                // Maybe not necessary
                val x = ln.x0

                solveQuadraticReal(cs.C,cs.B*x + cs.E,cs.A*x.pow(2) + cs.D*x + cs.F).forEach { y ->
                    lst.add(Point(x,y))
                }
            } else {
                // Rotate line to make it parallel with y-axis
                val delTheta = -atan2(ln.b, ln.a)

                val ln2 = Line.rotate(ln, delTheta)
                // Rotate the conics by the same angle
                val cs2 = rotate(cs, delTheta)

                val cTh = cos(-delTheta)
                val sTh = sin(-delTheta)

                // Line intersects with x-axis
                val x = ln2.x0

                solveQuadraticReal(cs2.C, cs2.B * x + cs2.E, cs2.A * x.pow(2) + cs2.D * x + cs2.F).forEach { y ->
                    lst.add(Point(x * cTh - y * sTh, x * sTh + y * cTh))
                }
            }

            return lst
        }

        fun lineIntersects2(ln: CLine, cs: ConicSection): List<Point> {
            val lst = mutableListOf<Point>()

            val t = complex(2,0)
            solveQuadraticComplex(
            (pow(ln.bc,t) *cs.A - ln.ac*ln.bc*cs.B + pow(ln.ac,t) *cs.C) / pow(ln.bc,t),
            -((ln.bc*cs.B - ln.ac*2*cs.C)*ln.cc - pow(ln.bc,t) *cs.D + ln.ac*ln.bc*cs.E)/ pow(ln.bc,t),
            (pow(ln.cc,t) *cs.C - ln.bc*ln.cc*cs.E + pow(ln.bc,t) *cs.F)/ pow(ln.bc,t)
            ).forEach { x ->
                if (isZero(x.im))
                    cs.f(x.re).forEach { pt ->
                        if (isTiny(ln.z(pt))) lst.add(pt)
                    }
            }

            return lst.toList()
        }

        /**
         * Factors a degenerate conic section into real lines, if any
         * @param degenerate A degenerate conic section, i. e. det == 0
         * @return a List of the Lines representing the degenerate conic
         */
        fun factorDegenerate(degenerate: ConicSection): List<Line> {
            // http://users.math.uoc.gr/~pamfilos/eGallery/problems/ConicsDegenerate.html
            // Note that this derivation is not valid for A == 0!
            //
            // https://www.emathhelp.net/en/calculators/algebra-2/conic-section-calculator/
            // Very useful!
            val lns = mutableListOf<Line>()

            with(degenerate) {
                if (isDegenerate(type)) {
                    if (!isZero(A)) {
                        // A != 0, so the following is a direct implementation of the above ConicsDegenerate.html page
                        val a = A
                        val b = B / 2
                        val c = C
                        val d = D / 2
                        val e = E / 2
                        val f = F

                        val u2 = (b.pow(2) - a * c).let {
                                if (isTiny(it)) 0.0 else it
                            }

                        val v2 = (d.pow(2) - a * f).let {
                                if (isTiny(it)) 0.0 else it
                            }

                        if ((u2 >= 0.0) && (v2 >= 0.0)) {
                            val u = sqrt(u2)
                            val v_ = sqrt(v2)
                            val uv = b * d - a * e
                            val v = if (uv < 0.0) -v_ else v_

                            lns.add(Line(a, b + u, d + v))
                            lns.add(Line(a, b - u, d - v))
                        }
                    }
                    else if (!isZero(C)) {
                        // A == 0, but C != 0 so same as above but with the following swaps
                        // a <-> c, d <-> e, x <-> y
                        // val a = A
                        val b = B / 2
                        val c = C
                        val d = D / 2
                        val e = E / 2
                        val f = F


                        val u2 = (b.pow(2)).let {
                                if (isTiny(it)) 0.0 else it
                            }

                        val v2 = (e.pow(2) - c * f).let {
                                if (isTiny(it)) 0.0 else it
                            }

                        if ((u2 >= 0.0) && (v2 >= 0.0)) {
                            val u = sqrt(u2)
                            val v_ = sqrt(v2)
                            val uv = b * e - c * d
                            val v = if (uv < 0.0) -v_ else v_

                            lns.add(Line(b + u, c, e + v))
                            lns.add(Line(b - u, c, e - v))
                        }
                    }
                    // The determinant when A = 0 and C = 0 is (D E - B F) B/4
                    else if (!isZero(B)) {
                        // B != 0, D E - B F == 0
                        lns.add(Line(B,0.0,E/B))
                        lns.add(Line(0.0,B,D/B))
//                        lns.add(Line(1.0,0.0,E/B))
//                        lns.add(Line(0.0,1.0,D/B))
                    }
                    else {
                        // B == 0
                        if ((!isZero(D)) || (!isZero(E)))
                            lns.add(Line(D,E,F))
                    }
                }
            }

            return lns.toList()
        }

        fun linearCombination(c1: ConicSection, c2: ConicSection, f: Double) = scaleCoefficients(
            ConicSection(
                c1.A + f*c2.A,
                c1.B + f*c2.B,
                c1.C + f*c2.C,
                c1.D + f*c2.D,
                c1.E + f*c2.E,
                c1.F + f*c2.F
            )
        )

        fun scaleCoefficients(cs: ConicSection, scale: Double = 10.0) : ConicSection {
            var absmax = 1.0

            if (abs(cs.A) > absmax) absmax = abs(cs.A)
            if (abs(cs.B) > absmax) absmax = abs(cs.B)
            if (abs(cs.C) > absmax) absmax = abs(cs.C)
            if (abs(cs.D) > absmax) absmax = abs(cs.D)
            if (abs(cs.E) > absmax) absmax = abs(cs.E)
            if (abs(cs.F) > absmax) absmax = abs(cs.F)

            val f = scale/absmax

            return ConicSection(f*cs.A,f*cs.B,f*cs.C,f*cs.D,f*cs.E,f*cs.F,cs.branch)
        }

          // Rotates graph about origin (original version)
        fun rotate(cs: ConicSection, delTheta: Double): ConicSection {
            // TODO See https://vonsteuben.enschool.org/ourpages/auto/2013/8/26/58023530/Chapter%208%20_pg_%20631-698_.pdf
            val cTh = cos(delTheta)
            val sTh = sin(delTheta)
            val cTh2 = cTh.pow(2)
            val sTh2 = sTh.pow(2)

            val Ar = cs.A*cTh2 - cs.B*cTh*sTh + cs.C*sTh2
            val Br = cs.B*(cTh2 - sTh2) - 2*(cs.C - cs.A)*cTh*sTh
            val Cr = cs.C*cTh2 + cs.B*cTh*sTh + cs.A*sTh2
            val Dr = cs.D*cTh - cs.E*sTh
            val Er = cs.E*cTh + cs.D*sTh
            // val Fr = cs.F

            return ConicSection(Ar,Br,Cr,Dr,Er,cs.F,branch=cs.branch)
        }

        fun rotateAbsolute(cs: ConicSection, theta: Double) = rotate(rotate(cs,-cs.theta),theta)

        fun translate(cs: ConicSection, delX: Double, delY: Double): ConicSection {
            // val At = cs.A
            // val Bt = cs.B
            // val Ct = cs.C
            val Dt = cs.D - 2*cs.A*delX - cs.B*delY
            val Et = cs.E - cs.B*delX - 2*cs.C*delY
            val Ft = cs.F + cs.A*delX.pow(2) + cs.B*delX*delY + cs.C*delY.pow(2) - cs.D*delX - cs.E*delY

            return ConicSection(cs.A,cs.B,cs.C,Dt,Et,Ft,branch=cs.branch)
        }

        fun rotation(cs: ConicSection): Double {
            // https://www.wikiwand.com/en/Matrix_representation_of_conic_sections
            //
            // By the principal axis theorem, the two eigenvectors of the matrix of the quadratic
            // form of a central conic section (ellipse or hyperbola) are perpendicular (orthogonal
            // to each other) and each is parallel to (in the same direction as) either the major
            // or minor axis of the conic. The eigenvector having the smallest eigenvalue (in absolute value)
            // corresponds to the major axis.[11]
            val lam = solveQuadraticReal(1.0,-cs.A-cs.C,cs.A*cs.C - (cs.B/2).pow(2)).sortedBy {
                abs(it)
            }
            // lam[0] is eigenvalue with smallest absolute value
            // lam[0] == 0 ==> parabola or degenerate
            // lam[0]*lam[1] > 0 ==> ellipse or degenerate
            // lam[0]*lam[1] < 0 ==> hyperbola or degenerate

            // A33 v = lam v
            //
            // (i)  A x + B/2 y = lam x  -->  dy/dx = (lam - A)/(B/2)
            // (ii) B/2 x + C y = lam y  -->  dy/dc = (B/2)/(lam - C)

            return if (isZero(lam[0])) {
                // Parabola
                val phi = if (isZero(cs.C - cs.A)) sign(cs.B)*PI/4 else atan(cs.B/(cs.C - cs.A))/2

                clampPI(listOf<Double>(-PI - phi, -PI_HALF - phi, -phi, PI_HALF - phi, PI - phi).filter {
                    val csr = rotate(cs, -it)
                    // Axis parallel to x-axis and branches along positive x-axis
                    isZero(csr.A) && (csr.C * csr.D < 0)
                }[0] + PI)

//                listOf<Double>(-PI-phi,-PI_HALF -phi,-phi, PI_HALF -phi,PI-phi).filter {
//                    val csr = rotate(cs,-it)
//                    // Axis parallel to x-axis and branches along positive x-axis
//                    isZero(csr.A) && (csr.C*csr.D < 0)
//                }[0]
            }
            else {
                // Ellipse or hyperbola

                if (isZero(cs.B)) {
                    val K = cs.D.pow(2)/(4*cs.A) + cs.E.pow(2)/(4*cs.C) - cs.F
                    val p = K/cs.A // a^2
                    val q = K/cs.C // b^2

                    if (p*q > 0) {
                        // Ellipse
                        if (abs(p) > abs(q)) 0.0 else PI_HALF
                    }
                    else {
                        // Hyperbola
                        if (p < 0) PI_HALF else 0.0
                    }
                } else {
                    // val th = if (isZero(cs.C - cs.A)) sign(cs.B)*PI/4 else atan(cs.B/(cs.C - cs.A))/2
                    val th = atan((lam[0] - cs.A)/(cs.B/2))

                    if (lam[0]*lam[1] > 0) {
                        // Ellipse
                        th
                    }
                    else {
                        // Hyperbola
                        val K = -cs.det/cs.det33
                        val p = K/lam[0]
                        if (p < 0) PI_HALF + th else th
                    }
                }
            }
        }

        fun position(cs: ConicSection): Point {
            return when {
                cs.isCentral -> {
                    Point(
                        (cs.B * cs.E - 2 * cs.C * cs.D) / (4 * cs.det33),
                        (cs.D * cs.B - 2 * cs.A * cs.E) / (4 * cs.det33)
                    )
                }
                cs.isParabola -> {
                    val cTh = cos(-cs.theta)
                    val sTh = sin(-cs.theta)
                    val cTh2 = cTh.pow(2)
                    val sTh2 = sTh.pow(2)

//                    val Ar = cs.A*cTh2 - cs.B*cTh*sTh + cs.C*sTh2
//                    val Br = cs.B*(cTh2 - sTh2) - 2*(cs.C - cs.A)*cTh*sTh
//                    assert(isTiny(Ar) && isTiny(Br)) {
//                        println("A=$Ar, B=$Br. Should both be 0")
//                    }
                    val Cr = cs.C*cTh2 + cs.B*cTh*sTh + cs.A*sTh2
                    val Dr = cs.D*cTh - cs.E*sTh
                    val Er = cs.E*cTh + cs.D*sTh
                    // val Fr = cs.F

                    val yt = -Er/(2*Cr)
                    val xt = -(4*Cr*cs.F - Er.pow(2))/(4*Cr*Dr)
                    //val a = -Dr/4

                    Point.rotate(Point(xt.toDouble(), yt.toDouble()), cs.theta)
                }
                else -> Point(0.0,0.0)
            }
        }


        fun whichBranch(centralAngle: Double): Int {
            return if (abs(clampPI(centralAngle)) < PI_HALF) POSITIVE_BRANCH else NEGATIVE_BRANCH
        }

        fun semiAxes(cs: ConicSection): Axes {
            // https://www.wikiwand.com/en/Conic_section Conversion to canonical form
            val (l1, l2) = solveQuadraticReal(1.0, -(cs.A + cs.C), cs.det33)

            val K = -cs.det/cs.det33

            val p = K/l1
            val pr = sqrt(abs(p))
            val q = K/l2
            val qr = sqrt(abs(q))

            return if (l1*l2 > 0)
                // Axes(pr,qr)
                Axes(max(pr,qr),min(pr,qr))
            else
                Axes(if (p > 0) pr else qr, if (p < 0) pr else qr)
        }

        fun polarParameters(cs: ConicSection): Polar {
            val a = cs.axes.a
            val b = cs.axes.b

            // Eccentricity (e), semi-latus rectum (l), linear eccentricity (c)
            return when {
                isCircle(cs.type) -> Polar(0.0, a, 0.0)
                cs.isEllipse -> {
                    val e = sqrt(1 - (b / a).pow(2))
                    Polar(e, b.pow(2) / a, a * e)
                }
                cs.isParabola -> {
                    val canonical = cs.canonicalForm
                    val a_ = canonical.D/(4*canonical.C)
                    val l = 2*a_
                    Polar(1.0, l.toDouble(), 0.0)
                }
                cs.isHyperbola -> {
                    val e = sqrt(1 + (b / a).pow(2))
                    Polar(e, b.pow(2) / a, a * e)
                }
                else -> Polar(0.0, 0.0, 0.0)
            }
        }

        fun eccentricity(cs: ConicSection): Double {
            // https://www.wikiwand.com/en/Conic_section  Eccentricity in terms of coefficients
            val Q = hypot(cs.A-cs.C,cs.B)
            val eta = sign(cs.det)

            return sqrt((2*Q)/(eta*(cs.A + cs.C) + Q))
        }

        fun equation(cs: ConicSection): String {
            val b = StringBuilder()

            if (!isTiny(cs.A)) {
                b.append("${fix(cs.A)} x ")
            }
            if (!isTiny(cs.B)) {
                b.append(if (cs.B >= 0) "+" else "-").
                append(" ${fix(abs(cs.B))} xy ")
            }
            if (!isTiny(cs.C)) {
                b.append(if (cs.C >= 0) "+" else "-").
                append(" ${fix(abs(cs.C))} y ")
            }
            if (!isTiny(cs.D)) {
                b.append(if (cs.D >= 0) "+" else "-").
                append(" ${fix(abs(cs.D))} x ")
            }
            if (!isTiny(cs.E)) {
                b.append(if (cs.E >= 0) "+" else "-").
                append(" ${fix(abs(cs.E))} y ")
            }
            if (!isTiny(cs.F)) {
                b.append(if (cs.F >= 0) "+" else "-").
                append(" ${fix(abs(cs.F))}")
            }
            b.append(" = 0")

            return b.toString()
        }

        fun generatorKT(cs: ConicSection): String {
            val b = StringBuilder("ConicSection.")

            b.append(
                when (mainType(cs.type)) {
                    ELLIPSE -> "createEllipse(${fix(cs.axes.a)},${fix(cs.axes.b)}"
                    HYPERBOLA -> "createHyperbola(${fix(cs.axes.a)},${fix(cs.axes.b)}"
                    PARABOLA -> "createParabola(${cs.polar.l / 2}"
                    else ->""
                }
            )

            b.append(",${fix(cs.x0)},${fix(cs.y0)},${fix2(cs.theta * RAD)}*DEG")

            if (cs.type == HYPERBOLA)
                b.append(",branch=${cs.branch})")
            else
                b.append(")")

            return b.toString()
        }

        fun matrix(cs: ConicSection): String {
            val b = cs.B/2
            val d = cs.D/2
            val e = cs.E/2

            return """
                ${fixw(cs.A, 8, 14)}${fixw(b, 8, 14)}${fixw(d, 8, 14)}
                |${fixw(b, 8, 14)}${fixw(cs.C, 8, 14)}${fixw(e, 8, 14)}|
                ${fixw(d, 8, 14)}${fixw(e, 8, 14)}${fixw(cs.F, 8, 14)}
            """.trimIndent()
        }

        /**
         * Slices a cone with a plane as is the definition of a conic section. The method will also yield
         * the degenerate cases that are lines. The resulting conic will have its main axis along the y-axis
         * @param G Distance between top of cone and the intersecting plane > 0
         * @param omega The angle between the cone's axis and the intersecting plane's normal
         * @param alpha Half of the cone's solid angle
         */
        fun sliceCone(
            G: Double,      // Distance between top of cone and the intersecting plane > 0
            omega: Double,  // The angle between the cone's axis and the intersecting plane's normal
            alpha: Double   // Half of the cone's solid angle
        ): Geometry? {
            val cs = if (isZero(alpha - PI_HALF))
                // This cone is actually a plane. It is obtained from the below ConicSection by dividing
                // its coefficients by tan(alpha), then taking the limits as alpha -> 90
                ConicSection(
                        0.0,
                        0.0,
                        sin(omega).pow(2),
                        0.0,
                        -2*G*cos(omega)*sin(omega),
                        (G*cos(omega)).pow(2)
                    ).also {
                }
           else
               ConicSection(
                    1.0,
                    0.0,
                    cos(omega).pow(2) - tan(alpha).pow(2)*sin(omega).pow(2),
                    0.0,
                    2*G*tan(alpha).pow(2)*cos(omega)*sin(omega)+2*G*cos(omega)*sin(omega),
                    G.pow(2)*sin(omega).pow(2)-G.pow(2)*tan(alpha).pow(2)*cos(omega).pow(2)
                )

            return if (cs.isDegenerate) {
                    factorDegenerate(cs).let { lst ->
                        if (lst.isEmpty()) null else lst[0]
                    }
            }
            else {
                if (!cs.isHyperbola)
                    cs
                else {
                    val br = if (alpha > PI_HALF) NEGATIVE_BRANCH else POSITIVE_BRANCH

                    ConicSection(cs,branch=br)//.scaleCoeffisients()
                }
            }
        }

        fun planeAltitude(G: Double,alt: Double) = ConicSection(1.0,0.0,1.0,0.0,0.0,-(G/tan(alt)).pow(2))
    }
}