package sundial.numberset

import TWO_PI
import fixD


class AngleSet(interval: List<NumberSet<Double>.Interval>) : CyclicSet(TWO_PI,interval) {

    constructor(leftType: Int, leftValue: Double, rightValue: Double, rightType: Int) :
            this(cyclicInterval(ANGLE_SET,leftType,leftValue,rightValue,rightType))

    constructor() : this(emptyList())

    override fun union(leftType: Int, leftValue: Double, rightValue: Double, rightType: Int): AngleSet {
        val itvs = cyclicInterval(leftType, leftValue, rightValue, rightType)

        return if (itvs.isEmpty())
            this
        else {
            val cs = AngleSet(itvs)

            union(cs) as AngleSet
        }
    }

    override fun str(n: Double) = fixD(n)

    override val stridx = ""

    override fun instance(intervals: List<Interval>) = AngleSet(intervals)

    override fun scale(f: Double) = this

    companion object {
        val ANGLE_SET = AngleSet()
        val UNIVERSAL_SET = ANGLE_SET.UNIVERSAL_SET as AngleSet
    }
}