package sundial.geometry

import MeasuredTextData

open class Text(val mt: MeasuredTextData, tilt: Double, pos: Point) : Rectangle(mt.width,mt.height,tilt,pos.x,pos.y) {
    override fun typeName() = "TEXT"
    override fun scale(f: Double) =
        Text(
            MeasuredTextData(mt.txt,f*mt.fontSizeMm,f*mt.width,f*mt.height, mt.offset*f),
            theta,
            Point(f*pos.x,f*pos.y)
        )//.also { printLn(MAGENTA,"${it.mt.txt} scale:${fix6(f)} x font-size:${mt.fontSizeMm}=${it.mt.fontSizeMm}") }
}


