package sundial.geometry

import fixL
import sundial.math.Vector
import kotlin.math.cos
import kotlin.math.hypot
import kotlin.math.sin

open class Point(val x: Double,val y: Double) {

    private operator fun invoke(x: Double, y: Double): Point {
        return Point(x,y)
    }

    operator fun plus(v: Vector): Point {
        return invoke(x+v.delX,y+v.delY)
    }

    operator fun minus(v: Vector): Point {
        return invoke(x-v.delX,y-v.delY)
    }

    open fun rotate(delTheta: Double) = rotate(this,delTheta)

    open fun translate(delX: Double,delY: Double) = translate(this,delX,delY)

    override fun toString(): String {
        return "{${fixL(x)},${fixL(y)}}"
    }

    fun toVector() = Vector(x,y)

    operator fun component1() = x

    operator fun component2() = y

    companion object {
        val ORIGO = Point(0.0,0.0)

        fun rotate(pt: Point, delTheta: Double): Point {
            val cTh = cos(delTheta)
            val sTh = sin(delTheta)

            return Point(pt.x*cTh - pt.y*sTh,pt.x*sTh + pt.y*cTh)
        }

        fun translate(pt: Point, delX: Double, delY: Double) = Point(pt.x+delX,pt.y+delY)

        fun distance(pt1: Point, pt2: Point) = hypot(pt2.x - pt1.x,pt2.y - pt1.y)

        fun cog(vararg pts: Point): Point? { // Center of Gravity
            var x = Double.NaN
            var y = Double.NaN

            pts.forEach {
                if (x.isNaN()) {
                    x = it.x
                    y = it.y
                }
                else {
                    x += it.x
                    y += it.y
                }
            }

            return if (pts.size == 0) null else Point(x/pts.size,y/pts.size)
        }
    }
}