package sundial.geometry

import RAD
import TINY
import fix
import fix2
import fixD
import isTiny
import isZero
import sundial.math.Transformation
import sundial.math.Vector
import sundial.numberset.NumberSet
import sundial.numberset.NumberSet.Companion.CLOSED
import sundial.numberset.NumberSet.Companion.OPEN
import sundial.numberset.RealSet
import kotlin.math.*

open class Line(): Geometry() {
    // a line:    fullDomain = RealSet.R
    // a ray:     fullDomain = RealSet.R_PLUS
    // a segment: fullDomain = RealSet(false,0.0,segmentLength,false)

    // Equation: a*x + b*y + c = 0
    constructor(a: Double,b: Double,c: Double,side:Int = LEFT_SIDE,fullDomain: RealSet = RealSet.R) : this() {
        this.a = a
        this.b = b
        this.c = c
        this.side = side

        fullDomain_ = fullDomain
    }

    constructor(ln: Line, side: Int=LEFT_SIDE, fullDomain: RealSet = RealSet.R) : this() {
        a = ln.a
        b = ln.b
        c = ln.c
        this.side = side
        P = ln.pos
        u = ln.u
        this.domain = ln.domain

        fullDomain_ = fullDomain
    }

    constructor(pt0: Point, pt1: Point, side:Int = LEFT_SIDE) : this(pt0.x,pt0.y,pt1.x,pt1.y,side=side)

    constructor(x0: Double,y0: Double,x1: Double,y1: Double,side:Int = LEFT_SIDE) : this() {
        Point(x0,y0).let {
            P = it
            u = Vector(it, Point(x1,y1)).normalize()
        }

        fullDomain_ = RealSet(CLOSED,0.0, hypot(x1 - x0,y1 - y0),OPEN)

        a = y1 - y0
        b = x0 - x1
        c = (x1-x0)*y0 - (y1-y0)*x0
        this.side = side
    }

    constructor(pt: Point, u: Vector,side:Int = LEFT_SIDE, fullDomain: RealSet = RealSet.R_PLUS) : this() {
        P = pt
        this.u = u.normalize()
        this.side = side

        a = u.delY
        b = -u.delX
        c = u.delX*pt.y - u.delY*pt.x

        fullDomain_ = fullDomain
    }

    var side: Int = LEFT_SIDE
        private set

    var a: Double = Double.NaN
        private set

    var b: Double = Double.NaN
        private set

    var c: Double = Double.NaN
        private set

    override val type by lazy {
        if (isTiny(a) && isTiny(b)) IMPOSSIBLE
        else if (isTiny(a)) PARALLEL_X_AXIS
        else if (isTiny(b)) PARALLEL_Y_AXIS
        else REGULAR
    }

    override fun typeName() = types[type] ?: super.typeName()

    private var P: Point = NAN_POINT
        get() {
            if (field == NAN_POINT) field = center(this)

            return field
        }

    override fun position(): Point = P

    // Unit vector along direction of ln. dy/dx = -a/b -> delX = 1, delY = -a/b
    // Normalize and rotate the unit vector
    var u: Vector = NAN_VECTOR
        private set
        get() {
            if (field == NAN_VECTOR) field = direction(this)

            return field
        }

    val n: Vector by lazy {
        Vector(-u.delY,u.delX)
    }

    override val theta: Double by lazy {
        u.rotation
//        atan2(u.delY,u.delX)
    }

    override val canonicalForm: Line by lazy {
        // x-axis
        Line(Point.ORIGO, Vector(1.0,0.0))
    }

    override fun toPoint(t: Double): Point? {
        // toPoint() does take account of the fullDomain
        return if (!fullDomain.contains(t))
            null
        else
            Point(
                pos.x + t * u.delX,
                pos.y + t * u.delY
            )
    }

    override fun points(itv: NumberSet<Double>.Interval?, smoothed: Boolean) = sequence<Point> {
        var t0: Double
        var t1: Double

        if (itv != null) {
            t0 = itv.leftBrace.value
            // TODO All Geometry should be clipped to avoid infinities
            if (t0.isInfinite()) t0 = -10000.0
            if (itv.leftBrace.type == OPEN)
                t0 += TINY
            t1 = itv.rightBrace.value
            if (t1.isInfinite()) t1 = 10000.0
            if (itv.rightBrace.type == OPEN)
                t1 -= TINY
            toPoint(t0)?.let {
                yield(it)
            }
            toPoint(t1)?.let {
                yield(it)
            }
        }
    }

    private var fullDomain_: RealSet = RealSet.R

    override val fullDomain by lazy {
        fullDomain_
    }

    override fun rotate(delTheta: Double) = rotate(this,delTheta)

    override fun translate(delX: Double,delY: Double) = translate(this,delX,delY)

    override fun scale(f: Double) =  Line(Line(Point(f*this.x0,f*this.y0),this.u),side,fullDomain.scale(f))

    fun z(p: Point) = a*p.x + b*p.y + c

    override fun hit(pt: Point): Int {
        // hit() does not take account of the fullDomain
        val v = Vector(pos,pt)
        val d = Vector.dot(v,n)

        return when {
            isTiny(d) -> HIT_ON
            d > 0 -> HIT_LEFT
            else -> HIT_RIGHT
        }
    }

//    override fun hit(pt: Point): Int {
//        // hit() does not take account of the fullDomain
//        val d = distance(pt)
//
//        return when {
//            isTiny(d) -> HIT_ON
//            d > 0 -> HIT_RIGHT
//            else -> HIT_LEFT
//        }
//    }

    // isOn() does not take account of the fullDomain
    fun isOn(p: Point) = isTiny(z(p) / hypot(a, b))

//    override fun toParameter(pt: Point): Double? = if (isOn(pt)) {
//        Vector.dot(Vector(pos, pt), u).let {
//            if (fullDomain.contains(it)) it else null
//        }
//    } else null

    override fun toParameter(pt: Point): Double? = if (isOn(pt)) Vector.dot(Vector(pos, pt), u) else null

//    override fun toParameter(p: Point): Double? = Vector.dot(Vector(pos, p), u)


    override fun tangent(t: Double): Vector? = u

    override fun normal(t: Double): Vector? {

        return if (side == LEFT_SIDE) super.normal(t) else super.normal(t)?.times(-1.0)
    }

    override val axis: Line by lazy {
        Line(pos,if (side== LEFT_SIDE) Vector(-u.delY, u.delX) else Vector(u.delY,-u.delX))
    }

//    // NOTE: distance() is signed!
//    fun distance(pt: Point) = Vector(pt.x - x0,pt.y - y0).componentNormalTo(u).norm()

    // NOTE: distance() is signed!
    fun distance(pt: Point) = z(pt)/ hypot(a,b)

    override fun construction(domain: RealSet?, tr: Transformation?, note: String?): String {
        val t = tr ?: Transformation.IDENTITY
        val n = note?.let { "$note "} ?: ""
        val s = if (side == RIGHT_SIDE) "RIGHT" else "LEFT"

        return if (domain == null)
            "${n}LINE through ${t.transform(pos)}, at an angle of ${fixD(theta+t.theta)}. Side: $s. Domain: $fullDomain"
        else {
            toPoint(domain.first())?.let { p1 ->
                toPoint(domain.last())?.let { p2 ->
                    "${n}LINE from ${t.transform(p1)} to ${t.transform(p2)}"
                }
            } ?: ""
        }
    }

    fun otherSide() = Companion.otherSide(this.side)
//    fun otherSide() = if (this.side == LEFT_SIDE) RIGHT_SIDE else LEFT_SIDE

    override fun toString(): String {
        val b = StringBuilder()

        b.append("LINE  ${types[this.type]}\n")
        b.append("${toKotlin(this)}\n")
//        b.append("${equation(this)}\n")
        b.append("parametric zero point: $pos\n")
        b.append("direction: ${fixD(u.rotation)}\n")
        b.append("side: ${if (side == LEFT_SIDE) "left" else "right"}\n")
        b.append("full domain: $fullDomain")
        domain?.let {
            b.append("\ndomain: $it")
        }

        return b.toString()
    }

    companion object {
        const val REGULAR = 0x1 shl 5
        const val PARALLEL_X_AXIS = 0x2 shl 5
        const val PARALLEL_Y_AXIS = 0x3 shl 5
        const val IMPOSSIBLE = 0x4 shl 5

        val types = mapOf(
            REGULAR to "REGULAR",
            PARALLEL_X_AXIS to "PARALLEL_X_AXIS",
            PARALLEL_Y_AXIS to "PARALLEL_Y_AXIS",
            IMPOSSIBLE to "IMPOSSIBLE"
        )

        const val LEFT_SIDE = HIT_LEFT
        const val RIGHT_SIDE = HIT_RIGHT

        private val NAN_POINT: Point = Point(Double.NaN, Double.NaN)
        private val NAN_VECTOR: Vector = Vector(Double.NaN,Double.NaN)

        // Rotates graph about origin
        fun rotate(ln: Line, delTheta_: Double): Line {
            val delTheta = -delTheta_
            val cTh = cos(delTheta)
            val sTh = sin(delTheta)

            return Line(ln.b*sTh + ln.a*cTh,ln.b*cTh - ln.a*sTh,ln.c, side = ln.side, fullDomain = ln.fullDomain)
        }

        fun translate(ln: Line, delX_: Double, delY_: Double): Line {
            val delX = -delX_
            val delY = -delY_

            return Line(ln.a,ln.b,ln.b*delY + ln.a*delX + ln.c, side = ln.side, fullDomain = ln.fullDomain)
        }

        fun center(ln: Line): Point {
            // Point on line closest to origin
            // https://en.wikipedia.org/wiki/Distance_from_a_point_to_a_line
            val f = -ln.c / (ln.a.pow(2) + ln.b.pow(2))

            return Point(f * ln.a, f * ln.b)
        }

        fun rotation(ln: Line): Double = atan(ln.u.delY/ ln.u.delX)

        fun direction(ln: Line): Vector = if (isTiny(ln.b))
            Vector(0.0, 1.0)
        else
            Vector(1.0, -ln.a / ln.b).normalize()

        fun domainLength(ln: Line) = ln.domain?.span ?: 0.0

