        fun equation(ln: Line): String {
            val q = listOf(ln.a,ln.b,ln.c).filter { it != 0.0 }.minBy { abs(it) }
            val order = log10(q).toInt()
            val f = 10.0.pow(1 - order)

            return StringBuilder().append("${fix(f * ln.a)} x ").
            append(if (ln.b >= 0) "+" else "-").
            append(" ${fix(abs(f * ln.b))} y ").
            append(if (ln.c >= 0) "+" else "-").
            append(" ${fix(abs(f * ln.c))}").
            append(" = 0").
            toString()
        }

        fun toKotlin(ln: Line): String {
            return "Line(Point(${fix(ln.pos.x)},${fix(ln.pos.y)}),Vector(1.0,0.0).rotate(${fix2(ln.theta * RAD)}*DEG),side=${ln.side},fullDomain=${ln.fullDomain_})"
        }

        fun hit(h: Int) = when (h) {
            HIT_ON -> "HIT_ON line"
            LEFT_SIDE -> "HIT_LEFT of line"
            RIGHT_SIDE -> "HIT_RIGHT of line"
            else -> "unknown hit"
        }

        fun intersect(ln1: Line,ln2: Line): Point? {
            val denom = ln1.u.delX*ln2.u.delY - ln1.u.delY*ln2.u.delX

            return if (isZero(denom))
                null
            else {
                // Does not check whether the intersection point lies within the fullDomains of the lines
                (((ln2.P.x - ln1.P.x) * ln2.u.delY + (ln1.P.y - ln2.P.y) * ln2.u.delX) / denom).let { s ->
                    ln1.P + ln1.u * s
                }
            }
        }

