        fun product(ln1: Line, ln2: Line) = ConicSection(
                ln1.a*ln2.a,
                ln1.a*ln2.b + ln2.a*ln1.b,
                ln1.b*ln2.b,
                ln1.a*ln2.c + ln2.a*ln1.c,
                ln1.b*ln2.c + ln2.b*ln1.c,
                ln1.c*ln2.c
            )

        fun isCoincident(ln1: Line?, ln2: Line?) = if ((ln1 == null) || (ln2 == null))
                false
        else
            (ln1.hit(ln2.pos) == HIT_ON) && (isTiny(ln1.theta-ln2.theta))



        fun otherSide(thisSide: Int) = when (thisSide) {
            LEFT_SIDE -> RIGHT_SIDE
            RIGHT_SIDE -> LEFT_SIDE
            else -> HIT_ON
        }
    }

}