package sundial.math
import ITER_MAX
import YELLOW
import fix2
import fix6
import printLn
import printLnErr
import printLnTmp
import printLnWrn
import sundial.geometry.Point
import kotlin.math.sign

/**
 * A simple class to find zero points and extremal points of a single valued function **f(t)** of one independent
 * variable **t**. Analytics assumes that the function is defined and has a derivative over a single, contiguous
 * interval.
 * @param minT the smallest limiting value of the interval
 * @param maxT the largest limiting value of the interval
 * @param n the number of smaller intervals the interval (minT,maxT) should be divided into. Each small interval should contain
 * at most one zero point or extremal point
 * @param deltaT acceptable precision in abscissa (t) values
 * @param info set true to print some debugging info
 * @param df the derived of [f]. It is null by default, this will cause an internal df to be used
 * @param f the function to be analysed. It may be supplied as a lambda function
 */
class Analytics(val minT: Double, val maxT: Double, val n: Int, val deltaT: Double, val info: Boolean=false, val df:((t: Double) -> Double)? = null, val f:(t: Double) -> Double) {
    /**
     * A safe version of [f] that does not call on [f] with t-values outside the interval
     * @return f(minT) for t < minT, f(maxT) for t > maxT, else f(t)
      */
    fun fSafe(t: Double) = if (t <= minT)
        f(minT)
    else if (maxT <= t)
        f(maxT)
    else
        f(t)

    /**
     * A numeric derivative df to [f]
     * @return **df(t)** approximately
     */
    fun dfNumeric(t: Double) = if (t < minT+deltaT/2)
        f(minT+deltaT) - f(minT)
    else if (maxT-deltaT/2 < t)
        f(maxT) - f(maxT-deltaT)
    else
        f(t+deltaT/2) - f(t-deltaT/2)

    /**
     * @property dff A function val that holds [df], if [df] is not null, else it holds [dfNumeric]. [dff] is
     * the derived of [f] that is used in the analysis
     */
    val dff = df ?: ::dfNumeric

    /**
     * @property zeropoints The abscissa (t) values of the zero points of [f], sorted in ascending order
     */
    val zeropoints: List<Double> by lazy {
        val z = mutableListOf<Double>()

        val l = maxT - minT
        var tl: Double
        var fl: Double
        var tr = 0.0
        var fr = fSafe(tr)

        for (i in 1 .. n) {
            tl = tr
            fl = fr
            tr = minT + (i*l)/n
            fr = fSafe(tr)
            if (sign(fl) != sign(fr)) {

                zeroin(tl, fl, tr, fr)?.let {
                    z.add(it)
                    if (info) printLnTmp(YELLOW, "Zeropoint: ${fix6(it)}")
                }
            }
        }

        z.toList()
    }

    private fun zeroin(t0: Double,f0: Double,t1: Double,f1: Double): Double? {
        var tz: Double? = null

        if (f0 == 0.0)
            tz = t0
        else if (f1 == 0.0)
            tz = t1
        else {
            var tl = t0
            var fl = f0
            var tr = t1
            var fr = f1
            var tm: Double
            var fm: Double

            var count = 0
            var loop = true
            while (loop) {
                tm = (tl + tr) / 2
                fm = f(tm)

                if (sign(fm) != sign(fl)) {
                    tr = tm
                    fr = fm
                } else if (sign(fm) != sign(fr)) {
                    tl = tm
                    fl = fm
                } else {
                    printLnWrn("Zero point expected but not found")
                    loop = false
                }

                if (loop && (tr - tl <= deltaT)) {
                    tz = (tl + tr) / 2
                    loop = false
                } else if (count++ > ITER_MAX) {
                    printLnErr("Maximum iteration count ($ITER_MAX) exceeded in search for zero-point")
                    loop = false
                }
            }
        }

        return tz
    }

    /**
     * @property extrema The extremal points of [f]. The list is sorted in ascending order by [Point].x. It may includes 
     * the endpoints 
     */
    val extrema: List<Point> by lazy {
        val lst = mutableListOf<Point>()
        lst.add(Point(minT,f(minT)))

        lst.addAll(Analytics(minT,maxT,n,deltaT,info=false,f=dff).zeropoints.map { t ->
            Point(t,f(t)).also { printLnTmp(YELLOW,"Extremal point: $it") }
        })

        lst.add(Point(maxT,f(maxT)))

        lst.toList()
    }

    /**
     * @property maxima The maximum points of [f]. The list is sorted in ascending order by [Point].x. It may include endpoints if
     * they are a local maximum
     */
    val maxima: List<Point> by lazy {
        val lst = mutableListOf<Point>()

        if (extrema[0].y > extrema[1].y)
            lst.add(extrema[0])

        for ( i in 2 until extrema.size) {
            if ((extrema[i-2].y < extrema[i-1].y) && (extrema[i-1].y > extrema[i].y))
                lst.add(extrema[i-1])
        }

        if (extrema[extrema.size-1].y > extrema[extrema.size-2].y)
            lst.add(extrema[extrema.size-1])

        lst.toList().also {
            if (info)
                printLnTmp(YELLOW,"maxima: $it")
        }
    }

    /**
     * @property maximumThe maximum point of [f]. It may be an endpoint.
     */
    val maximum: Point by lazy {
        extrema.maxBy {
            it.y
        }.also {
            if (info)
                printLnTmp(YELLOW,"maximum: $it")
        }
    }

    /**
     * @property minima The minimum points of [f]. The list is sorted in ascending order by [Point].x. It may include endpoints if
     * they are a local minimum
     */
    val minima: List<Point> by lazy {
        val lst = mutableListOf<Point>()

        if (extrema[0].y < extrema[1].y)
            lst.add(extrema[0])

        for ( i in 2 until extrema.size) {
            if ((extrema[i-2].y > extrema[i-1].y) && (extrema[i-1].y < extrema[i].y))
                lst.add(extrema[i-1])
        }

        if (extrema[extrema.size-1].y < extrema[extrema.size-2].y)
            lst.add(extrema[extrema.size-1])

        lst.toList().also {
            if (info)
                printLnTmp(YELLOW,"minima: $it")
        }
    }

    /**
     * @property minimum The minimum point of [f]. It may be an endpoint.
     */
    val minimum: Point by lazy {
        extrema.minBy {
            it.y
        }.also {
            if (info)
                printLnTmp(YELLOW,"minimum: $it")
        }
    }

    /**
     * If **y = f(t)**, then **t = inverse(y)**.
     * @return a list of the points where the line y=y0 crosses the graph of [f]
     */
    fun inverse(y0: Double) = Analytics(minT, maxT, n, deltaT, info = info, df = df) { t ->
        f(t) - y0
    }.zeropoints.map { x ->
        Point(x, y0)
    }

    override fun toString(): String {
        fun str(p: Point) = "(${fix2(p.x)}, ${fix2(p.y)})"

        val b = StringBuilder()
        b.append("${zeropoints.size} zero points: ")
        zeropoints.forEach { t ->
            b.append("${fix2(t)} ")
        }
        b.append("\n")

        b.append("${extrema.size} extremal points: ")
        extrema.forEach { p ->
            b.append("${str(p)} ")
        }
        b.append("\n")

        b.append("${minima.size} minimum points: ")
        minima.forEach { p ->
            b.append("${str(p)} ")
        }
        b.append("\n")

        b.append("${maxima.size} maximum points: ")
        maxima.forEach { p ->
            b.append("${str(p)} ")
        }
        b.append("\n")

        b.append("Minimum: ${str(minimum)}\n")
        b.append("Maximum: ${str(maximum)}\n")

        return b.toString()
    }
}
