package sundial.geometry

import GREEN
import ORANGE
import PI_HALF
import TWO_PI
import clamp2PI
import fixD
import measuredText
import printLnTmp
import sundial.DialParts
import sundial.PlaineSundial
import sundial.Sundial
import sundial.math.Vector
import sundial.numberset.AngleSet
import sundial.numberset.RealSet
import kotlin.math.*

/**
 * EoTGraph represents one half the Equation of Time as it is displayed on the dial. The EoT is split in two parts. The
 * first part for "spring" ecliptic longitudes between winter solstice and summer solstice [-PI/2,+PI/2]. The
 * second part for "autumn" ecliptic longitudes between summer solstice and winter solstice [+PI/2,+3*PI/2]
 * @param parts a toolbox of sundial parts
 * @param hourLine a Line along an hour
 * @param domain_ the domain of the preceding hour-line
 * @param isSpringGraph true if this graph should represent the spring EoT, else false
 * @param width the width of an area along the hour-line where the minute numerals are displayed
 */
class EoTGraph(val parts: DialParts, // A sundial toolbox
               val textSize: Double,
               val textRotation: Double,
               clipConic:(conic: Geometry) -> RealSet,
               val fmt:(minutes: Double) -> String = ::fmtMin,
               val eotN: Int? = null,
               val info: Boolean = false) {
    
    val springSteps: Map<Double, AngleSet> by lazy {
        if (info)
            printLnTmp(GREEN,"Computing spring EoT step function")

        eotN?.let {
            parts.getStepEoT(true,N=it,info=info)
        } ?: parts.getStepEoT(true,0.6*textSize,info=info)
    }

    val autumnSteps: Map<Double, AngleSet> by lazy {
        if (info)
            printLnTmp(ORANGE,"Computing autumn EoT step function")

        eotN?.let {
            parts.getStepEoT(false,N=it,info=info)
        } ?: parts.getStepEoT(false,0.6*textSize,info=info)
    }


    /**
     * A ordered list of intervals of ecliptic longitude and minute integers. This can be seen as the inverse
     * of the step function above
     */
    val springStepFn: List<Pair<AngleSet,Double>> by lazy {
        stepFn(springSteps)
    }

    val autumnStepFn: List<Pair<AngleSet,Double>> by lazy {
        stepFn(autumnSteps)
    }

    fun description(isSpring: Boolean): String {
        val season = if (isSpring) "spring" else "autumn"
        val span = if (isSpring) "[-90,+90]" else "[+90,+270]"
        val b = StringBuilder("Step function for $season solar longitudes $span\n${spc(3)}longitude${spc(5)}minutes\n")
        val stepFn = if (isSpring) springStepFn else autumnStepFn
        val idxMax = stepFn.size - 1

        stepFn.mapIndexed { idx, pair ->
            val a = pair.first.first()
            val s = fixD(if (a >= -PI_HALF) a else a + TWO_PI)
            b.append("${spc(3)}$s${spc(14-s.length)}${fmtSec(pair.second)}")
            if (idx < idxMax)
                b.append('\n')
        }

        return b.toString()
    }

    /**
     * A sorted list of intervals of ecliptic longitude and minute integers. This can be seen as the inverse
     * of the step function above
     */
    private fun stepFn(steps: Map<Double, AngleSet>): List<Pair<AngleSet, Double>> {

        val lst = mutableListOf<Pair<AngleSet, Double>>()

        steps.entries.forEach { (m, l) ->
            l.itvl().forEach { itv ->
                lst.add(Pair(AngleSet(itv.leftBrace.type, itv.leftValue(), itv.rightValue(), itv.rightBrace.type), m))
            }
        }

        lst.sortBy {
            val v = it.first.first()
            if (v < PI_HALF) v + TWO_PI else v
        }

        return lst.toList()
    }

    /**
     * Gets EoT in minutes for a <q>spring</q> solar ecliptic longitude between -PI/2 and +PI/2
     */
    fun getSpringMinutes(elng: Double) = springStepFn.find { p ->
        p.first.contains(elng)
    }?.second

    /**
     * Gets EoT in minutes for an <q>autumn</q> solar ecliptic longitude between +PI/2 and 3*PI/2
     */
    fun getAutumnMinutes(elng: Double) = autumnStepFn.find { p ->
        p.first.contains(elng)
    }?.second

    /**
     * The solar ecliptic longitudes of the discontinuities in the [springStepFn]
     */
    val springLongitudes: List<Double> by lazy {
        longitudes(springSteps,true)
    }

    /**
     * Ecliptic longitudes of the discontinuities in stepFn
     */
    fun longitudes(steps: Map<Double, AngleSet>,isSpring: Boolean): List<Double> {
        val s = mutableListOf<Double>()

        steps.values.forEach { a ->
            a.itvl().forEach { itv ->
                    if (isSpring) s.add(itv.leftValue()) else  s.add(clamp2PI(itv.leftValue()))
                    if (isSpring) s.add(itv.rightValue()) else s.add(clamp2PI(itv.rightValue()))
            }
        }

        // Sort longitudes in ascending order
        return s.sorted()
    }

    /**
     * A conic for each of the solar longitudes in [springLongitudes]
     */
    val springConics = mutableMapOf<String, Sundial.DialItem>().let { conicmap ->
        springLongitudes.forEach { lng ->
            val q = "${if (lng >= 0.0) '+' else '-'}${fixD(abs(lng))}"
            parts.getDeclinationConic(parts.decl(lng))?.let { conic ->
                val label = "${PlaineSundial.EOT_CONIC}$q"
                conic.label = label
                val domain = clipConic(conic)
                if (!domain.isEmptySet()) {
                    val itm = Sundial.DialItem(conic, domain, lng)
                    conicmap[label] = itm
                }
            }
        }

        conicmap.toMap()
    }

    suspend fun getNumbersWithPositions(isSpring: Boolean): Map<String,Sundial.DialItem> {
        val txts = mutableMapOf<String,Sundial.DialItem>()

        fun unique(key: String) = if (txts.containsKey(key)) {
            REGEX.find(key)?.let {
                val (pre,num) = it.destructured

                "$pre${num.toInt()+1}"
            } ?: "${key}_1"
        }
        else
            key

        fun sortedEndPoints(itm: Sundial.DialItem) =
            itm.geometry.toPoint(itm.domain.first())?.let { pf ->
                itm.geometry.toPoint(itm.domain.last())?.let { pl ->
                    DialParts.sortPointsAlong(Vector.I,pf,pl).let {
                        Pair(it[if (isSpring) 1 else 0],itm.pId)
                    }
                }
            }

        fun offset(pt0: Point,pt1: Point) = Point((pt0.x+pt1.x)/2,(pt0.y+pt1.y)/2).let { cog ->
            Vector(pt0,pt1).let { W ->
                Pair(cog,(if (isSpring) Vector(-W.delY,W.delX) else Vector(W.delY,-W.delX)).normalize())
            }
        }

        fun textPosAndSize(p0: Point?,p1: Point?) = if ((p0 != null)&& (p1 != null)) {
            val (P,V) = offset(p0, p1)
            val d = 0.7*Point.distance(p0,p1).let {
                if (it > 0.5*textSize) 0.5*textSize else it
            }

            Pair(P + V.times(0.4*textSize),d)
        }
        else
            null

        val conics = springConics.map { it.value }
//        val conics = (if (isSpring) springConics else autumnConics).map { it.value }

        conics.mapNotNull {
            sortedEndPoints(it)
        }.let { ends ->
            if (ends.isNotEmpty()) {
                var ends0 = ends[0]

                for (i in 1 until ends.size) {
                    ends[i].let { ends1 ->

                        textPosAndSize(ends0.first, ends1.first)?.let { (Ps, tsize) ->
                            val l = (ends[i - 1].second + ends[i].second) / 2
                            (if (isSpring) getSpringMinutes(l) else getAutumnMinutes(PI-l))?.let { m ->
                                val s = fmt(m)
                                val txt = Text(measuredText(s, tsize), textRotation, Ps)
                                val id = if (isSpring) PlaineSundial.SPRING_TEXT else PlaineSundial.AUTUMN_TEXT
                                txts[unique("${id}${s}")] = Sundial.DialItem(
                                    txt, txt.fullDomain, l
                                )
                            }
                        }

                        ends0 = ends1
                    }
                }
            }
        }

        return txts
    }

    // Mark points for debugging
    private fun addPt(pt: Point,color: Int) = points_.add(Pair(pt,color))

    private val points_ = mutableListOf<Pair<Point,Int>>()

    val points: List<Pair<Point,Int>> by lazy {
        points_.toList()
    }

    override fun toString(): String {
        fun spc(n: Int) = " ".repeat(n)
        fun fmt(m: Int) = "${(if (m > 0) '+' else if (m < 0) '-' else "")}${abs(m)}"
        val l = "longitude"

        val b = StringBuilder("EoT Steps\n")

        b.append("Step function for spring solar longitudes [-90,+90]\n${spc(3)}$l${spc(20-l.length)}minutes\n")
        springStepFn.map {
            val s = fixD(it.first.first())
            b.append("${spc(3)}$s${spc(20-s.length)}${fmtSec(it.second)}\n")
        }

        b.append("Step function for autumn solar longitudes [+90,+180]\n${spc(3)}$l${spc(20-l.length)}minutes\n")
        autumnStepFn.map {
            val s = fixD(it.first.first())
            b.append("${spc(3)}$s${spc(20-s.length)}${fmtSec(it.second)}\n")
        }

        return b.toString()
    }

    companion object {
        val REGEX =  """(\S*\d_)(\d+)$""".toRegex()

        const val NO_EOT = 0
        const val EOT_FULL_MINUTES = 1
        const val EOT_HALF_MINUTES = 2

        private fun spc(n: Int) = " ".repeat(n)

//        fun fmt(minutes: Double) = fmtHalf(minutes)

        fun fmtMin(minutes: Double): String {
            val m = minutes.roundToInt()
            val sgn = if (m == 0) "" else if (m > 0) "+" else "-"

            return "$sgn${abs(m)}"
        }

        fun fmtSec(m: Double) = if (m.isNaN()) "NaN" else "${(if (m > 0) '+' else if (m < 0) '-' else "")}${fmtSec_(abs(m))}"
        private fun fmtSec_(m: Double) = "${floor(m).toInt()}${((m%1)*60).roundToInt()}"

        fun fmtHalf(m: Double) = if (m.isNaN()) "NaN" else {
            val m_ = abs(m)
            val frac = m_ % 1.0
            var M_ = (m_ - frac).toInt()

            val S = if (frac < 0.25)
                ""
            else if (0.75 <= frac) {
                M_ += 1
                ""
            }
            else
                ""

            val sgn = if ((M_ == 0) && (S.isEmpty()))
                ""
            else if (m >= 0.0) '+' else '-'

            val M = if (M_ == 0) {
                if (S.isEmpty()) "0" else ""
            }
            else
                "$M_"

            "$sgn$M$S"
        }
    }
}