package sundial

import DEG
import DateTimeUTC
import DialData
import LT_BLUE
import MAGENTA
import RAD
import logC
import pixPerMm
import sundial.astronomy.EquationOfTime
import sundial.geometry.EoTGraph
import sundial.geometry.FitRectAround
import kotlin.math.E

abstract class Params(val debug: Boolean=false) {
    // Erroneous input with explanation. 'ins' is supposed to be inserted into 'msg' at the position of 
    class Error<P>(value: P,val msg: String, val ins: String?=null) : Input<P>(value)
    // Valid input value
    open class Input<P>(val value: P)

    // A function to display Error s
    abstract fun <P>message(err: Error<P>)

    fun getDialData() = name().let { name ->
        var ok = true

        year().let { y ->
            if (y is Error) {
                ok = false
                message(y)
            }

            width().let { w ->
                if (w is Error) {
                    ok = false
                    message(w)
                }

                height().let { h ->
                    if (h is Error) {
                        ok = false
                        message(h)
                    }

                    latitude().let { lat ->
                        if (lat is Error) {
                            ok = false
                            message(lat)
                        }

                        longitude().let { lng ->
                            if (lng is Error) {
                                ok = false
                                message(lng)
                            }

                            azimuth().let { az ->
                                if (az is Error) {
                                    ok = false
                                    message(az)
                                }

                                altitude().let { alt ->
                                    if (alt is Error) {
                                        ok = false
                                        message(alt)
                                    }

                                    clipHorizon().let { clipH ->
                                        if (clipH is Error) {
                                            ok = false
                                            message(clipH)
                                        }

                                        alignment().let { align ->
                                            if (align is Error) {
                                                ok = false
                                                message(align)
                                            }

                                            symbols().let { zooSym ->
                                                if (zooSym is Error) {
                                                    ok = false
                                                    message(zooSym)
                                                }

                                                hourSymbols().let { hSym ->
                                                    if (hSym is Error) {
                                                        ok = false
                                                        message(hSym)
                                                    }

                                                    hourSpan().let { hSpan ->
                                                        if (hSpan is Error) {
                                                            ok = false
                                                            message(hSpan)
                                                        }

                                                        tzMeridian().let { tzM ->
                                                            if (tzM is Error) {
                                                                ok = false
                                                                message(tzM)
                                                            }

                                                            minAltitudeAboveDial().let { minAlt ->
                                                                if (minAlt is Error) {
                                                                    ok = false
                                                                    message(minAlt)
                                                                }

                                                                elongStep().let { eStep ->
                                                                    if (eStep is Error) {
                                                                        ok = false
                                                                        message(eStep)
                                                                    }

                                                                    minutesStep().let { mStep ->
                                                                        if (mStep is Error) {
                                                                            ok = false
                                                                            message(mStep)
                                                                        }

                                                                        anniversary().let { anniv ->
                                                                            if (anniv is Error) {
                                                                                ok = false
                                                                                message(anniv)
                                                                            }

                                                                            showEoT().let { showEoT ->
                                                                                if (showEoT is Error) {
                                                                                    ok = false
                                                                                    message(showEoT)
                                                                                }

                                                                                eotFormat().let { eotFmt ->
                                                                                    if (eotFmt is Error) {
                                                                                        ok = false
                                                                                        message(eotFmt)
                                                                                    }

                                                                                    eotN().let { eotN ->
                                                                                        if (eotN is Error) {
                                                                                            ok = false
                                                                                            message(eotN)
                                                                                        }

                                                                                        if (ok)
                                                                                            DialData(
                                                                                                name.value,
                                                                                                y.value,
                                                                                                w.value,
                                                                                                h.value,
                                                                                                lat.value * DEG,
                                                                                                lng.value * DEG,
                                                                                                az.value * DEG,
                                                                                                alt.value * DEG,
                                                                                                showEoT.value,
                                                                                                eotFmt.value,
                                                                                                eotN.value,
                                                                                                eStep.value,
                                                                                                clipH.value,
                                                                                                align.value,
                                                                                                zooSym.value,
                                                                                                hSym.value,
                                                                                                hSpan.value,
                                                                                                tzM.value * DEG,
                                                                                                minAlt.value, // Degrees!
                                                                                                mStep.value,
                                                                                                eStep.value,
                                                                                                anniv.value?.let {
                                                                                                    DateTimeUTC(it)
                                                                                                },
                                                                                                pixPerMm
                                                                                            )
                                                                                        else
                                                                                            null
                                                                                    }
                                                                                }
                                                                            }
                                                                        }
                                                                    }
                                                                }
                                                            }
                                                        }
                                                    }
                                                }
                                            }
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
    }

    fun name() = trimmedInputValue(NAME)?.let { name ->
        Input(name)
    } ?: Input(null)

    fun latitude() =  trimmedInputValue(LATITUDE)?.let { s ->
        try {
            val t = s.replace("n", "", ignoreCase = true).replace('s', '-', ignoreCase = true)

            val q = t.toDouble()

            if ((-90.0 <= q) && (q < 90.0))
                Input(q)
            else
                Error(q,"Latitude  must be in range -90 to +90",s)
        } catch (ex: Exception) {
            Error(Double.NaN,"Latitude expected, found ", s.ifEmpty { "nothing" })
        }
    } ?: Error(Double.NaN,"No $LATITUDE")

    fun longitude() = trimmedInputValue(LONGITUDE)?.let { s ->
        try {
            val t = s.replace("e", "", ignoreCase = true).replace('w', '-', ignoreCase = true)

            val q = t.toDouble()

            if ((-180.0 <= q) && (q <= 180.0))
                Input(q)
            else
                Error(q,"Longitude  must be in range -180 to +180",s)
        } catch (ex: Exception) {
            Error(Double.NaN,"Longitude expected, found ", s.ifEmpty { "nothing" })
        }
    } ?: Error(Double.NaN,"No $LONGITUDE")

    fun azimuth() = trimmedInputValue(AZIMUTH)?.let { s ->
        try {
            val q = s.toDouble()

            if ((0.0 <= q) && (q < 360.0))
                Input(q)
            else
                Error(q,"Azimuth  must be in range 0 to 360",s)
        } catch (ex: Exception) {
            Error(Double.NaN,"Azimuth expected, found " , s.ifEmpty { "nothing" })
        }
    } ?: Error(Double.NaN,"No $AZIMUTH")

    fun altitude() = trimmedInputValue(ALTITUDE)?.let { s ->
        try {
            val q = s.toDouble()

            if ((-90.0 < q) && (q <= 90.0))
                Input(q)
            else
                Error(q,"Altitude  must be in range -90 to +90",s)
        } catch (ex: Exception) {
            Error(Double.NaN,"Altitude expected, found ", s.ifEmpty { "nothing" })
        }
    } ?: Error(Double.NaN,"No $ALTITUDE")


    fun hourSpan() = trimmedInputValue(HOUR_SPAN).let { s ->
        if (s.isNullOrEmpty())
            Input(null)
        else {
            if (isHMS24(s)) try {
                var h = 0.0
                s.split(":").forEachIndexed { i, t ->
                    when (i) {
                        0 -> h += t.toDouble()
                        1 -> h += t.toDouble() / 60
                        2 -> h += t.toDouble() / 3600
                    }
                }

                Input(h)
            } catch (e: Exception) {
                Error(null,"Hour span expected, found ", s.ifEmpty { "nothing" })
            }
            else {
                Error(null, "Hour span  not formatted as HH:MM where HH is 00 to 23", s)
            }
        }
    }


