    fun tzMeridian() = trimmedInputValue(TZ_MERIDIAN)?.let { select ->
        if (select == TZ_OFFSET)
            Input((tzOffsetHour().value as Double) * (180.0 / 12.0))
        else if ((select == TZ_LOCAL) || (select == TZ_MULT_15)) {
            val lng = longitude()

            if (!(lng is Error)) {
                val l = lng.value as Double

                if (select == TZ_LOCAL)
                    Input(l)
                else // TZ_MULT_15
                    Input(DialParts.defaultTimeZoneMeridian(l * DEG) * RAD)
            } else
                Error(Double.NaN,"Enter a valid longitude to compute the time zone meridian")

        } else
            Error(Double.NaN,"Could not obtain timezone median")
    } ?: Error(Double.NaN,"No $TZ_MERIDIAN")


    fun tzOffsetHour() = trimmedInputValue(TZ_OFFSET_HOUR).let { s ->
        if (s.isNullOrEmpty())
            Input(null)
        else {
            if (isHM12(s)) {
//                logC(MAGENTA,"isHM12")
                try {
                    var h = 0.0
                    s.split(":").forEachIndexed { i, t ->
                        when (i) {
                            0 -> h += t.toDouble()
                            1 -> h += t.toDouble() / 60
                        }
                    }

                    Input(h)
                } catch (e: Exception) {
                    Error(null,"Timezone offset expected, found ", s.ifEmpty { "nothing" })
                }
            }
            else {
//                logC(MAGENTA,"!isHM12")
                Error(null, "Timezone offset  not formatted as HH:MM where HH is 00 to 12", s)
            }
        }
    }

    fun minAltitudeAboveDial() = trimmedInputValue(MIN_ALTITUDE_ABOVE)?.let { s ->
        try {
            val q = s.toDouble()

            if ((0.0 < q) && (q < 90.0))
                Input(q)
            else
                Error(q,"Minimum altitude  not in range 0 to +90",s)
        } catch (ex: Exception) {
            Error(Double.NaN,"Minimum altitude above dial expected, found ", s.ifEmpty { "nothing" })
        }
    } ?: Error(Double.NaN,"No $MIN_ALTITUDE_ABOVE")

    fun width() = trimmedInputValue(WIDTH)?.let { s ->
        try {
            val q = s.toDouble()

            if ((100.0 < q) && (q < 5000.0))
                Input(q)
            else
                Error(q,"Width  not in the range 100mm to 5000mm",s)
        } catch (ex: Exception) {
            Error(Double.NaN,"Width expected, found ", s.ifEmpty { "nothing" })
        }
    } ?: Error(Double.NaN,"No $WIDTH")

    fun height() = trimmedInputValue(WIDTH)?.let { s ->
        try {
            val q = s.toDouble()

            if ((100.0 < q) && (q < 5000.0))
                Input(q)
            else
                Error(q,"Height $ not in the range 100mm to 5000mm",s)
        } catch (ex: Exception) {
            Error(Double.NaN,"Height expected, found ", s.ifEmpty { "nothing" })
        }
    } ?: Error(Double.NaN,"No $HEIGHT")

    fun year() = trimmedInputValue(YEAR)?.let { s ->
        try {
            val q = s.toInt()

            if ((2000 <= q) && (q <= 2500))
                Input(q)
            else
                Error(q,"Year  is not in the range 2000 to 2500",s)
        } catch (e: Exception) {
            Error(Int.MIN_VALUE, "Year expected, found ", s.ifEmpty { "nothing" })
        }
    } ?: Error(Int.MIN_VALUE, "No $YEAR")

    fun clipHorizon() = trimmedInputValue(CLIP_HORIZON)?.let { bool ->
        if (bool == "true")
            Input(true)
        else if (bool == "false")
            Input(false)
        else
            Error(false,"Clip horizon expected a boolean, found ",bool)
    } ?: Error(false,"No $CLIP_HORIZON")

    fun alignment() = trimmedInputValue(ALIGNMENT)?.let { align ->
        when (align) {
            ALIGN_HORIZON -> Input(FitRectAround.FIT_ALONG_DIRECTION)
            OPTIMIZE_AREA -> Input(FitRectAround.FIT_OMPTIMIZE_AREA)
            else -> Error(FitRectAround.FIT_ALONG_DIRECTION,"Could not obtain alignment ",align)
        }
    } ?: Error(FitRectAround.FIT_ALONG_DIRECTION,"No $ALIGNMENT")

    fun minutesStep() = trimmedInputValue(MINUTE_STEP)?.let { step ->
        when (step) {
            MINUTES_NEVER -> Input(null)
            MINUTES_1 -> Input(1)
            MINUTES_3 -> Input(3)
            MINUTES_5 -> Input(5)
            MINUTES_10 -> Input(10)
            MINUTES_15 -> Input(15)
            MINUTES_20 -> Input(20)
            MINUTES_30 -> Input(30)
            MINUTES_60 -> Input(60)
            else ->  Error(null,"Could not obtain minutes step $step")

        }
    } ?: Error(null,"No $MINUTE_STEP")

    fun elongStep() = trimmedInputValue(ELONG_STEP)?.let { step ->
        when (step) {
            ELONG_NEVER -> Input(null)
            ELONG_5 -> Input(5)
            ELONG_10 -> Input(10)
            ELONG_15 -> Input(15)
            ELONG_30 -> Input(30)
            else -> Error(null,"Could not obtain solar longitude step $step")
        }
    } ?: Error(null,"No $ELONG_STEP")

    fun showEoT() = trimmedInputValue(SHOW_EOT)?.let { bool ->
        if (bool == "true")
            Input(true)
        else if (bool == "false")
            Input(false)
        else
            Error(false,"$SHOW_EOT must be 'true' or 'false'")
    } ?: Error(false,"No $SHOW_EOT")

    fun eotFormat() = trimmedInputValue(EOT_FMT)?.let { format ->
        when (format) {
            EOT_NONE -> Input(EoTGraph.NO_EOT)
            EOT_FULL_MINUTES -> Input(EoTGraph.EOT_FULL_MINUTES)
            EOT_HALF_MINUTES -> Input(EoTGraph.EOT_HALF_MINUTES)
            else -> Error(EoTGraph.NO_EOT,"Could not obtain EoT format $format")
        }
    } ?: Error(EoTGraph.NO_EOT,"No $EOT_FMT")

    fun eotN() = trimmedInputValue(EOT_N).let { s ->
        if (s?.isEmpty() != false)
            Input(null)
        else
            try {
                val q = s.toInt()

                if ((5 <= q) && (q <= 20))
                    Input(q)
                else
                    Error(q,"EoT divisions  not in the range 5 to 20",s)
            } catch (ex: Exception) {
                Error(null,"EoT divisions expected, found ", s.ifEmpty { "nothing" })
            }
    } ?: Error(10,"No $EOT_N")

    fun symbols() = trimmedInputValue(ZOODIAC_SYMBOLS)?.let { symbols ->
        when (symbols) {
            SYMBOLS_NONE -> Input(PlaineSundial.NO_SYMBOLS)
            SYMBOLS_ZODIAC -> Input(PlaineSundial.ZOODIAC_SYMBOLS)
            SYMBOLS_ABBREV -> Input(PlaineSundial.ABBREVIATED_SYMBOLS)
            else -> Error(PlaineSundial.NO_SYMBOLS,"Could not obtain star-sign symbols $symbols")

        }
    } ?: Error(PlaineSundial.NO_SYMBOLS,"No $ZOODIAC_SYMBOLS")

    fun hourSymbols() = trimmedInputValue(HOUR_SYMBOLS)?.let { symbols ->
        when (symbols) {
            HOURS_NONE -> Input(PlaineSundial.NO_NUMERALS)
            HOURS_ARABIC -> Input(PlaineSundial.ARABIC_NUMERALS)
            HOURS_ROMAN -> Input(PlaineSundial.ROMAN_NUMERALS)
            else -> Error(PlaineSundial.NO_NUMERALS,"Could not obtain hour-symbols $symbols")
        }
    } ?: Error(PlaineSundial.NO_NUMERALS,"No $HOUR_SYMBOLS")

    fun anniversary() = trimmedInputValue(ANNIVERSARY)?.let { dtStr ->
        if (dtStr.isEmpty())
            Input(null)
        else
            Input("${dtStr}Z")
    } ?: Error(null,"No $ANNIVERSARY")

    private fun trimmedInputValue(inputId: String) = getInputValue(inputId)?.trim()

    abstract fun getInputValue(inputId: String): String?

