    companion object {
        const val NAME = "name"
        const val LATITUDE = "latitude"
        const val LONGITUDE = "longitude"
        const val AZIMUTH = "azimuth"
        const val ALTITUDE = "altitude"
        const val EAST = "east"
        const val SOUTH = "south"
        const val WEST = "west"
        const val NORTH = "north"
        const val ZENITH = "zenith"
        const val EQUATORIAL_NORTH = "enorth"
        const val EQUATORIAL_SOUTH = "esouth"
        const val HOUR_SPAN = "hour_span"
        const val TZ_MERIDIAN = "tz_meridian"
        const val TZ_LOCAL = "tz_local"
        const val TZ_MULT_15 = "tz_mult_15"
        const val TZ_OFFSET = "tz_offset"
        const val TZ_OFFSET_HOUR = "tz_offset_hour"
        const val MIN_ALTITUDE_ABOVE = "min_alt_above"
        const val YEAR = "year"
        const val CLIP_HORIZON = "clip_horizon"
        const val WIDTH = "width"
        const val HEIGHT = "height"
        const val A3FMT = "a3fmt"
        const val A4FMT = "a4fmt"
        const val ALIGNMENT = "alignment"
        const val ALIGN_HORIZON = "align_horizon"
        const val OPTIMIZE_AREA = "optimize_area"
        const val ZOODIAC_SYMBOLS = "zoodiac_symbols"
        const val SYMBOLS_NONE = "symbols_none"
        const val SYMBOLS_ZODIAC = "symbols_zodiac"
        const val SYMBOLS_ABBREV = "symbols_abbrev"
        const val HOUR_SYMBOLS = "hour_symbols"
        const val HOURS_NONE = "hours_none"
        const val HOURS_ARABIC = "hours_arabic"
        const val HOURS_ROMAN = "hours_roman"
        const val MINUTE_STEP = "minute_step"
        const val MINUTES_NEVER = "minutes_never"
        const val MINUTES_1 = "minutes_1"
        const val MINUTES_3 = "minutes_13"
        const val MINUTES_5 = "minutes_5"
        const val MINUTES_10 = "minutes_10"
        const val MINUTES_15 = "minutes_15"
        const val MINUTES_20 = "minutes_20"
        const val MINUTES_30 = "minutes_30"
        const val MINUTES_60 = "minutes_60"
        const val ELONG_STEP = "elong_step"
        const val ELONG_NEVER = "elong_never"
        const val ELONG_5 = "elong_5"
        const val ELONG_10 = "elong_10"
        const val ELONG_15 = "elong_15"
        const val ELONG_30 = "elong_30"
        const val SHOW_EOT = "show_eot"
        const val EOT_N = "eot_n"
        const val EOT_FMT = "eot_fmt"
        const val EOT_NONE = "eot_none"
        const val EOT_FULL_MINUTES = "eot_full_minutes"
        const val EOT_HALF_MINUTES = "eot_half_minutes"
        const val ANNIVERSARY = "anniversary"

        fun isHMS24(h: String) = HMS_24.matches(h)

        fun isHM12(h: String) = HM_12.matches(h)

        val HMS_24 = Regex("""(24(:00){0,2})|((2[0-3]|1\d|0?\d)(:[0-5]\d){0,2})""")

        val HM_12 = Regex("""^[+,\-]?(?:12?:(:00){0,2}|(?:1[0-1]|0?\d)(?::[0-5]\d)?)$""")
//        val HM_12 = Regex("""^[+,\-]?(?:(?:12?:(:00){0,2})|(?:(?:1[0-1]|0?\d)(?::[0-5]\d)?))$""")

        fun isSame(d1: DialData?,d2: DialData?) =
            if ((d1 == null) || (d2 == null))
                false
            else
                (d1.year == d2.year) &&
                        (d1.width == d2.width) &&
                        (d1.height == d2.height) &&
                        (d1.latitude == d2.latitude) &&
                        (d1.longitude == d2.longitude) &&
                        (d1.azimuth == d2.azimuth) &&
                        (d1.altitude == d2.altitude) &&
                        (d1.elongStep == d2.elongStep) &&
                        (d1.clipHorizon == d2.clipHorizon) &&
                        (d1.alignment == d2.alignment) &&
                        (d1.symbols == d2.symbols) &&
                        (d1.hourSymbols == d2.hourSymbols) &&
                        (d1.hourSpan == d2.hourSpan) &&
                        (d1.timezoneMeridian == d2.timezoneMeridian) &&
                        (d1.minAltAbove == d2.minAltAbove) &&
                        (d1.minutesStep == d2.minutesStep) &&
                        (d1.anniversary?.iUTC == d2.anniversary?.iUTC) &&
                        run {
                            if (d1.showEoT && d2.showEoT) {
                               ( d1.eotFmt == d2.eotFmt) && (d1.eotN == d2.eotN)
                            }
                            else
                                (d1.showEoT == d2.showEoT)
                        }
    }

