package sundial.math

import fixw
import sundial.geometry.Point
import kotlin.math.*

/**
 * Performs a transformation by first applying matrix M and then translate vector V
 */
class Transformation(val M: Matrix, val V: Vector) {

//    fun transform(P: Point) = M.mult(P-V) + V

    /**
     * Transforms a point
     */
    fun transform(P: Point) = M.mult(P) + V

    /**
     * Transformd a vector
     */
    fun transform(W: Vector) = M.mult(W) + V

    /**
     * Transforms a list of points
     */
    fun transform(pts: List<Point>) = pts.map {
        transform(it)
    }

    /**
     * The inverse transformation to this
     */
    val inv: Transformation? by lazy {
        inverse(this)
    }

    val theta: Double by lazy {
        M.mult(Vector.I).rotation
    }

    override fun toString() =  """
                ${fixw(M.m00,6,12)}  ${fixw(M.m01,6,12)}  x      ${fixw(V.delX,5,10)}
                                                +  |         |
                ${fixw(M.m10,6,12)}  ${fixw(M.m11,6,12)}  y      ${fixw(V.delY,5,10)}
            """.trimIndent()

    companion object {
        val O = Vector(0.0,0.0)

        /**
         * The identity transformation IDENTITY(X) = X
         */
        val IDENTITY = Transformation(Matrix.I,O)

        /**
         * A transformation that reflects an y-coordinate about a horizontal line
         */
        fun reflectAboutHorizontal(y: Double) = Transformation(Matrix.MIRROR_Y,Vector(0.0,2*y))

        /**
         * A transformation that mirrors a point about a center
         */
        fun mirrorPoint(center: Point) = rotation(PI,center)

        /**
         * A transform that rotates angle alpha about the origin
         */
        fun rotation(alpha: Double) = Transformation(Matrix.rotation(alpha),O)

        /**
         * A transform that rotates angle alpha about a center point
         */
        fun rotation(alpha: Double,center: Point): Transformation {
            // R(P) = M(P - V) + V = MP + V - M V
            val M = Matrix.rotation(alpha)
            val V = Vector(center.x,center.y)
            val MV = M.mult(V)

            return Transformation(M, V - MV)
        }

        fun translatinon(V: Vector) = Transformation(Matrix.I,V)

        fun scaling(fx: Double,fy: Double) = Transformation(Matrix(fx,0.0,0.0,fy),O)

        fun scaling(f: Double) = Transformation(Matrix(f,0.0,0.0,f),O)

        fun scaling(f: Double,center: Point): Transformation {
            val M = Matrix(f,0.0,0.0,f)
            val V = Vector(center.x,center.y)
            val MV = M.mult(V)

            return Transformation(M, V - MV)
        }

        fun inverse(t: Transformation) = t.M.inv?.let { Minv ->
            Transformation(Minv,-1.0*Minv.mult(t.V))
        }

        fun catenate(vararg t1: Transformation) = t1.asList().reduceRight { acc, t ->
            catenate(acc,t)
        }

        fun catenate(t1: Transformation, t2: Transformation): Transformation {
            // T(P) = (MM)P + MV + V
            val M = Matrix.mult(t1.M,t2.M)
            val V = Matrix.mult(t1.M,t2.V) + t1.V

            return Transformation(M,V)
        }
    }
}