package sundial.geometry

import TWO_PI
import sundial.math.Vector

class Star(P: Point,diameter: Double) : Polyline(makeStar(P,diameter)) {

    companion object {
        fun makeStar(P: Point,diameter: Double): List<Point> {
            val lst = mutableListOf<Point>()

            for (i in 0 .. 12) {
                val V = Vector(if (i%2 == 0) diameter/2 else diameter/6,0.0).rotate((TWO_PI *i)/12)

                lst.add(P+V)
            }
            return lst.toList()
        }
    }
}