package sundial

import GREEN
import MAGENTA
import PI_HALF
import TINY
import fix2
import fix6
import fixL
import logC
import printLn
import printLnTmp
import printLnWrn
import sundial.geometry.*
import sundial.math.Vector
import sundial.numberset.RealSet
import kotlin.math.*

abstract class Sundial(parts_: DialParts,
                       val width: Double,
                       val height: Double,
                       val info: Boolean = false,
                       val debug: Boolean = false) /*: Design()*/ {

    data class DialItem(val geometry: Geometry, var domain: RealSet, val pId: Double, var flags: Int=0)

    var parts: DialParts = parts_
        protected set

    var innerRect: Rectangle? = null
        protected set

    var outerRect: Rectangle? = null
        private set

//    var scale: Double? by Delegates.observable(null) { property, oldValue, newValue ->
//        printLn(MAGENTA,"scale = ${if (newValue == null) "null" else fix6(newValue)}")
//    }

    var scale: Double? = null
        private set

    var items = mutableMapOf<String,DialItem>()

    var convexHull: List<Point> = emptyList()
        private set

//    var textSize: Double by Delegates.observable(16.0) { property, oldValue, newValue ->
//        printLn(MAGENTA,"textSixe = ${fix6(newValue)}")
//    }

    var textSize: Double = 16.0
        private set

    // Mark points for debugging
    val points: MutableList<Pair<Point,Int>> by lazy {
        mutableListOf<Pair<Point,Int>>()
    }

    val arrows: MutableList<Pair<Arrow,Int>> by lazy {
        mutableListOf<Pair<Arrow,Int>>()
    }

    val polylines: MutableList<Pair<List<Point>,Int>> by lazy {
        mutableListOf<Pair<List<Point>,Int>>()
    }

    fun addArrow(C: Point,V: Vector,color: Int) {
        val arw = Arrow(C+V.times(-25.0),C+V.times(25.0))

        arrows.add(Pair(arw,color))
    }

    suspend fun make(fit: Int) {

        fun textRotation(r: Rectangle) = if (fit == FitRectAround.FIT_OMPTIMIZE_AREA)
            -(PI_HALF - parts.top.rotation + r.theta)
        else
            0.0

        // Create a model containing size determining Geometries
        createScaleModel().let { itms ->

            items = itms.toMutableMap()
            if (info)
                printLnTmp(GREEN,"${itms.size} items in scale model")
            // Determine an appropriate text size from the minimum enclosing rectangle
            measure(FitRectAround.MEASURE_ALONG_DIRECTION,itms)?.let { minAreaRect ->
                adaptTextSize(minAreaRect).let { textSize ->
                    this@Sundial.textSize = textSize

                    if (info)
                        printLnTmp(GREEN, "Text size adapted to model size: ${fixL(textSize)}")

                    // Measure the geometries in the model and determine the enclosing rectangle
                    measure(fit, itms)?.let { rect ->

                        if (info)
                            printLnTmp(GREEN, "Rectangle ${fixL(rect.w)}mm x ${fixL(rect.h)}mm enclosing model")

                        // Add the rest of the Geometries and measure the texts by measureText()
                        addMeasuredGeometries(textSize, textRotation(rect))
                        // convexHull is now enclosing all geometries
                        val conv = Polyline(convexHull.plus(convexHull[0]))
                        items[CONVEX_HULL] = DialItem(conv, conv.fullDomain, conv.length)

                        boundingRectAndScale(
                            convexHull,
                            width - BORDER_WIDTH,
                            height - BORDER_WIDTH,
                            fit
                        )?.let { (s, irect, _) ->
                            scale = s

                            innerRect = irect
                            val orect = Rectangle(
                                irect.w + BORDER_WIDTH,
                                irect.h + BORDER_WIDTH,
                                irect.theta,
                                irect.x0,
                                irect.y0
                            )
                            outerRect = orect

                            val txtRot = textRotation(irect)

                            items = items.mapValues { (_, itm) ->
                                scale(itm, s, txtRot)
                            }.toMutableMap()

                            // Inner and outer rectangle have already been scaled
                            items[INNER_RECT] = DialItem(irect, irect.fullDomain, 0.0)
                            items[OUTER_RECT] = DialItem(orect, orect.fullDomain, 0.0)

                            parts = DialParts(parts, s * parts.G)
                            printLnTmp(GREEN, "Gnomom scaled to ${fixL(parts.G)}mm")

                            this@Sundial.textSize *= s

                            addNotMeasuredExtras(this@Sundial.textSize, txtRot)
                        }
                    }
                }
            }
        }
    }

    // Create a scale-model
    abstract fun createScaleModel(): Map<String,DialItem>
    // Measure the scale model
    fun measure(fit: Int,itms: Map<String,DialItem>): Rectangle? {
        itms.values.forEach { itm ->
            itm.domain.itvl().forEach { itv ->
                convexHull = ConvexHull.convexHull(convexHull.plus(itm.geometry.points(itv)))
            }
        }

        return if (convexHull.size == 0) {
            if (info)
                printLnWrn("No items to measure")

            null
        }
        else
            boundingRectAndScale(convexHull,width,height,fit)?.rect?.also { rect ->
                convexHull.forEach { pt ->
                    if (rect.hit(pt) == Geometry.HIT_OUTSIDE) {

                    }
                }
            }
    }

    abstract suspend fun addMeasuredGeometries(textSize: Double, textRotation: Double)

    // Add texts and measure again
    // To be called from addMeasuredGeometries() with each text added
    fun measureText(text: Text) {
        convexHull = ConvexHull.convexHull(convexHull.plus(text.corners()))
    }

    // Scale up
    fun boundingRectAndScale(hull: List<Point>,w: Double, h: Double,fit: Int) = when (fit) {
        FitRectAround.FIT_OMPTIMIZE_AREA -> FitRectAround.scaleRotateToFit(hull,w,h,PI_HALF - parts.top.rotation)
        FitRectAround.FIT_ALONG_DIRECTION -> FitRectAround.fitAlongDirection(hull,w,h,parts.right)
        FitRectAround.MEASURE_ALONG_DIRECTION -> FitRectAround.measureAlongDirection(hull,parts.right)
        else -> null
    }

    abstract suspend fun addNotMeasuredExtras(textSize: Double,textRotation: Double)

    fun veryShortDescript() = parts.veryShortDescript()

    abstract fun printPartsInfo()

//    open suspend fun createSketch(): MutableMap<String, DialItem> {
//        return mutableMapOf<String, DialItem>()
//    }

    companion object {
        const val BORDER_WIDTH = 3.0 // mm

        const val INNER_RECT = "INNER_RECT"
        const val OUTER_RECT = "OUTER_RECT"
        const val CONVEX_HULL = "CONVEX_HULL"

        fun scale(s: Double,pts: List<Point>) = pts.map {
            Point(s*it.x,s*it.y)
        }

        fun scale(i: DialItem,f: Double,rot: Double) =
            if (i.geometry is Text) {

                DialItem(
                    Text(i.geometry.mt,rot,i.geometry.pos).scale(f),
                    i.geometry.fullDomain.scale(f),
                    i.pId,
                    i.flags
                )
            }
            else
                DialItem(i.geometry.scale(f),i.domain.scale(f),i.pId,i.flags)

        // Determines text-size and line thickness for the whole dial!
        fun adaptTextSize(rec: Rectangle): Double {
            return adaptTextSize(rec.w,rec.h)
        }

        fun adaptTextSize(width: Double,height: Double): Double {
            val p = min(width,height)
            val q = max(width,height)
            val f = p/q
            val squareness = (1.0 - abs(f - 1.0).pow(2.7)).pow(1/2.7)
            val textSize = 0.03*squareness*q
//            logC(MAGENTA,"W x H = ${fixL(width)}x${fixL(height)} Sides ratio:${fix6(f)} squareness:${fix2(squareness)} text size: ${fixL(textSize)}")

            return textSize
        }

        fun dropAnchorAt(
            rec: Rectangle,
            convexHull: List<Point>,
            dial: Sundial?=null): Pair<List<Point>,Point>? {

            fun area(pts: List<Point>): Double {
                var area = 0.0
                for (i in 2 until pts.size)
                    area += abs(pts[0].x*(pts[i-1].y - pts[i].y) +  pts[i-1].x*(pts[i].y - pts[0].y) +  pts[i].x*(pts[0].y - pts[i-1].y))

                return area/2
            }

            val cx = dial?.scale?.let { s ->
                scale(s,convexHull)
            } ?: convexHull

            var maxArea = Double.NEGATIVE_INFINITY
            var maxPts: List<Point>? = null

            for (i in cx.indices) {
                val pt0 = cx[i]
                val pt1 = (i + 1).let {
                    cx[if (it >= cx.size) it - cx.size else it]
                }
                val pt2 = (i + 2).let {
                    cx[if (it >= cx.size) it - cx.size else it]
                }
                val ln = Line(Line(pt0,pt1), fullDomain = RealSet.R)
                // A normal to ln, pointing to the inside of convexHull
                val n = Vector(pt1,pt2).componentNormalTo(ln.u).normalize()

                Clipper.intersectsRectLine(rec, ln).let { ipts ->
                    val pts = mutableListOf<Point>()
                    pts.addAll(ipts)

                    rec.corners().forEach { corner ->
                        val v = Vector(cx[i],corner)
                        if (v.norm() > TINY)
                            if (Vector.dot(v,n) < 0.0) {
                                pts.add(corner)
                            }
                    }

                    if (pts.size >= 3) {
                        pts.sortBy {
                            (rec.toParameter(it) ?: Double.NEGATIVE_INFINITY)
                        }

                        val area = area(pts)
                        if (area > maxArea) {
                            maxArea = area
                            maxPts = pts
                        }
                    }
                }
            }

            return maxPts?.let { lst ->
                Pair(lst,Point.cog(*lst.toTypedArray())!!)
            }
        }
    }
}