package sundial.geometry

import HUGE
import MAGENTA
import clampPI
import isTiny
import printLn
import sundial.geometry.Line.Companion.LEFT_SIDE
import sundial.math.Vector
import sundial.numberset.NumberSet
import sundial.numberset.NumberSet.Companion.CLOSED
import sundial.numberset.NumberSet.Companion.OPEN
import sundial.numberset.RealSet

class Wedge(ray0_: Line, ray1_: Line) : Geometry() {
    // A kludge to implement clipping by a pair of rays with coincident starting points
    override val type by lazy {
        val samePos = isTiny(ray1_.pos.x - ray0_.pos.x) && isTiny(ray1_.pos.y - ray0_.pos.y)
        val parallel = isTiny((ray0_.u - ray1_.u).norm())

        if (samePos) {
            if (parallel) COINCIDENT_LINES_WEDGE else REGULAR_WEDGE
        }
        else {
            if  (parallel)
                PARALLEL_LINES_WEDGE
            else
                NOT_A_WEDGE
        }
    }

    val ray0 = if (type == PARALLEL_LINES_WEDGE)
        // Shift the domain far out on the positive axis to make sure "all"
        // points have a positive parameter value
        Line(ray0_.pos-ray0_.u*(2*HUGE),ray0_.u, side = ray0_.side,
            fullDomain=RealSet(CLOSED,HUGE,Double.POSITIVE_INFINITY,OPEN))
    else
        ray0_

    val ray1 = if (type == PARALLEL_LINES_WEDGE)
        // Shift the domain far out on the negative axis to make sure "all"
        // points have a negative parameter value
        Line(ray1_.pos-ray0.u*(2*HUGE),ray0.u*(-1.0), side = ray0_.otherSide(),
            fullDomain=RealSet(OPEN,Double.NEGATIVE_INFINITY,-HUGE,CLOSED))
    else
    // Same as ray1_, only opposite direction with fullDomain R_MINUS
        Line(ray1_.pos,ray1_.u*(-1.0), side=ray0.side/*side = ray1_.otherSide()*/,fullDomain= RealSet.R_MINUS)

    val isAcuteAngle: Boolean by lazy {
        (Vector.dot(ray0.u,ray1.normal(0.0)!!) <= 0.0)//.also { printLn(MAGENTA,if (it) "ACUTE" else "OBTUSE")}
    }

    override fun position() = if ((type == REGULAR_WEDGE) || (type == COINCIDENT_LINES_WEDGE))
        ray0.pos
    else
        Point.cog(ray0.pos,ray1.pos)!!

    override val theta = (clampPI(ray0.theta) + clampPI(ray1_.theta))/2

    override val fullDomain = (ray0.fullDomain union ray1.fullDomain) as RealSet

    override val canonicalForm: Geometry by lazy {
        rotate(-theta)
    }

    override fun rotate(delTheta: Double) = Wedge(ray0.rotate(delTheta),ray1.rotate(delTheta))

    override fun translate(delX: Double, delY: Double) = Wedge(ray0.translate(delX,delY),ray1.translate(delX,delY))

    override fun scale(f: Double) = Wedge(ray0.scale(f),ray1.scale(f))

    override fun toPoint(t: Double) =
            if (t >= 0.0)
                ray0.toPoint(t)
            else
                ray1.toPoint(t)

    override fun toParameter(pt: Point) = ray0.toParameter(pt)?.let { u ->
        if (ray0.fullDomain.contains(u))
            u
        else
            null
    } ?: ray1.toParameter(pt)?.let { u ->
        if (ray1.fullDomain.contains(u))
            u
        else
            null
    }

    override fun tangent(t: Double) =
        if (t >= 0.0)
            ray0.tangent(t)
        else
            ray1.tangent(t)



    override fun normal(t: Double) =
        super.normal(t)?.let { n ->

            if (ray0.side == LEFT_SIDE) n else n * (-1.0)
        }


    override fun points(itv: NumberSet<Double>.Interval?, smoothed: Boolean) = sequence<Point> {
        if (itv != null) {
            toPoint(itv.leftValue())?.let { yield(it) }

            if (itv.contains(0.0) && (itv.leftValue() != 0.0) && (itv.rightValue() != 0.0))
                toPoint(0.0)?.let { yield(it) }

            toPoint(itv.rightValue())?.let { yield(it) }
        }
    }


    override fun construction(domain: RealSet?)= "Not yet implemented"

    override val axis = Line(pos, Vector.I.rotate(theta), fullDomain = if (type == REGULAR_WEDGE) RealSet.R_PLUS else RealSet.R)

    override fun hit(pt: Point): Int {
        val ok0 = ray0.toParameter(pt)?.let {
            ray0.fullDomain.contains(it)
        } ?: false

        val ok1 = ray1.toParameter(pt)?.let {
            ray1.fullDomain.contains(it)
        } ?: false

        return if (ok0 || ok1)
            HIT_ON
        else {
            val hit0 = (ray0.hit(pt) == ray0.otherSide())
            val hit1 = (ray1.hit(pt) == ray1.otherSide())

            if (isAcuteAngle) {
                if (hit0 && hit1) HIT_INSIDE else HIT_OUTSIDE
            }
            else {
                if (hit0 || hit1) HIT_INSIDE else HIT_OUTSIDE
            }
        }
    }

//    override fun hit(pt: Point): Int {
//        val ok0 = ray0.toParameter(pt)?.let {
//            ray0.fullDomain.contains(it)
//        } ?: false
//
