//                hit0
//            else
//                HIT_NOWHERE
//        }
//    }

    override fun toString() = "${typeStr[type]}\n${ray0}\n${ray1}"

    companion object {
        const val NOT_A_WEDGE = 0
        const val REGULAR_WEDGE = 0x1 shl 5
        const val PARALLEL_LINES_WEDGE = 0x2 shl 5
        const val COINCIDENT_LINES_WEDGE = 0x4 shl 5

        val typeStr = mapOf(NOT_A_WEDGE to "NOT_A_WEDGE", REGULAR_WEDGE to "REGULAR_WEDGE", PARALLEL_LINES_WEDGE to "PARALLEL_LINES_WEDGE",
            COINCIDENT_LINES_WEDGE to "COINCIDENT_LINES_WEDGE")
    }
}