package sundial.numberset

import fix
import printLnErr
import printLnWrn
import subnum

open class CyclicSet(val period: Double,interval: List<NumberSet<Double>.Interval>) : RealSet(interval) {

    constructor(cs: CyclicSet, leftType: Int, leftValue: Double, rightValue: Double, rightType: Int) :
            this(cs.period, cyclicInterval(cs,leftType,leftValue,rightValue,rightType))

    constructor(cs: CyclicSet) : this(cs.period,emptyList())

    constructor(period: Double) : this(period, emptyList())

    val halfP = period/2

    override val MIN_BRACE = Brace(LEFT, CLOSED,-halfP) // [ -period/2

    override val MAX_BRACE = Brace(RIGHT, OPEN,halfP) // period/2 >

    override fun contains(value: Double) = super.contains(clampHalf(value))

    override fun union(leftType: Int, leftValue: Double, rightValue: Double, rightType: Int): CyclicSet {
        val itvs = cyclicInterval(leftType, leftValue, rightValue, rightType)

        return if (itvs.isEmpty())
            this
        else {
            val cs = CyclicSet(period,itvs)

            union(cs) as CyclicSet
        }
    }

    override fun itvl(wrapAndJoin: Boolean): List<Interval> = if (!wrapAndJoin || (l.size < 2))
        l
    else {
        val first = l.first()
        val last = l.last()

        val firstL = Brace(LEFT,first.leftBrace.type,first.leftBrace.value) // TODO = first.leftBrace
        val lastR = Brace(RIGHT,last.rightBrace.type,last.rightBrace.value) // TODO = last.rightBrace

        if ((firstL.compareTo(MIN_BRACE) == 0) && (lastR.compareTo(MAX_BRACE) == 0)) {
            // If the first interval starts with MIN_BRACE and the last interval ends with MAX_BRACE
            val l3 = mutableListOf<Interval>()

            // Join the last interval in front of the first after shifting it one period
            l3.add(Interval(last.leftBrace.type,last.leftBrace.value - period,first.rightBrace.value,first.rightBrace.type))

            // TODO Possible to use filter instead?
            // Copy the rest of the intervals after that
            val iMax = l.size - 1
            l.forEachIndexed { i, itv ->
                if ((0 < i) && (i < iMax))
                    l3.add(itv)
            }

            l3.toList()
        }
        else
            l
    }

    override fun instance(intervals: List<Interval>) = CyclicSet(period,intervals)

    override val stridx = subnum(fix(period))

    fun cyclicInterval(itv: Interval) =
        cyclicInterval(this,itv.leftBrace.type,itv.leftBrace.value,itv.rightBrace.value,itv.rightBrace.type)

    fun cyclicInterval(leftType: Int, leftValue: Double, rightValue: Double, rightType: Int) = cyclicInterval(this,leftType,leftValue,rightValue,rightType)

    fun clampFull(x: Double): Double {
        val q = x % period

        return if (q < 0.0) q + period else q
    }

    fun clampHalf(x: Double): Double {
        val q = x % period

        return if (q < -halfP) q + period else if (q >= halfP) q - period else q
    }

    override fun splitKeepLeft(value: Double): NumberSet<Double> {
        val itvl = itvl(true)

        return if (canSplit(itvl)) {
            val itv = itvl.first()
            val ci = if (itv.contains(value))
                cyclicInterval(itv.leftBrace.type,itv.leftValue(),value, CLOSED)
            else if (itv.contains(value-period))
                cyclicInterval(itv.leftBrace.type,itv.leftValue(),value- period, CLOSED)
            else if (itv.rightValue() < value)
                cyclicInterval(itv.leftBrace.type,itv.leftValue(),itv.rightValue(),itv.rightBrace.type)
            else if (itv.rightValue() < value-period)
                cyclicInterval(itv.leftBrace.type,itv.leftValue(),itv.rightValue(),itv.rightBrace.type)
            else
                emptyList()

            instance(ci)
        }
        else
            this
    }

    override fun splitKeepRight(value: Double): NumberSet<Double> {
        val itvl = itvl(true)

        return if (canSplit(itvl)) {
            val itv = itvl.first()
            val ci = if (itv.contains(value))
                cyclicInterval(CLOSED,value,itv.rightValue(),itv.rightBrace.type)
            else if (itv.contains(value-period))
                cyclicInterval(CLOSED,value-period,itv.rightValue(),itv.rightBrace.type)
            else if (value < itv.leftValue())
                cyclicInterval(itv.leftBrace.type,itv.leftValue(),itv.rightValue(),itv.rightBrace.type)
            else if (value- period < itv.leftValue())
                cyclicInterval(itv.leftBrace.type,itv.leftValue(),itv.rightValue(),itv.rightBrace.type)
            else
                emptyList()

            instance(ci)
        }
        else
            this
    }

