    private fun canSplit(itvl: List<Interval>) = if (itvl.isEmpty())
        true
    else if (itvl.size > 1) {
        printLnErr("Splitting a non contiguous CyclicSet is not defined")
        false
    }
    else {
        // Splitting of universal sets not defined
        (!((itvl.first().leftBrace.compareTo(MIN_BRACE) == 0) && (itvl.first().rightBrace.compareTo(MAX_BRACE) == 0))).also {
            if (!it)
                printLnErr("Splitting a universal CyclicSet is not defined")
        }
    }

    companion object {
        fun cyclicInterval(cs: CyclicSet, leftType: Int, leftValue: Double, rightValue: Double, rightType: Int): List<NumberSet<Double>.Interval> {
            val lv = cs.clampHalf(leftValue)
            var rv = cs.clampHalf(rightValue)
//            println("lv = ${cs.str(lv)} rv = ${cs.str(rv)}")

            if (lv > rv) rv += cs.period

            return if (rv > cs.halfP) {
                listOf(
                    cs.Interval(cs.MIN_BRACE,cs.Brace(RIGHT,rightType,rv - cs.period)),
                    cs.Interval(cs.Brace(LEFT,leftType,lv),cs.MAX_BRACE)
                )
            }
            else
                listOf(cs.Interval(cs.Brace(LEFT,leftType,lv),cs.Brace(RIGHT,rightType,rv)))
        }


        fun cyclicInterval2(cs: CyclicSet, leftType: Int, leftValue: Double, rightValue: Double, rightType: Int): List<NumberSet<Double>.Interval> {
            val lv = cs.clampHalf(leftValue)
            val rv = cs.clampHalf(rightValue)

            return if (lv > rv) {
                printLnWrn("CyclicSet${subnum(cs.str(cs.period))} interval with left value:${cs.str(lv)} > right value:${cs.str(rv)} was interpreted as the complement interval")

                listOf(
                    cs.Interval(cs.MIN_BRACE,cs.Brace(RIGHT,rightType,rv)),
                    cs.Interval(cs.Brace(LEFT,leftType,lv),cs.MAX_BRACE)
                )
            }
            else
                listOf(cs.Interval(cs.Brace(LEFT,leftType,lv),cs.Brace(RIGHT,rightType,rv)))
        }

    }
}