package sundial.geometry

import IDS
import printLnWrn
import sundial.math.Transformation
import sundial.math.Vector
import sundial.numberset.NumberSet
import sundial.numberset.RealSet

abstract class Geometry {
    abstract val type: Int

    open fun typeName() = "UNKNOWN TYPE"

    val pos: Point by lazy {
        position()
    }

    abstract fun position(): Point

    abstract val theta: Double

    val x0: Double by lazy {
        pos.x
    }

    val y0: Double by lazy {
        pos.y
    }

    abstract val fullDomain: RealSet

    open var domain: RealSet? = null
        set(value) {
            if (field != null) {
                printLnWrn("A domain was already set for Geometry ${label ?: "UNKNOWN"}")
            }

            field = value
        }

    abstract val canonicalForm: Geometry

    var label: String? = null
        get() = if (field != null) field else "${this::class.simpleName}${IDS.next()}"



    abstract fun rotate(delTheta: Double): Geometry

    abstract fun translate(delX: Double,delY: Double): Geometry

    abstract fun scale(f: Double): Geometry

    abstract fun toPoint(t: Double): Point?

    abstract fun toParameter(pt: Point): Double?

    // Tangent to the Geometry (curve), pointing in the direction of increasing internal
    // parameter
    abstract fun tangent(t: Double): Vector?

    open fun construction(domain: RealSet?=null) = construction(domain,null,null)

    open fun construction(domain: RealSet?,tr: Transformation?,note: String?) = if (note != null) "${note}\n" else ""

//    abstract fun construction(domain: RealSet? = null): String

    // TODO Using an extended class in the base class!?
    abstract val axis: Line

    // outside is just a flag utilized by the clipping algorithms. It is not used internally in the Geometry
    var outside: Boolean = false

    fun flip(): Boolean {
        outside = !outside

        return outside
    }

    // Normal to the Geometry (curve), pointing to the outside of closed Geometries.
    // The normal is at a right angle to the tangent, pointing LEFT
    open fun normal(t: Double): Vector? = tangent(t)?.let {
        // Tangent rotated 90 by the right hand rule
        Vector(-it.delY,it.delX)
    }

    fun points(smoothed: Boolean = false) = sequence<Point> {
        domain?.itvl()?.forEach { itv ->
            points(itv,smoothed).forEach { pt ->
                yield(pt)
            }
        }
    }

    // TODO Why not abstract?
    open fun points(itv: NumberSet<Double>.Interval?, smoothed: Boolean = false) = sequence<Point> {}

    // Will determine where the Point lies in relation to this Geometry
    abstract fun hit(pt: Point): Int

    // An Arrow to display the tangent at parametric point t
    fun T(t: Double,length: Double=50.0): Arrow? {
        return if (fullDomain.contains(t)) {
            toPoint(t)?.let { P ->
                tangent(t)?.let { T ->
                    Arrow(P,T.times(length)).also {
                        it.domain = it.fullDomain
                    }
                }
            }
        }
        else
            null
    }

    // An Arrow to display the normal at parametric point t
    fun N(t: Double,length: Double=50.0): Arrow? {
        return if (fullDomain.contains(t)) {
            toPoint(t)?.let { P ->
                normal(t)?.let { N ->
                    Arrow(P,N.times(length)).also {
                        it.domain = it.fullDomain
                    }
                }
            }
        }
        else
            null
    }

    // TODO abstract fun clipWith(clipper: Geometry)

    companion object {
        const val HIT_ON = 0
        const val HIT_RIGHT = -1
        const val HIT_LEFT = 1
        const val HIT_OUTSIDE = -2
        const val HIT_INSIDE = 2
        const val HIT_NOWHERE = 3

        fun hit(c: Int) = when (c) {
            HIT_ON -> "HIT_ON"
            HIT_RIGHT -> "HIT_RIGHT"
            HIT_LEFT -> "HIT_LEFT"
            HIT_OUTSIDE -> "HIT_OUTSIDE"
            HIT_INSIDE -> "HIT_INSIDE"
            else -> "HIT_NOWHERE"
        }

    }
}