/**
 * A toolbox and parts for making a sundial. The "gnomon" is a stick, normal to the dial, that throws a shadow
 * on the dial.
 * @property eot astronoical parameters and the equation of time
 * @property geolat geographic latitude of the dial. Radians
 * @property geolng geographic longitude of the dial. Radians
 * @property azimuth azimuth angle of the gnomon, measured from north and eastward . Radians
 * @property altitude altitude angle og the gnomon. Radians
 * @property G the length of the gnomon in millimeters
 * @property timeZoneMeridian longitude of the timezone meridian. Default value is [geolng]. Radians
 * @property centralHour middle hour of all hours displayed. Hours.
 * @property hourSpan span of all hours displayed or null for full span of hours. Hours.
 * @property info Set to true to print information during run
 */
class DialParts(
    val eot: EquationOfTime,
    val geolat: Double,
    val geolng: Double,
    val azimuth: Double,
    val altitude: Double,
    val minAltitude: Double = 10.0,
    val G: Double = 100.0,
    val timeZoneMeridian: Double = geolng, // Solar local time is default
//    val daylightSavingTime: Boolean = false, // TODO Deprecate
    val centralHour: Double? = null,
    val hourSpan: Double? = null,
    val info: Boolean = false
) {

    // Constructor used to scale G
    internal constructor(parts: DialParts, G: Double) :  this(
        parts.eot,
        parts.geolat,parts.geolng,
        parts.azimuth,parts.altitude,
        minAltitude = parts.minAltitude,
        G=G,
        timeZoneMeridian = parts.timeZoneMeridian,
//        daylightSavingTime=parts.daylightSavingTime,
        centralHour = parts.centralHour,
        hourSpan = parts.hourSpan
    )

    /**
     * Obliquity / inclination of the ecliptic
     */
    val epsilon = eot.epsilon

    /**
     * Unit vector world axis, pointing from the earth's center and toward the North Pole
     */
    val Wa = Vector3.K

    /**
     * World coordinate system:
     *      x-axis through the center of the earth towards N0 E0,
     *      y-axis through the center of the earth towards N0 E90,
     *      z-axis through the center of the earth towards N90.
     * [E] is a unit vector pointing towards the east point of the horizon at ([geolat],[geolng]).
     * [E],[N],[Z] are base vectors in a horizontal coordinate system.
     * World coordinates
     */
    val E = Vector3(-sin(geolng), cos(geolng), 0.0)

    /**
     * [N] is a unit vector pointing towards the north point of the horizon at ([geolat],[geolng]).
     * [E],[N],[Z] are base vectors in a horizontal coordinate system.
     * World coordinates
     */
    val N = Vector3(-sin(geolat) * cos(geolng), -sin(geolat) * sin(geolng), cos(geolat))

    /**
     * [Z] is a unit vector, normal to the horizon plane at ([geolat], [geolng]), pointing towards local zenith.
     * [E],[N],[Z] are base vectors in a horizontal coordinate system.
     * World coordinates.
     */
    val Z = Vector3(cos(geolat)*cos(geolng),cos(geolat)*sin(geolng), sin(geolat))

    /**
     * Unit vector in the direction of the gnomon at ([geolat], [geolng]).
     * World coordinates
     */
    val g: Vector3 = (cos(altitude) * sin(azimuth) * E + cos(altitude) * cos(azimuth) * N + sin(altitude) * Z).normalize()

    private var color = GOLD
    private fun color(): Int {
        color = if (color == GOLD) YELLOW else GOLD

        return color
    }

    /**
     * The dial is horizontal if Z and g are parallel or anti-parallel
     */
    val isHorizontal = isTiny((Z.componentNormalTo(g)).norm()).also { // When |altitude| = 90

        if (info) {
            printLn(color(),"Location latitude: ${fixLat(geolat)} longitude: ${fixLng(geolng)}")

            printLn(color(),"Orientation azimuth: ${fixD(azimuth)} altitude: ${fixD(altitude)} ${if (it) "(horizontal)" else ""}")
        }
    }

    /**
     * The dial is equatorial if Wa and g are parallel or anti-parallel
     */
    val isEquatorial = isTiny(g.componentNormalTo(Wa).norm()).also {
        if (info && it)
            printLn(color(),"The dial is equatorial")
    }

    val isSouthernHemisphere = (geolat < 0.0).also {
        if (info)
            printLn(color(),"The dial is located in the ${if (it) "southern" else "northern"} hemisphere")
    }

    /**
     * The hour lines are parallel if g is normal to Wa
     */
    val isParalellHourLines = isTiny(Wa dot g).also {
        if (info && it)
            printLn(color(),"The dial has parallel hour-lines")
    }

    val flatLat = if (isHorizontal) geolat else asin(g.z) // Flat means horizontal

    val flatLng = if (isHorizontal) geolng else atan2(g.y,g.x).also {
        if (info)
            printLn(color(),"This dial is parallel to a horizontal dial located at ${fixLat(flatLat)} ${fixLng(it)}")
    }

    /**
     * [Northwards] is a unit vector pointing north in the dial plane. The component of Wa normal to g normalized.
     */
    val Northwards = if (isEquatorial)
        Z.componentNormalTo(g).let { n ->
            if (isTiny(n.norm()))
                null // g is vertical
            else
                ((if (geolat >= 0.0) -1.0 else 1.0)* n).normalize()
        }
    else
        Wa.componentNormalTo(g).normalize() // In plane of the dial

    /**
     * [Eastwards] is a unit vector pointing east in the dial plane ([Northwards] x [g]).
     */
    val Eastwards = if (Northwards == null)
            null
    else
        (Northwards cross g).normalize() // In plane of the dial

    /**
     * [Upwards] is a unit vector pointing up in the dial plane. The component of [Z] normal to [g] normalized.
     */
    val Upwards = if (isHorizontal)
        null
    else
        (Z.componentNormalTo(g)).normalize() // In plane of the dial

    /**
     * Top and Right are unit base vectors for the plane of the dial
     */
//    private val Top = Upwards ?: Northwards ?: Vector3.J // In plane of the dial
    private val Top = Northwards ?: Upwards ?: Vector3.J // In plane of the dial

    /**
     * Top and Right are base vectors for the plane of the dial
     */
    private val Right = (Top cross g).normalize() // In plane of the dial

    /**
     * WaS and WaW are base vectors for the equator plane which is normal to world axis [Wa].
     * WaS points towards local south and WaW points west
     */
    val WaS = Z.componentNormalTo(Wa).let { n ->
        // In the equator plane
        if (isTiny(n.norm()))
            -1.0*N // At the poles
        else
            n.normalize()
    }

    /**
     * WaS and Waw are base vectors for the equator plane (normal to world axis Wa).
     * WaS points towards local south and Waw points west
     */
    val WaW = (WaS cross Wa).normalize() // West. In the equator plane

    /**
     * Angle between world axis [Wa] and gnomon [g]. Radians
     */
    val omega: Double = acos(g.z).also {
        if (info)
            printLn(color(),"Angle between world axis and gnomon: ${fixD(it)}")
    }

    /**
     * Angle between horizon normal [Z] and gnomon [g]
     */
    val beta: Double = if (isHorizontal) 0.0 else acos(Z dot g).also {
        if (info && !isHorizontal)
            printLn(color(),"Angle between horizon normal and gnomon: ${fixD(it)}")
    }

    /**
     * Unit Vector in dial coordinates. The conversion of the cross product [Z] x [g] to dial coordinates.
     * If the dial is horizontal, this Vector is null.
     */
    val horizontal = if (isHorizontal)
        null // Horizon plane does not intersect dial plane
    else
        (Z cross g).let { v ->
            toDialVector(v).normalize()
//            Vector(v dot Right,v dot Top).normalize()
        }



    /**
     * Unit Vector? in dial coordinates. The component of [Z] in the plane of the dial.
     * If the dial is horizontal, this Vector is null.
     * If the dial is tilted to the horizon, up points upwards in the plane of the dial
     */
    val up = Upwards?.let {
        toDialVector(it).normalize()
    }


    /**
     * Unit Vector? in dial coordinates. Trace-vector between the dial plane (with normal [g]) and
     * the [N] - [Z] -plane (with normal [E]).
     * If [g] if parallel or antiparallel  to [E], this Vector is null, else it points northward in the plane of the dial
     */
    val north = Northwards?.let {
        toDialVector(it).normalize()
    }

    /**
     * Unit Vector? in dial coordinates. Trace-vector between the dial plane (with normal [g]) and
     * the [Z] - [E] -plane (with normal [N]).
     * If [g] if parallel or antiparallel  to [N], this Vector is null, else it points eastward in the plane of the dial
     */
    val east = Eastwards?.let {
        toDialVector(it).normalize()
    }

    /**
     * A unit vector pointing towards the top of the page that the dial is rendered on.
     * Its direction is the same as the up direction for the text on the dial.
     */
    val top = (Upwards ?: Northwards ?: Vector3.J).let {
        toDialVector(it).normalize()
    }
//    val top = (Upwards ?: Northwards ?: Vector3.J).let {
//        toDialVector(it).normalize()
//    }

    /**
     * A unit vector pointing towards the left of the page that the dial is rendered on.
     */
    val right = top.rotate(-PI_HALF)

    /**
     * Rotate -my for [horizontal] vector to become horizontal
     */
    val my = horizontal?.rotation ?: 0.0

    val radiant = run {

        if (isParalellHourLines)
            Point(0.0,if (geolat < 0.0) Double.POSITIVE_INFINITY else Double.NEGATIVE_INFINITY)
        else {
            val wg = Wa dot g
            val r = G*g - (G/wg)*Wa

            toDialPoint(r)
//            Point(r dot Right,r dot Top)
        }
    }.also {
        if (info)
            printLn(color(),"Common point of all hour lines (radiant): $it")
    }

    /**
     * Length of the base of the style, i.e. the distance between the [radiant] and  the origin.
     */
    val F = if (radiant.y.isInfinite())
        Double.POSITIVE_INFINITY
    else
        Point.distance(Point.ORIGO,radiant)

    /**
     * Trace-line in the dial-plane of a plane through the tip of the gnomon [G]*[g], parallel to the horizon
     * There is no such Line if the gnomon is normal to the horizon
     */
    val horizonTrace = run {
        // Z is normal to the local horizon plane and thus defines it
        // g is normal to the plane of the dial and thus defines it
        val n = Z.componentNormalTo(g)

        if (isTiny(n.norm()))
            null // Z and g is (anti)parallel. Dial is horizontal and have no horizon trace
        else {
            val beta = acos(Z dot g)
            val up = n.normalize()

            val P = up*(G/tan(beta))
            val u = (Z cross g).normalize()
            val v = if ((u cross up) dot g >= 0.0) u else -0.1*u

            Line(toDialPoint(P),toDialVector(v), side = Line.LEFT_SIDE, fullDomain = RealSet.R)
        }
    }

    fun toDialVector(v3: Vector3) = Vector(v3 dot Right,v3 dot Top)

    fun toDialPoint(v3: Vector3) = Point(v3 dot Right,v3 dot Top)

    /**
     * Sun's declination as a function of the sun's ecliptic longitude. Angles in radians.
     */
    fun decl(eclLong: Double) = asin(eot.sEps*sin(eclLong))

    /**
     * Sun's ecliptic longitude as a function of the sun's declination. Range in -PI/2 to +PI/2.
     * There are actually two longitudes to each declination. The second longitude is
     *          PI - eclLng(decl)
     */
    fun eclLng(decl: Double): Double {
        val x = sin(decl)/eot.sEps

        return if (x > 1.0)
            PI_HALF
        else if (x < -1.0)
            -PI_HALF
        else
            asin(x) // [-PI_HALF,PI_HALF>
    }

    /**
     * Declination of the sun at the start and end of the period of midnight sun.
     * This will have a value if |[geolat]| >= 90-[epsilon], else it will be null
     */
    val declMidnightSun = (if (geolat >= 0) {
        val v = PI_HALF - geolat
        if (v < epsilon) v else null
    }
    else {
        val v = -PI_HALF - geolat
        if (v > -epsilon) v else null
    }).also { decl ->
        if (info)
            decl?.let {
                printLn(color(),"Declination for the first/last midnight sun: ${fixD(it)}")
            }
    }


    private val declHorizonMin = max(-epsilon,geolat - PI_HALF)
    private val declHorizonMax = min(epsilon,geolat + PI_HALF)

//    /**
//     * A point in the middle of the spread. An alternative "origin" for better layout.
//     */
//    val C = run {
//        val y0 = G*tan(flatLat + declHorizonMin)
//        val y1 = G*tan(flatLat + declHorizonMax)
//
//        Point(0.0, (y0 + y1) / 2)
//    }

    /**
     * The gnomon foot is a line running from the origin to the [radiant]
     */
    fun getGnomonFoot(size: Double = 5.0) = if (isTiny(F))
        // The radiant coincides with origin
            Line(Point(0.0,-size, ), Point(0.0,size)) // A short vertical line through the origin
        else {
            if (radiant.y.isInfinite())
                Line(Point(radiant.x,0.0,),Vector(0.0,if (radiant.y > 0) 1.0 else -1.0), fullDomain = RealSet.R)
            else
                Line(Point.ORIGO, radiant)
        }

    /**
     * Declinations for which the sun comes above the horizon
     */
    val declinationsAboveHorizon =
        // |latitude - declination| <= PI/2   Condition for the sun to come above horizon at least once during the year
        declinationsAbove(geolat)

    /**
     * Declinations for which the sun comes above the plane of the dial
     */
    val declinationsAboveDial = declinationsAbove(flatLat)

    /**
     * Declinations for which the sun shines on the plane of the dial
     */
    val declinations = (if (isHorizontal)
        declinationsAboveHorizon
    else
        (declinationsAboveDial intersection declinationsAboveHorizon) as AngleSet).also {
        if (info)
            printLn(color(),"Solar declinations for which the Sun comes above the horizon and in front of the dial: $it")
    }

    /**
     * Declinations for which the sun comes above the horizon
     * @param latitude of the dial
     * @return set of declinations
     */
    fun declinationsAbove(lat: Double): AngleSet {
        val declMin = max(-epsilon,lat - PI_HALF)
        val declMax = min(epsilon,lat + PI_HALF)

        return AngleSet(CLOSED,declMin,declMax,CLOSED)
    }

    /**
     * Ecliptic longitudes for which the sun comes above the horizon
     * @param isSpring true for spring longitudes, false for autumn longitudes
     * @return set of longitudes
     */
    fun longitudesAboveHorizon(isSpring: Boolean) = declinationsAboveHorizon.let { d ->
        if (d.isEmptySet())
            AngleSet.ANGLE_SET
        else {
            val l1 = eclLng((d).first())
            val l2 = eclLng(d.last())

            if (isSpring) AngleSet(CLOSED,l1,l2, CLOSED) else AngleSet(CLOSED,PI-l2,PI-l1, CLOSED)
        }
    }

    /**
     * Ecliptic longitudes for which the sun comes above the dial
     * @param isSpring true for spring longitudes, false for autumn longitudes
     */
    fun longitudesAboveDial(isSpring: Boolean) = declinations.let { d ->
        if (d.isEmptySet())
            AngleSet.ANGLE_SET
        else {
            val l1 = eclLng((d).first())
            val l2 = eclLng(d.last())

            if (isSpring) AngleSet(CLOSED,l1,l2, CLOSED) else AngleSet(CLOSED,PI-l2,PI-l1, CLOSED)
        }
    }

    /**
     * Day-line for the tropic of cancer
     */
    val tropicCancer = if (flatLat <= -PI_HALF + epsilon)
    // No tropicCancer for the Antarctic
        null
    else
        getDeclinationConic(epsilon)

    /**
     * Day-line for the tropic of capricorn
     */
    val tropicCapricorn = if (flatLat >= PI_HALF - epsilon)
    // No tropicCapricorn for the Arctic
        null
    else
        getDeclinationConic(-epsilon)

    /**
     *  The winter tropic represents the day-line where the sun reaches the winter solstice at the latitude
     *  of the dial
     */
    val winterTropic = if (geolat >= 0) tropicCapricorn else tropicCancer

    /**
     * The declination corresponding to the [winterTropic]
     */
    val winterTropicDeclination = if (geolat >= 0) {
        // Tropic of capricorn
        -epsilon
    } else {
        // Tropic of cancer
        epsilon
    }

    /**
     *  The summer tropic represents the day-line where the sun reaches the summer solstice at the latitude
     *  of the dial
     */
    val summerTropic = if (geolat >= 0) tropicCancer else tropicCapricorn

    /**
     * The declination corresponding to the [summerTropic]
     */
    val summerTropicDeclination = if (geolat >= 0) {
        // Tropic of cancer
        epsilon
    } else {
        // Tropic of capricorn
        -epsilon
    }

    /**
     * zoneOffset = [timeZoneMeridian] - [geolng], converted to hours
     */
    val zoneHourOffset = ((timeZoneMeridian - geolng)/ HOURS).also {
        if (info && !isHorizontal) {
            printLn(color(), "Longitude of timezone meridian: ${fixLng(timeZoneMeridian)} (${sHMS(timeZoneMeridian)})")
            printLn(color(), "Offset from geographic longitude to timezone meridian: ${fixD(it*HOURS)} (${sHMS(it*HOURS)})")
        }
    }

    /**
     * The zone hour is the hour of the entire timezone, not corrected for the Equation of Time.
     * [hourAngle] is the local hour angle of the sun or the gnomon shadow. Hours
     */
    fun getZoneHourByHA(hourAngle: Double) = clamp24(getZoneHourByLH(getLocalHourByHA(hourAngle)))

    /**
     * Gets the local hour angle of the sun from the zone hour. Radians
     */
    fun getHourAngleByZH(zoneHour: Double) = (zoneHour - zoneHourOffset)* HOURS - PI

    fun getLocalHourByHA(hourAngle: Double)  = (hourAngle - PI)/ HOURS

    fun getZoneHourByLH(localHour: Double) = localHour + zoneHourOffset

    /**
     * Hour angles for which the summer tropic is 0 degrees or more above the horizon
     */
    val hourAnglesAboveHorizon = (hourAnglesAbovePlane(Z,0*DEG)?: AngleSet.UNIVERSAL_SET).also {
        if (info) {
            printLn(color(), "Hour angles for which the Sun is above horizon at summer solstice: ${haAndTime(it)}")
        }
    }

    /**
     * Hour angles for which the summer tropic is [minAltitude] degrees or more above the plane of the dial
     */
    val hourAnglesAboveDial = (hourAnglesAbovePlane(g,minAltitude*DEG) ?: AngleSet.UNIVERSAL_SET).also {
        if (info && !isHorizontal) {
            printLn(color(), "Hour angles for which the Sun comes at least ${fixD(minAltitude*DEG)} above the plane of the dial at summer solstice: ${haAndTime(it)}")
        }
    }

    /**
     * The intersection of [hourAnglesAboveHorizon] abd [hourAnglesAboveDial]
     */
    val hourAnglesVisible = ((hourAnglesAboveHorizon intersection hourAnglesAboveDial) as AngleSet).also {
        if (info && !isHorizontal) {
            printLn(color(), "Hour angles where both conditions above are fulfilled: ${haAndTime(it)}")
        }
    }

    /**
     * Determines the set of hour-angles for which the altitude of the midsummer sun is above a
     * minimum altitude angle over a given plane.
     * @param PN the plane normal
     * @param altitude the minimum altitude angle for points on the summer tropic
     * @return the hour-angles
     */
    fun hourAnglesAbovePlane(PN: Vector3,altitude: Double) =
        traceLinePoint(Wa,sin(if (geolat >= 0.0) epsilon else -epsilon),PN,sin(altitude))?.let { V0 ->
            val V = (PN cross Wa).normalize()
            solveQuadraticReal(V dot V, 2 * (V0 dot V), (V0 dot V0) - 1).let {
                if (it.size < 2)
                    AngleSet.UNIVERSAL_SET
                else
                    it.let { (t0, t1) ->
                        (V0 + t0 * V).let { P0 ->
                            atan2(P0.normalize() dot WaW, P0.normalize() dot WaS).let { ha0 ->
                                (V0 + t1 * V).let { P1 ->
                                    atan2(P1.normalize() dot WaW, P1.normalize() dot WaS).let { ha1 ->
                                        val hC = (ha0 + ha1) / 2
                                        val U = cos(hC) * WaS + sin(hC) * WaW
                                        val isAbove = (U dot PN >= 0.0)

                                        if (isAbove)
                                            AngleSet(CLOSED,min(ha0, ha1),max(ha0, ha1), CLOSED)
                                        else
                                            (AngleSet(CLOSED,-PI,min(ha0,ha1),CLOSED) union AngleSet(CLOSED,max(ha0,ha1),PI,OPEN)) as AngleSet
                                    }
                                }
                            }
                        }
                    }
            }
        }

    /**
     * Determine a point on the trace-line between two planes
     * @param M unit vector normal to first plane
     * @param m distance along M to first plane
     * @param N unit vector normal to second plane
     * @param n distance along N to second plane
     * @return the point where the trace line intersects the plane spanned out by M and N
     */
    fun traceLinePoint(M: Vector3, m: Double, N: Vector3, n: Double) =
        N.componentNormalTo(M).normalize().let { V ->
            val VN = V dot N

            if (isTiny(VN))
                null // M and N are (anti)parallel
            else {
                val t = (n*(N dot N) - m*(M dot N))/VN

                m*M + t*V
            }
        }

    fun hoursAboveHorizon(altitude: Double) = //  By the Sunrise formula
        ((sin(altitude) - sin(geolat)*sin(if (geolat > 0.0) epsilon else -epsilon))/(cos(geolat)*cos(epsilon))).let { x ->
            if (abs(x) <= 1.0)
                acos(x).let {
                    AngleSet(CLOSED,-it,it,CLOSED)
                }
            else {
                printLnErr("Argument to big: acos(${fix6(x)})")
                null
            }
        }


    val centralHourAngle = (if (hourAnglesAboveDial.isSameAs(AngleSet.UNIVERSAL_SET))
        0.0
    else
        (hourAnglesAboveDial.first() + hourAnglesAboveDial.last())/2).also {
        if (info) {
            printLn(color(), "Central hour-angle = ${fixD(it)}  ${HMS(getZoneHourByHA(it))}")
        }
    }

    /**
     * The hour angles for which the sun comes above the horizon, in front of the dial
     * and within the [hourSpan] at any time during the year
     */
    val hourAngles = (if (hourSpan != null) {

        if (hourSpan*HOURS < hourAnglesVisible.length) {
            val h = hourSpan*HOURS
            val dh = h/2
            val a0 = clampPI(centralHourAngle - dh)
            val a1 = clampPI(centralHourAngle + dh)

            val a = if (hourAnglesVisible.contains(a0) && hourAnglesVisible.contains(a1))
                AngleSet(CLOSED,a0,a1,CLOSED)
            else if (!hourAnglesVisible.contains(a0))
                AngleSet(CLOSED,hourAnglesVisible.first(),hourAnglesVisible.first()+h,CLOSED)
            else if (!hourAnglesVisible.contains(a1))
                AngleSet(CLOSED,hourAnglesVisible.last()-h,hourAnglesVisible.last(), CLOSED)
            else
                hourAnglesVisible

            (a intersection hourAnglesVisible) as AngleSet
        }
        else
            hourAnglesVisible
    }
    else
        hourAnglesVisible).also {
        if (info) {
            printLn(color(), "Solar hour angles displayed: ${haAndTime(it)}")
        }
    }.also {
        if (info) {
            printLn(color(), "Actual span of hours displayed: ${HMS(it.length / HOURS)}")
            printLn(color(),"Requested span og hours: ${if (hourSpan == null) "maximum" else HMS(hourSpan)}")
        }
    }

    fun hourAnglesAboveAltitude(alt: Double): AngleSet {
        val sinE = sin(if (geolat >= 0.0) epsilon else -epsilon)
        val cosE = cos(epsilon)

        return ((sin(alt) - sin(geolat)*sinE)/(cos(geolat)*cosE)).let { cosHa ->
            val ha = acos(cosHa)

            AngleSet(CLOSED,-ha,ha,CLOSED)
        }
    }

    private fun haAndTime(ha: AngleSet,elng: Double?=null): String {
        val sb = StringBuilder(if (ha.isEmptySet()) "$EMPTY" else "$ha, ")
        val et = elng?.let { el ->
            eot.eot(el)
        } ?: 0.0
        ha.itvl().forEachIndexed { i,itv ->
            if (i > 0) sb.append(" and ")
            if ((itv.leftValue() == -PI) && (itv.rightValue() == PI))
                sb.append("from 00:00 to 24:00 (duration 24:00)")
            else {
                    val first = HM(getZoneHourByHA(itv.leftValue()+et))
                    val last = HM(getZoneHourByHA(itv.rightValue()+et))
                    val len = HM((itv.rightValue() - itv.leftValue()) / HOURS)

                    sb.append("from $first to $last (duration $len)")
                }
        }

        return sb.toString()
    }

    // A line representing the minimum hour displayed on the dial
    val hourMinLine = if (hourAngles == AngleSet.UNIVERSAL_SET) {
        // Dial displays 24 hours
        null
    }
    else {
        val ha = hourAngles.first() + PI

        if (isParalellHourLines)
            getLineByHA(ha, side = Line.LEFT_SIDE)
        else
            HaV3(ha).let { u3 ->  // Vector3 along hour-line
                (-1.0*dHaV3(ha)).let { n3 -> // Vector in the direction of change by ha
                    val u = toDialVector(u3) // Hour-line projected on the dial plane
                    val uLeft = Vector(-u.delY,u.delX)
                    val n = toDialVector(n3) // Direction of change projected on the dial plane
                    val side = if (Vector.dot(n,uLeft) >= 0.0) Line.LEFT_SIDE else Line.RIGHT_SIDE
                    
                    getLineByHA(ha, side = side)
                }
            }
    }

    // A line representing the maximum  hour displayed on the dial
    val hourMaxLine = if (hourAngles == AngleSet.UNIVERSAL_SET) {
        // Dial displays 24 hours
        null
    }
    else
        getLineByHA(hourAngles.last()+PI, side = hourMinLine?.otherSide() ?: Line.RIGHT_SIDE)

    /**
     * For a declination of the sun, return the conic that traces the shadow for
     * this declination
     */
    fun getDeclinationConic(declination: Double): Geometry? =
        ConicSection.sliceCone(G,omega, PI_HALF + declination)

    /**
     * For an altitude above the dial, return the conic that
     * traces the shadow for this altitude. The conic
     * will be a circle
     */
    fun getDialAltitudeConic(altitude: Double) = ConicSection.sliceCone(G,0.0,PI_HALF+altitude)

    /**
     * For an altitude above the horizon, return the conic that
     * traces the shadow for this altitude
     */
    fun getHorizonAltitudeConic(altitude: Double): Geometry? {
        // Z (normal to local horizon) is axis of cone
        // Plane normal is g, cone axis is Z, angle between g and Z is beta

        // TODO Must use PI_HALF + altitude for northern latitudes to select the NEGATIVE_BRANCH for the hyperbola
        // TODO Why?
        return ConicSection.sliceCone(G,beta, PI_HALF + (if (geolat >= 0.0) altitude else -altitude))?.rotate(my)
    }

    /**
     * Gets a hour [Line] corresponding to the [zoneHour]
     * @param zoneHour zone hour in hours
     */
    fun getLineByZH(zoneHour: Double) = getLineByHA((zoneHour - zoneHourOffset)* HOURS)

    /**
     * Gets the Vector3 in the equator plane corresponding to a hour angle
     * @param ha hour angle
     * @return a vector in the equator plane
     */
    fun HaV3(ha: Double) = cos(ha)*WaS + sin(ha)*WaW

    /**
     * Gets the derived of [HaV3] with respect to the hour angle
     * @param ha hour angle
     * @return a vector in the equator plane
     */
    fun dHaV3(ha: Double) = -sin(ha)*WaS + cos(ha)*WaW


    /**
     * Gets a hour [Line] corresponding to the [hourAngle]
     * @param hourAngle hour angle in radians
     */
    fun getLineByHA(ha: Double, side: Int = Line.RIGHT_SIDE) =
        getTraceLineHA(ha)?.let { ln ->
            Line(ln.pos, ln.u,side = side,fullDomain = ln.fullDomain)
        }


    data class Anniversary(
        val dt: DateTimeUTC, // Time of anniversary
        val eclLong: Double, // Sun's ecliptic longitude at time
        val cs: Geometry?, // Day line at time
        val hl: Line?, // Hour line at time
        val pt: Point?, // Intersection point between cs and hl
        val isOnDial: Boolean,
        val isSunUp: Boolean,
        val isSunInFront: Boolean)

    /**
     * Will generate an [Anniversary] for the dial. This can be used to mark an annual celebration, such as a birthday.
     * Care must be taken to position the dial so that it is in sunlight at the supplied date and time. If the
     * anniversary is during the night, the shadow can never fall on the dial.
     * @param dt date and time of the anniversary
     * @param rect the rectangle enclosing the dial
     */
    fun getAnniversary(dt: DateTimeUTC,rect: Rectangle): Anniversary? {
        // True ecliptic longitude at date of anniversary
        val elng = Astronomy.trueLongitude(dt)
        // the Equation of Time is apparent solar time minus mean solar time
        // EOT = sundial time - clock time
        val eot = EquationOfTime(dt.year).eot(elng)
        val zoneHour = dt.hour + dt.minute/60.0 + dt.second/3600.0 + timeZoneMeridian/HOURS - eot/HOURS
        val decl = decl(elng)

        val ha = getHourAngleByZH(zoneHour)
        val r = HaV3(ha)*cos(decl) + Wa*sin(decl)

        val isSunUp = r dot Z > 0.0
        val isSunInFrontOfDial = (r dot g > 0.0)

        return getDeclinationConic(decl)?.let { cs -> // The day-line through the anniversary

            getLineByZH(zoneHour)?.let { hl0 ->
                // Need to return a line with fulldomain == R to draw the cross at the anniversary
                val hl = if (isSunInFrontOfDial)
                    Line(hl0, side = hl0.side, fullDomain = RealSet.R) // Hour line trough anniversary
                else
                    null

                val lst = hl?.let {
                    Clipper.intersects(cs, it).filter { iPt ->
                        val elngPt = clamp2PI(getLongitudeOfPoint(iPt))

                        hl0.toParameter(iPt)?.let { tPt ->
                            // PI - elngPt is also a solution
                            (isTiny(elngPt - elng) || isTiny(clamp2PI(PI - elngPt) - elng)) // && (domain.contains(tPt))
                        } ?: false
                    }
                } ?: emptyList()

                if (lst.size > 1)
                    printLnWrn("Ambiguity while computing anniversary")

                val pt = if (lst.isNotEmpty() && isSunInFrontOfDial) {
                    lst[0]
                } else null

                val isOnDial = pt?.let {
                    rect.hit(pt) == Geometry.HIT_INSIDE
                } ?: false

                Anniversary(dt,elng,cs,hl,pt,isOnDial,isSunUp,isSunInFrontOfDial)
            }
        }
    }

    /**
     * Value for tilt parameter of [Text]. This value will make texts upright.
     * Used to make the SCAD files
     */
    val txtRotate = (top.rotation - PI).also {
        if (info && !isHorizontal)
            printLn(color(),"Rotation to bring text upright: ${fixD(it)}")
    }

    /**
     * Gets the hour angle of a point on the dial. The angle is in the range [-PI,PI>
     */
    fun getHourAngleOfPoint(pt: Point) = (pt.x*Right + pt.y*Top).let { v3 ->
        v3.componentNormalTo(Wa).normalize().let { u ->
            atan2(u dot WaW,u dot WaS)
        }
    }

    /**
     * Gets the ecliptic longitude corresponding to a point on the dial. The longitude is
     * in the range [-PI/2,PI/2]. If *el* is returned, then *PI - el* is also a longitude of the
     * same point. Radians
     */
    fun getLongitudeOfPoint(pt: Point) = eclLng(getDeclinationOfPoint(pt))

    /**
     * Gets the declination corresponding to a point on the dial. The declination is in the
     * range [-PI/2,PI/2]. Radians
     */
    fun getDeclinationOfPoint(pt: Point) = (pt.x*Right + pt.y*Top).let { v3 ->
        val X = G*g - v3
        val R = X.norm()

        if (R == 0.0)
            0.0
        else
            asin((X dot Wa)/R)
    }

    /**
     * A short textual description of the dial parts, consisting of geographic location,
     * orientation and meridian of the time zone. if [showFlatPos] if true, then the geographical
     * position of the flat dial is also included.
     */
    fun veryShortDescript(showFlatPos: Boolean = false): String {
        val b = StringBuilder()

        b.append("${fixLat(geolat)} ${fixLng(geolng)}")

        val s = if (isHorizontal)
            " ZENITH"
        else if (altitude != 0.0)
            " az:${fixD(azimuth)} alt:${fixD(altitude)} (${azimuth2Cardinal(azimuth)})"
        else if (azimuth == 0.0)
            " NORTH"
        else if (azimuth == PI_HALF)
            " EAST"
        else if (azimuth == -PI)
            " SOUTH"
        else if (azimuth == -PI_HALF)
            "WEST"
        else
            " az:${fixD(azimuth)} alt:0(${azimuth2Cardinal (azimuth)})"

        b.append(s)
//        b.append(if (!isFlat) " az:${sundial.fixD(azimuth)}(${sundial.azimuth2Cardinal(azimuth)}) alt:${sundial.fixD(altitude)}" else " HORIZONTAL")
        b.append(" tzm: ${fixLng(timeZoneMeridian)} G:${fixL(G)} F:${if (isHuge(F)) INFINITY else fixL(F)} ")
        if (showFlatPos && (!isHorizontal))
            b.append(" [${fixLat(flatLat)} ${fixLng(flatLng)}] ")

        return b.toString()

    }

    /**
     * See [veryShortDescript]
     */
    fun shortDescript() = veryShortDescript(showFlatPos = true)

