    /**
     * Gets the trace line between the dial plane and the half plane defined by the world axis [Wa] and the
     * hour angle vector [HaV3]
     * @param ha hour angle
     * @return A [Line] or null
     */
    fun getTraceLineHA(hourAngle: Double): Line? {
        val hV3 = HaV3(hourAngle)
        val Hg = hV3 dot g
        val Wg = Wa dot g

        return if (isTiny(Hg) && isTiny(Wg))
            null
        else if (isTiny(Wg)) {
            val P = G*(g - (1/Hg)*hV3)
            val U = Wa

            Line(toDialPoint(P),toDialVector(U).normalize(), fullDomain = RealSet.R)
        }
        else {

            val P = G*g - (G/Wg)*Wa // = radiant
            val U = hV3 - (Hg/Wg)*Wa

            Line(toDialPoint(P),toDialVector(U).normalize(), fullDomain =  RealSet.R_PLUS)
        }
    }

    /**
     * Gets a map from minutes of EoT to solar ecliptic longitudes.
     * @param isSpring true for a map of spring EoT, false for the autumn EoT
     * @param spacing a value suggesting the space between the lines marking the EoT
     * @param info if true, a textual description will be printed
     * @return a map for the EoT
     */
    fun getStepEoT(isSpring: Boolean,spacing: Double,info: Boolean=false): Map<Double,AngleSet> {
        val decl = ((declinations intersection AngleSet(CLOSED,minAltitude*DEG-omega,PI_HALF,OPEN)) as AngleSet)
        val l0 = G/tan(decl.first() + omega)
        val l1 = G/tan(decl.last() + omega)

        return getStepEoT(isSpring,N=(abs(l1 - l0)/spacing).roundToInt(),info=info)
    }

    /**
     * Gets a map from minutes of EoT to solar ecliptic longitudes.
     * @param isSpring true for a map of spring EoT, false for the autumn EoT
     * @param N the number of intervals the range of solar longitudes should be split into
     * @param info if true, a textual description will be printed
     * @return a map for the EoT
     */
    fun getStepEoT(isSpring:Boolean,N:Int=10,info: Boolean=false): Map<Double,AngleSet> {
        data class EoTPoint(val minute: Double,val lambda: Double)

        // TODO Check for southern latitudes
        // decl >= minAltitude - omega for the Sun to be at or above minAltitude
        val decl = ((declinations intersection AngleSet(CLOSED,minAltitude*DEG-omega,PI_HALF,OPEN)) as AngleSet)

        val lam0 = eclLng(decl.first())
        val lam1 = eclLng(decl.last())

        fun length(lam: Double) = G/tan(decl(lam) + omega)

        val l0 = length(lam0)
        val l1 = length(lam1)

        fun lambda(len: Double) = if (isTiny(len-l0))
            lam0
        else if (isTiny(len-l1))
            lam1
        else {
            val arg_ = sin(atan2(G,len) - omega)/eot.sEps
            // To avoid failure if |arg_1| is just a little bit larger than 1
            val arg = if (isTiny(abs(arg_)-1.0)) sign(arg_) else arg_

            asin(arg)
        }

        val color = if (isSpring) MAGENTA else CYAN

        val points = buildList<EoTPoint> {
            for (i in 0 .. N) (l0 + i*(l1-l0)/N.toDouble()).also { l ->
                val lam_ = lambda(l)
                val lam = if (isSpring) lam_ else PI - lam_
                val m = eot.minutes(lam)
                add(EoTPoint(m,lam))
                if (info)
                    printLnTmp(color,"EoT(${fixD(lam)}) = ${EoTGraph.fmtSec(m)}")
            }

            sortBy { it.lambda }
        }

        return buildMap<Double,AngleSet> {
            for (i in 0 .. points.size-2)
                this[(points[i].minute + points[i+1].minute)/2] = AngleSet(CLOSED,points[i].lambda,points[i+1].lambda,OPEN)
        }
    }

    companion object {
        /**
         * Default width of a time-zone in degrees
         */
        const val TIME_ZONE_WIDTH = 15.0  // Default width of a time-zone in degrees

        /**
         * Gets a default time-zone meridian by rounding the longitude to the nearest whole multiple of 15.
         * Radians
         */
        fun defaultTimeZoneMeridian(longitude: Double) =
            floor(floor(longitude * RAD + TIME_ZONE_WIDTH / 2) / TIME_ZONE_WIDTH) * TIME_ZONE_WIDTH * DEG

        /**
         * Longitude of the timezone meridian. Radians
         * @param hourOffset offset from UTC in hours
         */
        fun timeZoneMeridian(hourOffset: Double) = TIME_ZONE_WIDTH * hourOffset * DEG


        /**
         * For a latitude, longitude and azimuth, find the altitude that makes the third component of g,  g.z == 0.
         * This will put the flat dial associated with this dial at the equator. This method is intended for debugging.
         */
        fun getGz0Altitude(lat: Double,az: Double) = solveTrig(cos(az)*cos(lat),sin(lat),0.0).let { lst ->
            var a: Double = Double.NEGATIVE_INFINITY

            lst.forEach {
                if (it > a) a = it
            }

            if (a.isInfinite()) null else a
        }

        /**
         * Sort some [Point]s by their projections along a [Vector]
         * @param v a vector not [Vector.O]
         * @param pt points
         * @return a sorted list
         */
        fun sortPointsAlong(v: Vector,vararg pt: Point) = sortPointsAlong(v,pt.asList())

        /**
         * Sort a list of [Point]s by their projections along a [Vector]
         * @param v a vector not [Vector.O]
         * @param pt points
         * @return a sorted list
         */
        fun sortPointsAlong(v: Vector,pts: List<Point>): List<Point> {
            val u = v.normalize()

            return pts.sortedBy { pt ->
                pt.x*u.delX + pt.y*u.delY
            }
        }
    }
