object Clipper{
    fun clipGeometries(clip: Geometry,vararg gs: Geometry?) {
        gs.forEach { g ->
            if (g != null)
                clipGeometry(clip,g).let {
                    g.domain = ((g.domain?:g.fullDomain) intersection it) as RealSet
                }
        }
    }

