    fun clipByClippers(g: Geometry, vararg clippers: Geometry?, debug: Boolean = false): RealSet {
        var domain = /*g.domain ?:*/ g.fullDomain

        if (debug) {
            println("Clipping ${g.label} ${g.typeName()}")
        }
        clippers.forEach { clipper ->
            if (clipper != null) {
                if (debug) {
                    print("\t.. $domain clip with ${clipper.label}${if (clipper.outside) "*" else ""}")
                }

                clipGeometry(clipper, g, outside = clipper.outside,debug=debug).let {
                    domain = (domain intersection it) as RealSet
                }
            }
        }

        val tiny = domain.length.let {
            if ((it > 0.0) && isTiny(it)) {
                domain = mkEmptySet(g.fullDomain)
                "(tiny)"
            }
            else
                ""
        }

        if (debug) {
            print("\t$domain $tiny\n")
        }

        return domain
    }

    fun clipGeometry(clipper: Geometry, g: Geometry, outside: Boolean=false,debug: Boolean=false): RealSet {

        val fullDomain = when (g.fullDomain) {
            is AngleSet -> g.fullDomain as AngleSet
            is CyclicSet -> g.fullDomain as CyclicSet
            else -> g.fullDomain
        }

        var domain = when (g.fullDomain) {
            is AngleSet -> AngleSet()
            is CyclicSet -> CyclicSet(g.fullDomain as CyclicSet)
            else -> RealSet()
        }

        data class NtrXit( val u: Double, val entering: Boolean)

        val a = intersects(clipper,g,debug=debug).mapNotNull { pt ->
            // Only allow points that lie within the full domains of clipper and g
            clipper.toParameter(pt)?.let { t ->
//                printLn(RED,fix6(t))
                g.toParameter(pt)?.let { u ->
//                    printLn(GREEN,"${fixD(u)} ${g.fullDomain}")
                    clipper.normal(t)?.let { N ->
                        g.tangent(u)?.let { T ->
                            NtrXit(u,Vector.dot(N,T) <= 0.0)
                        }
                    }
                }
            }

        }.sortedBy {
            it.u
        }.toMutableList().let {
            // TODO Perhaps only cyclic sets where g i a closed geometry as ellipses and rectangles
            // If the list starts with an exit, move the first element to the end
            if (fullDomain is CyclicSet)
                if (it.isNotEmpty())
                    if (!it.first().entering)
                        it.add(it.removeAt(0))

            it.toList()
        }.also { if (debug) println(", \t${it.size} intersect(s) ..")}


//        a.forEach { nx ->
//            print("${fullDomain.str(nx.u)}  ${if (nx.entering) "entering" else "exiting"} ")
//        }
//        printLn()
//        printLn(MAGENTA,"${a.size} intersections between ${clipper.label} and ${g.label}")

        if (a.isEmpty()) { // is g entirely inside clipper
            if (fullDomain.itvl().isEmpty()) {
                printLnWrn("${g::class.simpleName} ${g.typeName()} ${g.label} has no full-domain")
            }
            else {
                val itv = fullDomain.itvl().first()
                val lv = if (itv.leftValue().isInfinite()) -HUGE else itv.leftValue()
                val rv = if (itv.rightValue().isInfinite()) HUGE else itv.rightValue()
                val v0 = (lv + rv)/2 + (rv - lv)/10
//                val v0 = (lv + rv)/2
                g.toPoint(v0)?.let { pt -> // A point on g
                    val hit = when (clipper.hit(pt)) {
                        Geometry.HIT_OUTSIDE -> outside // If the point hits outside, then it is a hit if outside is true
                        Geometry.HIT_INSIDE -> !outside // If the point hits inside, then it is a hit if outside is false
                        Geometry.HIT_ON -> !outside
                        Geometry.HIT_LEFT -> if (clipper is Line) clipper.side==Line.RIGHT_SIDE else false
                        Geometry.HIT_RIGHT -> if (clipper is Line) clipper.side==Line.LEFT_SIDE else false
                        else -> false
                    }

                    // Return here to avoid outside turning to complementary at the end
                    return if (hit) {
                        fullDomain
                    } else {
                        mkEmptySet(g.fullDomain) // An empty set
                    }
                }
            }
        }
        else if (a.size == 1) {
            a[0].let { nx ->
                domain = if (nx.entering)
                    g.fullDomain.splitKeepRight(nx.u) as RealSet
                else
                    g.fullDomain.splitKeepLeft(nx.u) as RealSet
            }
        }
        else { // 2 or more intersection points
            val iMax = a.size - 2
            for (i in 0..iMax step 2) {

                if ((a[i].entering) && (!a[i+1].entering)) {
                    val d = mkSet2(fullDomain, CLOSED, a[i].u, a[i + 1].u, CLOSED)
//                    print(MAGENTA,"Enter - exit $d")
                    domain = d.union(domain) as RealSet
                }
                else if ((!a[i].entering) && (a[i+1].entering)) {
                    val d = mkSet2(fullDomain, CLOSED, a[i].u, a[i + 1].u, CLOSED).complement()
//                    print(MAGENTA,"Exit - enter $d")
                    domain = d.union(domain) as RealSet
                }
                else
                    printLnErr("Clipping ERROR")
            }
        }

        if (outside) {
//            println(indent("#1: ${g.label} $domain\naxis: ${NumberSet.axisToString(domain)}"))
            domain = if (domain.isEmptySet())
                fullDomain
            else
                (fullDomain intersection (domain.complement())) as RealSet
//            println(indent("#2: ${g.label} $domain\naxis: ${NumberSet.axisToString(domain)}"))
        }


        return domain
    }

    fun mkSet2(fullDomain: RealSet, leftType: Int, leftValue: Double, rightValue: Double, rightType: Int): RealSet {
        return when (fullDomain) {
            is AngleSet -> AngleSet(leftType,leftValue,if (rightValue < leftValue) rightValue+fullDomain.period else rightValue, rightType)
            is CyclicSet -> CyclicSet(fullDomain,leftType,leftValue,if (rightValue < leftValue) rightValue+fullDomain.period else rightValue, rightType)
            else -> RealSet(leftType,min(leftValue,rightValue),max(leftValue,rightValue), rightType)
        }
    }

    fun copyGeometry(g: Geometry, domain: RealSet): Geometry? {
        return when (g) {
            is Line -> Line(g,fullDomain=domain)
            is Rectangle -> Rectangle(g,domain as CyclicSet)
            is ConicSection -> ConicSection(g,domain as AngleSet)
            else -> null
        }
    }

    fun intersects(p: Geometry, q: Geometry,debug: Boolean=false): List<Point> {
        return when (p) {
            is Rectangle -> when (q) {
                is Line -> intersectsRectLine(p,q)
                is Rectangle -> Rectangle.intersects(p, q)
                is ConicSection -> intersectsRectConic(p,q)
                else -> {
                    printLnErr("A ${p::class.simpleName} cannot intersect a ${q::class.simpleName}")
                    emptyList<Point>()
                }
            }

            is Line -> when (q) {
                is Line -> Line.intersect(p, q)?.let {
                    listOf(it)
                } ?: emptyList()
                is ConicSection -> ConicSection.lineIntersects(p, q)
                is Rectangle -> intersectsRectLine(q,p)
                is Wedge -> intersectsWedgeGeometry(q,p)
                else -> {
                    printLnErr("A ${p::class.simpleName} cannot intersect a ${q::class.simpleName}")
                    emptyList<Point>()
                }
            }

            is ConicSection -> when (q) {
                is ConicSection -> {
                    // TODO Should actually test the rotations (theta)
                    if (isTiny(p.B) && isTiny(p.D) && isTiny(q.B) && isTiny(q.D)) {
                        val lst = mutableListOf<Point>()
                        val P = p.A*q.C - q.A*p.C
                        val Q = p.A*q.E - q.A*p.E
                        val R = p.A*q.F - q.A*p.F
                        solveQuadraticReal(P,Q,R).forEach { y ->
                            val x = kotlin.math.sqrt((-p.C*y.pow(2) - p.E*y - p.F)/p.A)
                            if (x.isFinite()) {
                                val pt = Point(x, y)

                                if ((p.hit(pt) == Geometry.HIT_ON) && (q.hit(pt) == Geometry.HIT_ON)) {
                                    lst.add(Point(-x, y))
                                    lst.add(Point(x, y))
                                }
                            }
                        }

                        lst.toList()
                    }
                    else {
                        // TODO This does not find all intersections!
//                        printLnWrn("Dubious intersection algorithm")
                        ConicSection.intersects(p,q/*,MAGENTA*/)
                    }
                }
                is Line -> ConicSection.lineIntersects(q, p)
                is Rectangle -> intersectsRectConic(q,p)
                is Wedge -> intersectsWedgeGeometry(q,p,debug=debug)
                else -> {
                    printLnErr("A ${p::class.simpleName} cannot intersect a ${q::class.simpleName}")
                    emptyList<Point>()
                }
            }

            is Wedge -> intersectsWedgeGeometry(p,q,debug=debug)

            else -> {
                printLnErr("Cannot intersect a ${q::class.simpleName} and a ${p::class.simpleName}")
                emptyList<Point>()
            }
        }
    }

    fun intersectsRectLine(rect: Rectangle, ln: Line): List<Point> {
        val lst = mutableListOf<Point>()

        rect.lns.forEach { rl -> // Line of rectangle
            Line.intersect(ln, rl)?.let { pt -> // Point of intersection
                rl.toParameter(pt)?.let { t ->
                    if (rl.fullDomain.contains(t))
                        lst.add(pt)
                }
            }
        }

        return lst
    }

