    fun intersectsRectConic(rect: Rectangle, cs: ConicSection): List<Point> {
        val pts = mutableListOf<Point>()

        rect.lns.forEach { ln ->
            ConicSection.lineIntersects(ln, cs).forEach { pt ->
                rect.toParameter(pt)?.let {
                    pts.add(pt)
                }
            }
        }

        return pts
    }

    fun intersectsWedgeGeometry(wedge: Wedge, g: Geometry,debug: Boolean=false): List<Point> {
        val lst = mutableListOf<Point>()

        when (g) {
            is Line -> {
                listOf(wedge.ray0,wedge.ray1).forEach { ray ->
                    Line.intersect(ray,g)?.let { pt ->
                        ray.toParameter(pt)?.let { u ->
                            if (ray.fullDomain.contains(u))
                                lst.add(pt).also { if (debug) print(if (ray == wedge.ray0) RED else GREEN," $pt") }
                        }
                    }
                }
            }
            is ConicSection -> {
                listOf(wedge.ray0,wedge.ray1).forEach { ray ->
                    ConicSection.lineIntersects(ray, g).let { l ->
                        l.forEach { pt ->
                            if (g.hit(pt) == Geometry.HIT_ON)
                                ray.toParameter(pt)?.let { u ->
                                    if (ray.fullDomain.contains(u))
                                        lst.add(pt).also { if (debug) print(if (ray == wedge.ray0) RED else GREEN," $pt") }
                                }
                        }
                    }
                }
            }
            else -> {
                printLnErr("A ${wedge::class.simpleName} cannot intersect a ${g::class.simpleName}")
            }
        }

        return lst.toList()
    }

