package sundial.math

import fix6
import isZero
import sundial.geometry.Point
import kotlin.math.cos
import kotlin.math.sin

class Matrix(
    val m00: Double,val m01: Double,
    val m10: Double,val m11: Double
) {

    fun precat(m: Matrix) = mult(m, this)

    fun postcat(m: Matrix) = mult(this, m)

    fun postcat(v: Vector) = mult(this, v)

    fun mult(v: Vector) = mult(this, v)

    fun mult(p: Point) = mult(this, p)

    val transposed: Matrix by lazy {
        Matrix(
            m00, m10,
            m01, m11
        )
    }

    val inv: Matrix? by lazy {
        inverse(this)
    }

    val det: Double by lazy {
        determinant(this)
    }

    override fun toString(): String {
        return """
                ${fix6(m00)}  ${fix6(m01)}
                ${fix6(m10)}  ${fix6(m11)}
            """.trimIndent()
    }

    companion object {
        val I = Matrix(
            1.0, 0.0,
            0.0, 1.0
        )

        val MIRROR_X = Matrix(
            -1.0, 0.0,
            0.0, 1.0
        )

        val MIRROR_Y = Matrix(
            1.0, 0.0,
            0.0, -1.0
        )

        val MIRROR = Matrix(
            -1.0, 0.0,
            0.0, -1.0
        )


        fun rotation(alpha: Double): Matrix {
            val sA = sin(alpha)
            val cA = cos(alpha)

            return Matrix(
                cA, -sA,
                sA, cA
            )
        }

        fun mult(P: Matrix, Q: Matrix) = Matrix(
            P.m00 * Q.m00 + P.m01 * Q.m10, P.m00 * Q.m01 + P.m01 * Q.m11,
            P.m10 * Q.m00 + P.m11 * Q.m10, P.m10 * Q.m01 + P.m11 * Q.m11
        )

        fun mult(M: Matrix, V: Vector) = Vector(
            M.m00 * V.delX + M.m01 * V.delY,
            M.m10 * V.delX + M.m11 * V.delY
        )

        fun mult(M: Matrix, P: Point) = Point(
            M.m00 * P.x + M.m01 * P.y,
            M.m10 * P.x + M.m11 * P.y
        )

        fun determinant(M: Matrix) = (M.m00*M.m11 - M.m01*M.m10)

        fun inverse(M: Matrix) = if (isZero(M.det)) null else
            Matrix(
                M.m11/M.det,-M.m01/M.det,
                -M.m10/M.det,M.m00/M.det
            )

    }
}