package sundial.numberset

import EMPTY
import UNION
import printLnWrn


abstract class NumberSet<N: Number?>(intervals: List<NumberSet<N>.Interval>) {

    inner class Brace(val side: Int,val type: Int, val value: N) : Comparable<Brace> {

        override fun compareTo(other: Brace): Int {
            val res1 = compareValues(value,other.value)
            return if (res1 != 0)
                res1
            else {
                // When the braces have the same value!

                // v0 < v1  --> -1
                // v0 == v1 --> 0
                // v0 > v1  --> 1
                if ((side == LEFT) && (other.side == LEFT)) {
                    if (type == other.type) 0
                    else if ((type == OPEN) && (other.type == CLOSED)) 1        // < [
                    else /*if ((type == sundial.CLOSED) && (b.type == sundial.OPEN))*/ -1   // [ <
                }
                else if ((side == LEFT) && (other.side == RIGHT)){
                    if ((type == OPEN) && (other.type == OPEN)) 1               // < >
                    else if ((type == OPEN) && (other.type == CLOSED))  1       // < ]
                    else if ((type == CLOSED) && (other.type == OPEN))  1      // [ >
                    else /*if ((type == sundial.CLOSED) && (b.type == sundial.CLOSED))*/  -1   // [ ]
                }
                else if ((side == RIGHT) && (other.side == LEFT)){
                    if ((type == OPEN) && (other.type == OPEN)) -1           // > <
                    else if ((type == OPEN) && (other.type == CLOSED))  -1   // > [
                    else if ((type == CLOSED) && (other.type == OPEN))  -1   // ] <
                    else /*if ((type == sundial.CLOSED) && (b.type == sundial.CLOSED))*/  1   // ] [
                }
                else /*if ((side == RIGHT) && (b.side == RIGHT))*/ {
                    if (type == other.type) 0
                    else if ((type == OPEN) && (other.type == CLOSED)) 1       // > ]
             // else if ((type == sundial.OPEN) && (b.type == sundial.CLOSED)) -1       // > ]
                    else /*if ((type == sundial.CLOSED) && (b.type == sundial.OPEN))*/ -1    // ] >
                }
            }
        }

        override fun toString() = strbrace(this)
    }


    open inner class Interval(val leftBrace: Brace, val rightBrace: Brace) {
        // TODO Let Interval like Brace also implement Comparable. This will make
        // TODO it possible to run deep comparisons between NumberSets
        constructor(leftType: Int,leftValue: N,rightValue: N,rightType: Int) :
                this(Brace(LEFT,leftType,leftValue),Brace(RIGHT,rightType,rightValue))

        fun intersection(itv2: Interval): Interval? {
            return if (rightBrace.compareTo(itv2.leftBrace) == -1)
                null
            else if (itv2.rightBrace.compareTo(leftBrace) == -1)
                null
            else {
                val itv = Interval(maxBrace(leftBrace, itv2.leftBrace),minBrace(rightBrace, itv2.rightBrace))

                if (itv.isValid()) itv else null
            }
        }

//        fun intersection(itv2: NumberSet<N>.Interval): NumberSet<N>.Interval? {
//            return if (rightBrace < itv2.leftBrace)
//                null
//            else if (itv2.rightBrace < leftBrace)
//                null
//            else {
//                val itv = Interval(maxBrace(leftBrace, itv2.leftBrace),minBrace(rightBrace, itv2.rightBrace))
//
//                if (itv.isValid()) itv else null
//            }
//        }

        fun isValid() = (leftBrace.compareTo(rightBrace) == -1)

        fun contains(value: N) = contains(Brace(LEFT, CLOSED,value)) && contains(Brace(RIGHT, CLOSED,value))

        fun contains(brace: Brace) : Boolean {
            val compL = leftBrace.compareTo(brace)
            val compR = rightBrace.compareTo(brace)

            return (((compL == -1) || (compL == 0)) && ((compR == 1) || (compR == 0)))
        }

        fun maxBrace(br1: Brace, br2: Brace) = if (br1 >= br2) br1 else br2

        fun minBrace(br1: Brace, br2: Brace) = if (br1 <= br2) br1 else br2

//        open fun splitKeepLeft(value: N): List<Interval> {
//            return if (contains(value)) {
//                listOf(Interval(leftBrace,Brace(RIGHT, sundial.CLOSED,value)))
//            }
//            else
//                emptyList()
//        }
//
//        open fun splitKeepRight(value: N): List<Interval> {
//            return if (contains(value)) {
//                listOf(Interval(Brace(LEFT, sundial.CLOSED,value),rightBrace))
//            }
//            else
//                emptyList()
//        }


        fun leftValue() = leftBrace.value

        fun rightValue() = rightBrace.value

        fun leftOpen() = (leftBrace.type == OPEN)

        fun rightOpen() = (rightBrace.type == OPEN)

        override fun toString() = "$leftBrace,$rightBrace"
    }

    abstract val MIN_BRACE: Brace
    abstract val MAX_BRACE: Brace

    val UNIVERSAL_SET: NumberSet<N> by lazy {
        instance(listOf(Interval(MIN_BRACE,MAX_BRACE)))
    }

    val EMPTY_SET: NumberSet<N> by lazy {
        instance(emptyList())
    }

    internal val l: List<Interval> = intervals.toList()

    open fun itvl(wrapAndJoin: Boolean = true) = l

    fun isEmptySet() = l.isEmpty()

    fun isUniversalSet() = if (l.size != 1)
        false
    else
        ((l.first().leftBrace.compareTo(MIN_BRACE) == 0) && (l.first().rightBrace.compareTo(MAX_BRACE) == 0))

    open fun contains(value: N): Boolean {
        val itr = l.listIterator()
        while (itr.hasNext())
            if (itr.next().contains(value))
                return true

        return false
    }

    abstract fun compareValues(value1: N, value2: N): Int

    fun instance() = instance(emptyList())

    abstract fun instance(intervals: List<Interval>): NumberSet<N>

    abstract fun str(n: N): String

    abstract fun strbrace(brace: Brace): String

    abstract val stridx: String

    abstract fun scale(f: N): NumberSet<N>

    fun isSameAs(other: NumberSet<N>): Boolean {
        return if (this::class != other::class)
            false
        else {
            l.zip(other.l).all { (itv1,itv2) ->
                (itv1.leftBrace.compareTo(itv2.leftBrace) == 0) && (itv1.rightBrace.compareTo(itv2.rightBrace) == 0)
            }
        }
    }

    infix fun minus(ns: NumberSet<N>) = intersection(ns.complement())

    infix fun union(ns: NumberSet<N>) = (complement().intersection((ns.complement()))).complement()

    infix fun union(itv: Interval) = (complement().intersection(instance(listOf(itv)).complement())).complement()

    infix fun intersection(ns: NumberSet<N>): NumberSet<N> {
            val class1 = this::class
            val class2 = ns::class
            if (class1 != class2)
                printLnWrn("Operation performed between different types of NumberSets: ${class1.simpleName} and ${class2.simpleName}")

            val m = mutableListOf<Interval>()

            ns.l.forEach { itv ->
                intersection(itv).let {
                    if (it.l.isNotEmpty()) m.addAll(it.l)
                }
            }

        return instance(m.toList())
    }

    fun intersection(itv: Interval): NumberSet<N> {
        val m = mutableListOf<Interval>()

        l.forEach { itv2 ->
            itv.intersection(itv2)?.let {
                m.add(it)
            }
        }

        return instance(m.toList())
    }

