    fun complement(): NumberSet<N> {

        return if (isEmptySet())
            UNIVERSAL_SET
        else if (isUniversalSet())
            EMPTY_SET
        else {
            val m = mutableListOf<Interval>()

            var lb: Brace? = null
            var rb: Brace? = null

            braceSeq().forEach {
//              println(it)
                if (lb == null) lb = it
                else rb = it

                if (rb != null) {
                    Interval(lb!!, rb!!).let { itv ->
                        if (itv.isValid()) m.add(itv)
                    }

                    lb = null
                    rb = null
                }
            }

            instance(m.toList())
        }
    }

    // Takes the list of intervals and turns it into a sequence of braces, from left to right
    private fun braceSeq() = sequence <Brace> {
        val iMax = l.size - 1

        l.forEachIndexed { i,itv ->
            val compareMin = itv.leftBrace.compareTo(MIN_BRACE)
            val compareMax = itv.rightBrace.compareTo(MAX_BRACE)

            if ((i == 0) && (compareMin == 1))
                yield(MIN_BRACE)

            if (compareMin != 0) yield(turnBrace(itv.leftBrace))
            if (compareMax != 0) yield(turnBrace(itv.rightBrace))

            if ((i == iMax) && (compareMax == -1))
                yield(MAX_BRACE)
        }
    }

    fun turnBrace(br: Brace): Brace = Brace(if (br.side == LEFT) RIGHT else LEFT,if (br.type == OPEN) CLOSED else OPEN,br.value)

    open fun splitKeepLeft(value: N) = this.intersection(instance(listOf(Interval(MIN_BRACE,Brace(RIGHT,CLOSED,value)))))

    open fun splitKeepRight(value: N) = this.intersection(instance(listOf(Interval(Brace(LEFT, CLOSED,value),MAX_BRACE))))

    override fun toString(): String {
        val itvl = itvl()

        return if (itvl.size == 0)
            "$EMPTY$stridx"
        else {
            val b = StringBuilder()
            val iMax = itvl.size - 1

            if (itvl.size > 1) b.append('(')
            itvl.forEachIndexed { i, itv ->
                b.append(itv)
                if (i < iMax) b.append(" $UNION ")
            }
            if (itvl.size > 1) b.append(')')

            b.toString()
        }
    }


    companion object {
        const val OPEN = 0
        const val CLOSED = 1
        const val LEFT = -1
        const val RIGHT = 1

        const val INTERVAL_OK = 10
        const val INTERVAL_ILLEGAL = 11

        const val SET_OK = 12
        const val SET_OVERLAPPING = 13
        const val SET_NOT_ORDERED = 14
        const val SET_UNJOINT = 15
    }
}